import { CommonModule } from '@angular/common';
import {
  ChangeDetectorRef,
  Component,
  DestroyRef,
  OnInit,
  TemplateRef,
  effect,
  inject,
  signal,
  viewChild,
} from '@angular/core';
import { takeUntilDestroyed } from '@angular/core/rxjs-interop';
import { ReactiveFormsModule } from '@angular/forms';

import { ButtonModule } from 'primeng/button';
import { DynamicDialogRef, DynamicDialogConfig } from 'primeng/dynamicdialog';

import {
  EntitiesService,
  AssetStateProfileCreateRequest,
  AssetStateProfileResponse,
} from '../../../../../api';
import { WizardComponent } from '../../../../../shared/components/wizard/wizard.component';
import { isDefined, isNullOrUndefined } from '../../../../../shared/utils';
import { BroadcastEventMessageService } from '../../../services/broadcast-event-messages.service';
import { AssetStateProfileDetailsSectionComponent } from '../asset-state-profile-details-section/asset-state-profile-details-section.component';
import { Step } from '../../../../../shared/models';
import { finalize } from 'rxjs';

@Component({
  selector: 'app-asset-state-profile-create-wizard',
  imports: [
    CommonModule,
    ReactiveFormsModule,
    WizardComponent,
    ButtonModule,
    AssetStateProfileDetailsSectionComponent,
  ],
  templateUrl: './asset-state-profile-create-wizard.component.html',
  styleUrl: './asset-state-profile-create-wizard.component.scss',
})
export class AssetStateProfileCreateWizardComponent implements OnInit {
  assetStateProfileDetailsStep = viewChild<
    TemplateRef<{
      $implicit: Step;
    }>
  >('assetStateProfileDetailsStep');
  confirmationTemplate = viewChild<TemplateRef<{ $implicit: Step }>>(
    'confirmationTemplate',
  );
  private readonly cdr = inject(ChangeDetectorRef);

  // References to form components
  assetStateProfileDetailsForm =
    viewChild<AssetStateProfileDetailsSectionComponent>(
      'assetStateProfileDetailsForm',
    );

  private readonly entitiesService = inject(EntitiesService);
  private readonly broadcastService = inject(BroadcastEventMessageService);
  protected readonly destroyRef = inject(DestroyRef);
  private readonly dialogRef = inject(DynamicDialogRef);
  private readonly dialogConfig = inject(DynamicDialogConfig);

  // AssetStateProfile model to collect data across steps
  newAssetStateProfile = signal<Partial<AssetStateProfileCreateRequest>>({});

  // Form validity state
  formIsValid = signal<boolean>(false);

  displayWizard = false;
  steps: Step[] = [];
  previousStepIndex = 0;
  currentStepIndex = 0;
  ownerId: string | null = null;
  isSubmitting = signal<boolean>(false);

  constructor() {
    effect(() => {
      const detailsStep = this.assetStateProfileDetailsStep();
      const confirmStep = this.confirmationTemplate();

      if (detailsStep && confirmStep) {
        this.steps = [
          {
            value: 1,
            header: 'Asset State Profile Details',
            content: detailsStep,
            complete: false,
            validate: () =>
              this.assetStateProfileDetailsForm()?.validate() ?? false,
          },
          {
            value: 2,
            header: 'Confirmation',
            content: confirmStep,
            complete: true,
          },
        ];
        this.cdr.detectChanges();
      }
    });
  }

  ngOnInit(): void {
    if (isDefined(this.dialogConfig.data)) {
      this.ownerId = this.dialogConfig.data.ownerId ?? null;

      // Initialize assetStateProfile with ownerId
      this.newAssetStateProfile.update((current) => ({
        ...current,
        ownerId: this.ownerId ?? '',
      }));
    }
  }

  openWizard() {
    this.displayWizard = true;
    this.newAssetStateProfile.set({});
  }

  // Form validity handlers
  onDetailsFormValidityChange(isValid: boolean) {
    this.steps[0].disableNext = !isValid;
    this.formIsValid.set(isValid);
  }

  // Update the assetStateProfile state with form data
  updateAssetStateProfileDetails(formData: Record<string, unknown>) {
    this.newAssetStateProfile.update((current) => {
      const updated = { ...current };

      Object.keys(formData).forEach((key) => {
        if (key !== 'ownerId') {
          updated[key] = formData[key];
        }
      });

      if (isNullOrUndefined(updated.ownerId)) {
        updated.ownerId = this.ownerId ?? '';
      }

      return updated;
    });
  }

  onWizardComplete(): void {
    // Process the final data from all steps
    const assetStateProfileData =
      this.newAssetStateProfile() as AssetStateProfileCreateRequest;
    this.isSubmitting.set(true);

    this.entitiesService
      .createAssetStateProfile(assetStateProfileData)
      .pipe(
        takeUntilDestroyed(this.destroyRef),
        finalize(() => this.isSubmitting.set(false)),
      )
      .subscribe({
        next: (response: AssetStateProfileResponse) => {
          this.broadcastService.broadcast('entity-updated', {
            entityType: 'asset-state-profile',
            operation: 'create',
            entityId: response.id,
          });
          this.dialogRef.close(response);
        },
        error: (error: Error) => {
          // Handle error, could show message and keep dialog open
          console.error('Error creating asset state profile:', error);
        },
      });
  }

  // Update cancel to close dialog with no result
  onWizardCancel(): void {
    this.newAssetStateProfile.set({});
    this.dialogRef.close();
  }
}
