import { inject, Injectable, signal } from '@angular/core';
import { LocalStorageService } from '../../../../shared/services/local-storage.service';

@Injectable({
  providedIn: 'root',
})
export class AdminContainerStateService {
  private readonly localStorage = inject(LocalStorageService);

  // Container-specific state signals
  readonly treeCollapsed = signal<boolean>(true);
  readonly nestedContentScrollable = signal<boolean>(false);

  // UI state
  readonly selectedItem = signal<unknown>(null);
  readonly detailsLoading = signal<boolean>(false);
  readonly hasMoreData = signal<boolean>(false);
  readonly loadingMore = signal<boolean>(false);

  constructor() {
    // Initialize tree collapsed state from local storage
    const storedTreeState = this.localStorage.getItem<boolean>(
      'admin-tree-collapsed',
    );
    if (storedTreeState !== null) {
      this.treeCollapsed.set(storedTreeState);
    }
  }

  /**
   * Toggles the tree panel state and persists to local storage
   */
  toggleTreePanel(): void {
    const newState = !this.treeCollapsed();
    this.treeCollapsed.set(newState);
    this.localStorage.setItem('admin-tree-collapsed', newState);
  }

  /**
   * Sets the nested content scrollable state
   */
  setNestedContentScrollable(scrollable: boolean): void {
    if (this.nestedContentScrollable() !== scrollable) {
      this.nestedContentScrollable.set(scrollable);
    }
  }

  /**
   * Resets all state to initial values
   */
  reset(): void {
    this.selectedItem.set(null);
    this.detailsLoading.set(false);
    this.hasMoreData.set(false);
    this.loadingMore.set(false);
  }
}
