import { inject, Injectable } from '@angular/core';
import { Router } from '@angular/router';
import { isDefined, isNullOrUndefined } from '../../../../shared/utils';
import { ADMIN_TREE } from '../../models/tree-node-data.interface';
import { BroadcastEventMessageService } from '../broadcast-event-messages.service';
import { DEFAULT_SYSTEM_ID } from '../../../../core/models';
import { IdNameType } from '../../../../api';

@Injectable({
  providedIn: 'root',
})
export class AdminNavigationService {
  private readonly router = inject(Router);
  private readonly broadcastService = inject(BroadcastEventMessageService);

  async navigateToNode(nodeType: string, nodeKey: string): Promise<boolean> {
    let entityType = '';
    const entityId = nodeKey;

    switch (nodeType) {
      case ADMIN_TREE.NODE_TYPES.CLIENT:
        entityType = 'client';
        break;
      case ADMIN_TREE.NODE_TYPES.VENDOR:
        entityType = 'vendor';
        break;
      case ADMIN_TREE.NODE_TYPES.GROUP:
        return false; // Prevent navigation for now
      case ADMIN_TREE.NODE_TYPES.DISTRIBUTOR:
        entityType = 'distributor';
        break;
      case ADMIN_TREE.NODE_TYPES.SUPER_ADMIN:
        return this.navigateToSuperAdminDistributors();
      default:
        console.warn('Unknown node type for navigation:', nodeType);
        return false;
    }

    if (entityType && entityId) {
      this.broadcastService.broadcast('entity-changed', {
        entityType: nodeType,
        entityId: nodeKey,
      });
      return this.handleEntityNavigation(entityType, entityId);
    }
    return false;
  }

  private handleEntityNavigation(
    entityType: string,
    entityId: string,
  ): Promise<boolean> {
    const currentUrl = this.router.url.split('?')[0]; // Remove query params
    const urlSegments = currentUrl
      .split('/')
      .filter((segment) => segment.length > 0);

    // Extract current entity info from URL
    // Expected format: /administration/client/123/assets or /admin/client/123/assets
    let currentEntityType: string | undefined;
    let currentEntityId: string | undefined;
    let currentEntityTypeIndex = -1;

    // Find entity type in URL segments
    const entityTypes = new Set([
      'client',
      'vendor',
      'distributor',
      'super-admin',
    ]);
    for (let i = 0; i < urlSegments.length; i++) {
      if (entityTypes.has(urlSegments[i])) {
        currentEntityType = urlSegments[i];
        currentEntityTypeIndex = i;
        // Entity ID should be the next segment
        if (i + 1 < urlSegments.length) {
          currentEntityId = urlSegments[i + 1];
        }
        break;
      }
    }
    if (currentEntityType === entityType && currentEntityId === entityId) {
      return Promise.resolve(true);
    }

    let tabPath = '';

    // Extract tab path if switching within the same entity type
    if (currentEntityType === entityType && currentEntityTypeIndex > -1) {
      // Extract everything after /entityType/entityId/
      const tabSegments = urlSegments.slice(currentEntityTypeIndex + 2);
      if (tabSegments.length > 0) {
        tabPath = tabSegments.join('/');
      }
    }

    const navigationTarget: (string | undefined)[] = [
      '/admin',
      entityType,
      entityId,
    ];
    if (tabPath) {
      navigationTarget.push(...tabPath.split('/'));
    }

    return this.router.navigate(navigationTarget.filter(isDefined));
  }

  private async navigateToSuperAdminDistributors(): Promise<boolean> {
    try {
      return await this.router.navigate([
        '/admin',
        'super-admin',
        DEFAULT_SYSTEM_ID,
        'distributors',
      ]);
    } catch (error) {
      console.error('Failed to navigate to super admin distributors:', error);
      return false;
    }
  }

  navigateToEntityDetails(
    entityType: string,
    entityId: string,
    owner: IdNameType,
  ): Promise<boolean> {
    if (isNullOrUndefined(owner)) {
      console.error(
        `Cannot navigate to ${entityType} details without an owner.`,
      );
      return Promise.resolve(false);
    }

    let path: string[] = [];
    const queryParams = { details: entityId, type: entityType };

    switch (entityType) {
      case 'device':
        path = [
          '/admin',
          'client',
          owner.id,
          'hardware',
          'client-hardware-devices',
        ];
        break;
      case 'simcard':
        path = ['/admin', 'client', owner.id, 'hardware', 'client-sim-cards'];
        break;
      case 'asset':
        path = [
          '/admin',
          'client',
          owner.id,
          'assets',
          'grid',
          'client-assets',
        ];
        break;
      case 'user': {
        const ownerEntityType = owner.type === 'vendor' ? 'vendor' : 'client';
        const accountPath =
          ownerEntityType === 'vendor' ? 'distributor-users' : 'client-users';
        path = ['/admin', ownerEntityType, owner.id, 'accounts', accountPath];
        break;
      }
      default:
        console.warn(
          `Navigation to details not implemented for entity type: ${entityType}`,
        );
        return Promise.resolve(false);
    }

    return this.router.navigate(path, { queryParams });
  }
}
