import {
  ChangeDetectionStrategy,
  ChangeDetectorRef,
  Component,
  computed,
  effect,
  inject,
  OnDestroy,
  OnInit,
  signal,
  Type,
} from '@angular/core';
import { Router } from '@angular/router';
import { CommonModule } from '@angular/common';
import {
  BreakpointObserver,
  Breakpoints,
  BreakpointState,
} from '@angular/cdk/layout';
import { takeUntilDestroyed } from '@angular/core/rxjs-interop';

import { MessageService, TreeNode } from 'primeng/api';
import { faAngleLeft, faAngleRight } from '@fortawesome/free-solid-svg-icons';

import { AdaptiveLayoutComponent } from '../../../../shared/components/splitter/adaptive-layout/adaptive-layout.component';
import { NodeNotFoundEvent } from '../../../../shared/components/tree-panel/tree-panel.component';
import { AdminStateService } from '../../services/state/admin-state.service';
import { ADMIN_TREE } from '../../models/tree-node-data.interface';
import { EventSource } from '../../models/event-source.enum';
import { ClientResponse, VendorResponse } from '../../../../api';
import { isDefined, isNullOrUndefined } from '../../../../shared/utils';
import { ClientDataService } from '../../../clients/services/client-data.service';
import { ProgressSpinnerModule } from 'primeng/progressspinner';
import { EntityDetailsService } from '../../services/entity/entity-details.service';
import { AdminSearchService } from '../../services/admin-search.service';
import { TreeSearchResult } from '../../../../shared/models';
import { AuthDataService } from '../../../../core/services/auth-data.service';
import { ApiSchemaService } from '../../../../core/services/api-schema.service';
import { AdminTreeContextExtractorService } from '../../services/tree/admin-tree-context-extractor.service';
import { AdminTreeManagementService } from '../../services/tree/admin-tree-management.service';
import { AdminNavigationService } from '../../services/navigation/admin-navigation.service';
import { AdminTreeEventHandlerService } from '../../services/tree/admin-tree-event-handler.service';
import { TreeNavigationPanelComponent } from './tree-navigation-panel/tree-navigation-panel.component';
import { AdminMainContentComponent } from './admin-main-content/admin-main-content.component';
import { AdminContainerStateService } from '../../services/state/admin-container-state.service';
import { AdminRouteHandlerService } from '../../services/navigation/admin-route-handler.service';
import { AdminScrollManagementService } from '../../services/tree/admin-scroll-management.service';
import { AdminTreeService } from '../../services/tree/admin-tree.service';
import { AdminTreeRefreshService } from '../../services/tree/admin-tree-refresh.service';
import { AdminTreeNodeService } from '../../services/tree/admin-tree-node.service';
import { AdminTreeSearchNavigationService } from '../../services/tree/admin-tree-search-navigation.service';
import { AdminTreeErrorHandlerService } from '../../services/admin-tree-error-handler.service';

import { ButtonModule } from 'primeng/button';
import { TooltipModule } from 'primeng/tooltip';
import { GridConfigurationFactory } from '../../../../shared/components/grid/grid-config-factory';
import { TreeTableConfigurationFactory } from '../../../../shared/components/tree-table/tree-table-config-factory';
import {
  ADMIN_GRID_CONFIGS_PROVIDER,
  ADMIN_TREE_TABLE_CONFIGS,
} from '../../configs';

@Component({
  selector: 'app-admin-container',
  imports: [
    AdaptiveLayoutComponent,
    CommonModule,
    ProgressSpinnerModule,
    TreeNavigationPanelComponent,
    AdminMainContentComponent,
    ButtonModule,
    TooltipModule,
  ],
  templateUrl: './admin-container.component.html',
  styleUrl: './admin-container.component.scss',
  changeDetection: ChangeDetectionStrategy.OnPush,
  providers: [
    GridConfigurationFactory,
    ADMIN_GRID_CONFIGS_PROVIDER,
    TreeTableConfigurationFactory,
    ADMIN_TREE_TABLE_CONFIGS,
  ],
})

/*
 * Admin Container Layout
 *
 * This component uses a PrimeNG p-splitter for resizable panels.
 * The layout adapts between showing/hiding detail panels based on state.
 */
export class AdminContainerComponent implements OnInit, OnDestroy {
  public readonly adminState = inject(AdminStateService);
  private readonly messageService = inject(MessageService);
  private readonly clientDataService = inject(ClientDataService);
  private readonly cdr = inject(ChangeDetectorRef);
  readonly detailsService = inject(EntityDetailsService);
  private readonly adminSearchService = inject(AdminSearchService);
  private readonly adminTreeService = inject(AdminTreeService);
  private readonly authDataService = inject(AuthDataService);
  private readonly apiSchemaService = inject(ApiSchemaService);
  readonly contextExtractor = inject(AdminTreeContextExtractorService);
  private readonly treeManagementService = inject(AdminTreeManagementService);
  private readonly navigationService = inject(AdminNavigationService);
  private readonly treeEventHandler = inject(AdminTreeEventHandlerService);
  private readonly containerState = inject(AdminContainerStateService);
  private readonly routeHandler = inject(AdminRouteHandlerService);
  private readonly scrollManagement = inject(AdminScrollManagementService);
  private readonly treeRefreshService = inject(AdminTreeRefreshService);
  private readonly treeNodeService = inject(AdminTreeNodeService);
  private readonly router = inject(Router);
  private readonly searchNavigation = inject(AdminTreeSearchNavigationService);
  private readonly breakpointObserver = inject(BreakpointObserver);
  private readonly adminTreeErrorHandler = inject(AdminTreeErrorHandlerService);

  readonly isLoading = this.adminState.isLoading;
  readonly selectedItem = this.containerState.selectedItem;

  // Responsive breakpoint signals
  readonly screenSize = signal<'xs' | 'sm' | 'md' | 'lg' | 'xl'>('xl');
  readonly isMobile = computed(() => ['xs', 'sm'].includes(this.screenSize()));
  readonly isTablet = computed(() => this.screenSize() === 'md');
  readonly isDesktop = computed(() => ['lg', 'xl'].includes(this.screenSize()));

  // Computed flags for responsive behavior
  readonly shouldAutoCollapseTree = computed(() => this.isTablet());
  readonly shouldUseDrawerForDetails = computed(
    () => this.isTablet() || this.isMobile(),
  );
  readonly detailsDrawerVisible = signal<boolean>(false);

  // Track manual tree toggle to prevent auto-collapse override
  private userManuallySizedTree = false;

  readonly detailsComponent = signal<Type<unknown> | null>(null);
  readonly detailsInputs = signal<Record<string, unknown>>({});
  readonly hasMoreData = this.containerState.hasMoreData;
  readonly loadingMore = this.containerState.loadingMore;
  selectedTreeNode = this.treeEventHandler.selectedTreeNode;
  readonly scrollToNodeKey = this.scrollManagement.scrollToNodeKey;
  readonly scrollToCenterNodeKey = this.scrollManagement.scrollToCenterNodeKey;

  // Node search loading state
  readonly searchingForNode = this.searchNavigation.searchingForNode;
  readonly searchingNodeKey = this.searchNavigation.searchingNodeKey;

  // Search-related computed values
  readonly searchResults = this.adminSearchService.results;
  readonly isSearching = this.adminSearchService.isSearching;
  readonly hasSearchQuery = this.adminSearchService.hasQuery;

  // Computed loading state for tree (combines general loading + node search loading)
  readonly treeLoading = computed(
    () => this.isLoading() || this.searchingForNode(),
  );

  /**
   * Computed property that builds the hierarchy tree including "Load More" nodes.
   * Now enhanced with search highlighting and expansion.
   */
  readonly hierarchyTree = this.treeManagementService.hierarchyTree;

  activeGridComponent: unknown;
  readonly detailsLoading = signal<boolean>(false);

  /**
   * Returns the CSS class for the main container based on details panel state
   */
  readonly hostClass = computed(() => {
    return this.detailsService.showPanel() ? 'has-details' : '';
  });

  /**
   * Returns the CSS class for the main splitter
   */
  readonly mainSplitterClass = computed(() => {
    return 'main-layout';
  });

  /**
   * Returns the CSS class for the details panel based on its visibility
   */
  readonly detailsPanelClass = computed(() => {
    return this.detailsService.showPanel()
      ? 'details-visible-state'
      : 'details-hidden-state';
  });

  /**
   * Signal to determine if the nested router outlet content should be scrollable.
   * Defaults to false (for grid pages).
   */
  protected nestedContentScrollable =
    this.containerState.nestedContentScrollable;

  faAngleLeft = faAngleLeft;
  faAngleRight = faAngleRight;

  // Collapsible tree state
  readonly treeCollapsed = this.containerState.treeCollapsed;

  // Flag to prevent infinite loops when programmatically changing selected client
  private isNavigatingToClient = false;
  private initialRouteProcessed = false;
  // Flag to track when component is fully initialized and ready for runtime client changes
  private componentFullyInitialized = false;

  // Only show tree if user has access level above client
  readonly showTreePanel = computed(() => {
    // Client-level users never see the tree
    // All other user types (distributor, vendor, super-admin) should see it
    return this.authDataService.shouldShowTreeNavigation();
  });

  toggleTreePanel() {
    this.userManuallySizedTree = true;
    this.containerState.toggleTreePanel();
  }

  constructor() {
    const shouldShowTree = this.authDataService.shouldShowTreeNavigation();

    // Only initialize tree-related services if tree will be shown
    if (shouldShowTree) {
      // Initialize scroll management service with required dependencies
      this.scrollManagement.initialize({
        selectedTreeNode: () => this.selectedTreeNode(),
        setSelectedTreeNode: (node: TreeNode | null) =>
          this.treeEventHandler.setTreeSelection(node),
      });

      // Effect to handle tree panel toggle signals
      effect(() => {
        const shouldToggle = this.treeEventHandler.toggleTreePanel();
        if (shouldToggle) {
          this.toggleTreePanel();
          this.treeEventHandler.toggleTreePanel.set(false);
        }
      });

      // Effect to handle error messages
      effect(() => {
        const errorData = this.treeEventHandler.errorMessage();
        if (errorData) {
          this.handleError(errorData.error, errorData.message);
          this.treeEventHandler.errorMessage.set(null);
        }
      });

      // Effect to handle responsive tree panel collapse
      effect(() => {
        const shouldAutoCollapse = this.shouldAutoCollapseTree();
        const isCurrentlyCollapsed = this.treeCollapsed();

        if (
          shouldAutoCollapse &&
          !isCurrentlyCollapsed &&
          !this.userManuallySizedTree
        ) {
          this.toggleTreePanel();
        }

        if (!shouldAutoCollapse) {
          this.userManuallySizedTree = false;
        }
      });

      // Track previous route-based selection to detect route changes for non-client nodes
      let previousRouteKey: string | undefined = undefined;
      let previousRouteType: string | undefined = undefined;
      effect(() => {
        if (!this.initialRouteProcessed) return;

        const { keyToSelect, typeToSelect } =
          this.routeHandler.getEntityInfoFromRoute();
        const currentRouteKey = isDefined(keyToSelect)
          ? keyToSelect
          : undefined;
        const currentRouteType = isDefined(typeToSelect)
          ? typeToSelect
          : undefined;

        if (
          currentRouteKey === previousRouteKey &&
          currentRouteType === previousRouteType
        ) {
          return;
        }

        previousRouteKey = currentRouteKey;
        previousRouteType = currentRouteType;

        if (isDefined(keyToSelect) && isDefined(typeToSelect)) {
          if (typeToSelect === ADMIN_TREE.NODE_TYPES.CLIENT) {
            return;
          }

          const currentSelected = this.selectedTreeNode();
          if (
            currentSelected?.key === keyToSelect &&
            currentSelected?.type === typeToSelect
          ) {
            return;
          }

          this.treeEventHandler.setEventSource(EventSource.SYSTEM);
          this.scrollManagement.suppressScrollToClient.set(false);

          void Promise.resolve().then(async () => {
            setTimeout(async () => {
              await this.handleRouteBasedSelection(keyToSelect, typeToSelect);
            }, 100);
          });
        }
      });

      let previousCollapsed = this.treeCollapsed();
      effect(() => {
        const collapsed = this.treeCollapsed();
        if (previousCollapsed && !collapsed) {
          this.scrollManagement.suppressScrollToClient.set(false);

          const selectedNode = this.selectedTreeNode();
          if (isDefined(selectedNode?.key)) {
            this.scrollManagement.scrollToCenterNode(selectedNode.key);
          } else {
            const selClient = this.adminState.selectedClient();
            if (
              isDefined(selClient?.id) &&
              this.treeEventHandler.shouldAllowAutoScroll()
            ) {
              this.scrollManagement.scrollToClient(selClient.id);
            }
          }
        }
        previousCollapsed = collapsed;
      });
    }

    // Effect to watch for selected client changes and automatically navigate to it
    let previousClientId: string | undefined = undefined;
    effect(() => {
      const selectedClient = this.clientDataService.getSelectedClientSignal()();
      const currentClientId = selectedClient?.id;

      if (!this.initialRouteProcessed || currentClientId === previousClientId) {
        previousClientId = currentClientId;
        return;
      }

      previousClientId = currentClientId;

      if (!this.componentFullyInitialized) {
        if (isDefined(selectedClient?.id)) {
          this.updateTreeSelectionForCurrentClient(selectedClient).catch(
            () => {},
          );
        }
        return;
      }

      if (isDefined(selectedClient?.id) && !this.isNavigatingToClient) {
        const currentUrl = this.router.url;
        const expectedClientPath = `/admin/client/${selectedClient.id}`;

        if (currentUrl.startsWith(expectedClientPath)) {
          this.updateTreeSelectionForCurrentClient(selectedClient).catch(
            () => {},
          );
          return;
        }

        this.treeEventHandler.setEventSource(EventSource.SYSTEM);
        this.scrollManagement.suppressScrollToClient.set(false);

        void Promise.resolve().then(async () => {
          await this.handleSelectedClientChange(selectedClient);
        });
      }
    });

    // Effect to handle details panel drawer mode (keep this - not tree-specific)
    effect(() => {
      const shouldUseDrawer = this.shouldUseDrawerForDetails();
      const isPanelOpen = this.detailsService.showPanel();

      if (shouldUseDrawer && isPanelOpen) {
        this.detailsDrawerVisible.set(true);
      }

      if (!shouldUseDrawer && this.detailsDrawerVisible()) {
        this.detailsDrawerVisible.set(false);
      }
    });

    // Setup responsive breakpoint detection
    this.setupBreakpointObserver();
  }

  /**
   * Updates tree selection when we're already on the correct route but selection is wrong
   */
  private async updateTreeSelectionForCurrentClient(
    selectedClient: Partial<ClientResponse>,
  ): Promise<void> {
    if (!isDefined(selectedClient.id)) return;

    try {
      // Update admin state
      this.adminState.setSelectedClient(selectedClient as ClientResponse);

      // Prepare tree data if needed
      const searchResult =
        await this.adminSearchService.prepareClientForTreeSelectionWithOwner(
          selectedClient,
        );

      if (searchResult) {
        const treeNodes = this.hierarchyTree();
        const nodeToSelect = this.treeNodeService.findNodeByKey(
          treeNodes,
          selectedClient.id,
        );

        if (nodeToSelect) {
          this.treeEventHandler.setTreeSelection(nodeToSelect);

          // Only scroll if tree is expanded and this is a system event
          if (
            !this.treeCollapsed() &&
            this.treeEventHandler.shouldAllowAutoScroll()
          ) {
            this.scrollManagement.scrollToClient(selectedClient.id);
          }
        }
      }
    } catch (error) {
      console.warn('Error updating tree selection for current client:', error);
    }
  }

  ngOnInit(): void {
    try {
      const shouldShowTree = this.authDataService.shouldShowTreeNavigation();

      if (shouldShowTree) {
        // Initialize the tree event handler with required dependencies
        this.treeEventHandler.initialize({
          getHierarchyTree: () => this.hierarchyTree(),
          findNodeByKeyRecursive: (nodes: TreeNode[], key: string) =>
            this.treeNodeService.findNodeByKey(nodes, key),
        });

        // Initialize route handling using the route handler service
        this.routeHandler.initializeRouteHandling({
          selectNodeFromRouteOrState: () =>
            void this.selectNodeFromRouteOrState(),
          ensureSuperAdminNodeExpansion: () =>
            this.ensureSuperAdminNodeExpansion(),
          onChangeDetection: () => {
            this.cdr.detectChanges();
            this.initialRouteProcessed = true;
            this.componentFullyInitialized = true;
          },
        });

        // Initialize the tree refresh service
        this.treeRefreshService.initialize();
      } else {
        // For client users, just mark as initialized
        this.initialRouteProcessed = true;
        this.componentFullyInitialized = true;
        this.cdr.detectChanges();
      }
    } catch (error) {
      // Check if it's a version mismatch error
      const isVersionMismatch = this.adminTreeErrorHandler.handleAdminTreeError(
        error,
        'component initialization',
      );

      if (!isVersionMismatch) {
        // Handle locally or rethrow for other errors
        console.error('Admin container initialization error:', error);
        throw error;
      }
    }
  }

  ngOnDestroy(): void {
    // Cleanup any subscriptions or resources if needed
    this.treeEventHandler.cleanup();
    this.adminState.clearAllState();
  }

  /**
   * Setup breakpoint observer to detect screen size changes
   */
  private setupBreakpointObserver(): void {
    this.breakpointObserver
      .observe([
        Breakpoints.XSmall,
        Breakpoints.Small,
        Breakpoints.Medium,
        Breakpoints.Large,
        Breakpoints.XLarge,
      ])
      .pipe(takeUntilDestroyed())
      .subscribe((result: BreakpointState) => {
        // Check from largest to smallest to prioritize the most specific match
        if (result.breakpoints[Breakpoints.XLarge]) {
          this.screenSize.set('xl');
        } else if (result.breakpoints[Breakpoints.Large]) {
          this.screenSize.set('lg');
        } else if (result.breakpoints[Breakpoints.Medium]) {
          this.screenSize.set('md');
        } else if (result.breakpoints[Breakpoints.Small]) {
          this.screenSize.set('sm');
        } else if (result.breakpoints[Breakpoints.XSmall]) {
          this.screenSize.set('xs');
        }
      });
  }

  /**
   * Handles when a node is not found in the current tree data
   */
  onNodeNotFoundInData(event: NodeNotFoundEvent): void {
    this.treeEventHandler.onNodeNotFoundInData(event);
  }

  // --- Tree Building Logic with Pagination ---

  /**
   * Ensures the super admin node is expanded by default when using modern schema
   */
  private ensureSuperAdminNodeExpansion(): void {
    const isSuperAdmin = this.authDataService.isSuperAdmin();
    const usesModernSchema = this.apiSchemaService.usesModernSchema();
    const distributor = this.adminState.vendorDistributor();

    if (isSuperAdmin && usesModernSchema && isDefined(distributor)) {
      // Use distributor-scoped expanded nodes with 'super-admin' as the distributor key
      const superAdminExpandedNodes =
        this.adminState.expandedNodes()['super-admin'] ?? new Set();

      if (isNullOrUndefined(superAdminExpandedNodes.has('super-admin-root'))) {
        // Use the new distributor-scoped node expansion method
        this.adminState.setDistributorNodeExpanded(
          'super-admin',
          'super-admin-root',
          true,
        );
      }
    }
  }

  private async navigateToNode(
    nodeType: string,
    nodeKey: string,
  ): Promise<boolean> {
    return this.navigationService.navigateToNode(nodeType, nodeKey);
  }

  private handleError(error: unknown, message: string): void {
    console.error(message, error);
    this.messageService.add({
      severity: 'error',
      summary: 'Error',
      detail: `${message}: ${error instanceof Error ? error.message : 'Unknown error'}`,
    });
  }

  /**
   * Navigates to the selected client from clientDataService and selects it in the tree
   * This method handles the home button functionality
   */
  async navigateToSelectedClient(): Promise<void> {
    const selectedClient = this.clientDataService.getSelectedClient();

    if (!selectedClient || !isDefined(selectedClient.id)) {
      this.messageService.add({
        severity: 'warn',
        summary: 'Warning',
        detail: 'No client selected to navigate to',
      });
      return;
    }

    try {
      // Set this as a system-initiated event to allow auto-scrolling
      this.treeEventHandler.setEventSource(EventSource.SYSTEM);
      // Clear any existing scroll suppression for system events
      this.scrollManagement.suppressScrollToClient.set(false);

      // Check if client has owner information needed for tree navigation
      if (isNullOrUndefined(selectedClient.owner?.id)) {
        // Load the full client data to get owner information
        const fullClient = await this.adminTreeService.fetchClient(
          selectedClient.id,
        );

        if (isDefined(fullClient)) {
          await this.handleSelectedClientChange(fullClient);
        } else {
          console.warn('Could not load full client data for navigation');
          // Fallback navigation
          await this.navigateToNode(
            ADMIN_TREE.NODE_TYPES.CLIENT,
            selectedClient.id,
          );
          this.adminState.setSelectedClient(selectedClient as ClientResponse);
        }
      } else {
        // Client has owner info, use the navigation logic directly
        await this.handleSelectedClientChange(selectedClient);
      }

      // Only expand tree if user explicitly requests it (removed automatic expansion)
      // The toggle button will show the selected client name regardless
    } catch (error) {
      this.handleError(error, 'Error navigating to selected client');
    }
  }

  /**
   * Handles automatic navigation when the selected client changes
   * This prevents infinite loops by setting a flag during navigation
   * Uses the client's owner information for efficient hierarchy loading
   */
  private async handleSelectedClientChange(
    selectedClient: Partial<ClientResponse>,
  ): Promise<void> {
    if (this.isNavigatingToClient || !isDefined(selectedClient.id)) {
      return;
    }

    try {
      this.isNavigatingToClient = true;

      // Always update the admin state first, regardless of tree loading success
      this.adminState.setSelectedClient(selectedClient as ClientResponse);

      // Always navigate to the client
      await this.navigateToNode(
        ADMIN_TREE.NODE_TYPES.CLIENT,
        selectedClient.id,
      );

      // If tree panel is collapsed, still prepare the tree data but don't do visual operations
      if (this.treeCollapsed()) {
        // Prepare tree data in background so it's ready when user expands
        try {
          await this.adminSearchService.prepareClientForTreeSelectionWithOwner(
            selectedClient,
          );

          // Update the selected tree node even when collapsed so it's correct when expanded
          const treeNodes = this.hierarchyTree();
          const nodeToSelect = this.treeNodeService.findNodeByKey(
            treeNodes,
            selectedClient.id,
          );

          if (nodeToSelect) {
            this.treeEventHandler.setTreeSelection(nodeToSelect);
          }
        } catch (error) {
          console.warn('Could not prepare tree data while collapsed:', error);
        }
        return;
      }

      // Tree panel is expanded, so prepare the tree hierarchy and do visual operations
      const searchResult =
        await this.adminSearchService.prepareClientForTreeSelectionWithOwner(
          selectedClient,
        );

      if (searchResult) {
        // Find and select the proper node from the fully loaded tree
        const treeNodes = this.hierarchyTree();
        const nodeToSelect = this.treeNodeService.findNodeByKey(
          treeNodes,
          selectedClient.id,
        );

        if (nodeToSelect) {
          this.treeEventHandler.setTreeSelection(nodeToSelect);
        } else {
          console.warn('Could not find tree node for client after preparation');
        }

        // Scroll to the client since tree is visible
        this.scrollManagement.scrollToClient(selectedClient.id);
      } else {
        console.warn('Could not prepare client for tree selection');
      }
    } catch (error) {
      this.handleError(error, 'Error handling selected client change');
    } finally {
      // Reset the flag to allow future navigation
      this.isNavigatingToClient = false;
    }
  }

  /**
   * Handles node expansion
   */
  async onNodeExpand(node: TreeNode): Promise<void> {
    // User-initiated expansion - suppress auto-scrolling for this action
    this.scrollManagement.temporarilySuppressScrollToClient(0); // Suppress indefinitely until cleared
    this.treeEventHandler.setEventSource(EventSource.USER);
    try {
      // Perform the expansion
      await this.treeEventHandler.onNodeExpand(node);

      // After expansion, scroll to center the expanded node
      // This ensures the newly loaded children are visible
      this.scrollToExpandedNode(node);
    } catch (error) {
      this.handleError(error, 'Error expanding node');
    }
  }

  async onGroupExpand(groupNode: TreeNode): Promise<void> {
    // User-initiated group expansion - suppress auto-scrolling for this action
    this.scrollManagement.temporarilySuppressScrollToClient(0); // Suppress indefinitely until cleared
    this.treeEventHandler.setEventSource(EventSource.USER);
    try {
      await this.treeEventHandler.onNodeExpand(groupNode);
      this.scrollToExpandedNode(groupNode);
    } catch (error) {
      this.handleError(error, 'Error expanding group');
    }
  }

  onNodeCollapse(node: TreeNode): void {
    // User-initiated collapse - suppress auto-scrolling for this action
    this.scrollManagement.temporarilySuppressScrollToClient(0); // Suppress indefinitely until cleared
    this.treeEventHandler.setEventSource(EventSource.USER);
    this.treeEventHandler.onNodeCollapse(node);
  }

  async onNodeSelect(node: TreeNode): Promise<void> {
    // User-initiated selection - suppress auto-scrolling for this action
    this.scrollManagement.temporarilySuppressScrollToClient(0); // Suppress indefinitely until cleared
    this.treeEventHandler.setEventSource(EventSource.USER);

    // Let the tree event handler handle the selection - don't duplicate the logic
    return this.treeEventHandler.onNodeSelect(node);
  }

  onNodeUnselect(node: TreeNode): void {
    this.treeEventHandler.onNodeUnselect(node);
  }

  findSelectedNode(): TreeNode | null {
    // First check the selectedTreeNode from internal state
    const selectedNode = this.selectedTreeNode();
    if (selectedNode) return selectedNode;

    // Get current route information to determine what should be selected
    const { keyToSelect, typeToSelect } = this.getEntityInfoFromRoute();

    if (!isDefined(keyToSelect)) {
      // Fallback to client-based selection if no route-based selection
      const selectedClient = this.adminState.selectedClient();
      if (isNullOrUndefined(selectedClient)) return null;

      const nodes = this.hierarchyTree();
      return this.treeNodeService.findNodeByKey(nodes, selectedClient.id);
    }

    // Find the node based on route parameters
    const nodes = this.hierarchyTree();
    if (isDefined(typeToSelect)) {
      return this.treeNodeService.findNodeByKey(
        nodes,
        keyToSelect,
        typeToSelect,
      );
    } else {
      return this.treeNodeService.findNodeByKey(nodes, keyToSelect);
    }
  }

  // Capture the activated router outlet component
  onRouterOutletActivate(component: unknown): void {
    this.activeGridComponent = component;
  }

  /**
   * Scrolls to center an expanded node to show its children
   */
  private scrollToExpandedNode(node: TreeNode): void {
    if (isDefined(node.key)) {
      // Use a different scroll method that centers the node instead of selecting it
      this.scrollManagement.scrollToCenterNode(node.key);
    }
  }

  /**
   * Gets entity information from route parameters
   */
  private getEntityInfoFromRoute(): {
    keyToSelect: string | null;
    typeToSelect: string | null;
  } {
    return this.routeHandler.getEntityInfoFromRoute();
  }

  /**
   * Selects a node in the tree based on the current route parameters.
   * This method updates the `selectedTreeNode` signal and loads missing data if needed.
   */
  private async selectNodeFromRouteOrState(): Promise<void> {
    const { keyToSelect, typeToSelect } = this.getEntityInfoFromRoute();
    const treeNodes = this.hierarchyTree();

    // If no key to select, clear selection
    if (isNullOrUndefined(keyToSelect)) {
      this.treeEventHandler.setTreeSelection(null);
      return;
    }

    // First try to find the node in the current tree
    const nodeToSelect = this.findNodeToSelect(
      treeNodes,
      keyToSelect,
      typeToSelect,
    );

    if (nodeToSelect) {
      // Node found in tree - select it
      this.treeEventHandler.setTreeSelection(nodeToSelect);
      this.updateStateBasedOnNodeType(nodeToSelect);
      this.scrollManagement.handleNodeSelectionScroll(keyToSelect);
    } else if (isDefined(keyToSelect) && isDefined(typeToSelect)) {
      // Node NOT found in tree - need to load it (important for page refresh!)
      await this.handleRouteBasedSelection(keyToSelect, typeToSelect);
    }
  }

  /**
   * Finds the correct node to select based on key and optional type
   */
  private findNodeToSelect(
    treeNodes: TreeNode[],
    key: string,
    type: string | null,
  ): TreeNode | null {
    // If we have a type, try to find by both key and type first
    if (isDefined(type)) {
      const nodeByType = this.treeNodeService.findNodeByKey(
        treeNodes,
        key,
        type,
      );
      if (nodeByType) return nodeByType;
    }

    // Fall back to just finding by key
    return this.treeNodeService.findNodeByKey(treeNodes, key);
  }

  /**
   * Handles search query changes
   */
  onSearchQueryChange(query: string): void {
    this.treeEventHandler.onSearchQueryChange(query);
  }

  /**
   * Handles search result selection
   */
  async onSearchResultSelect(result: TreeSearchResult): Promise<void> {
    // Clear any existing scroll suppression for system-initiated search result selection
    this.scrollManagement.suppressScrollToClient.set(false);
    return this.treeEventHandler.onSearchResultSelect(result);
  }

  /**
   * Handles search clear
   */
  onSearchClear(): void {
    // Clear any existing scroll suppression when clearing search
    this.scrollManagement.suppressScrollToClient.set(false);
    this.treeEventHandler.onSearchClear();
  }

  /**
   * Handles selection based on route parameters for any node type
   */
  private async handleRouteBasedSelection(
    keyToSelect: string,
    typeToSelect: string | null,
  ): Promise<void> {
    // Check if the node exists in the tree AND is already correctly selected
    const currentSelected = this.selectedTreeNode();
    const treeNodes = this.hierarchyTree();
    const nodeExistsInTree = this.findNodeToSelect(
      treeNodes,
      keyToSelect,
      typeToSelect,
    );

    if (
      nodeExistsInTree &&
      currentSelected?.key === keyToSelect &&
      currentSelected?.type === typeToSelect
    ) {
      return;
    }

    // Prepare tree data based on node type (loads missing clients)
    const searchResult = await this.prepareTreeDataForNodeType(
      keyToSelect,
      typeToSelect,
    );

    if (isDefined(searchResult)) {
      // Update tree selection (this sets the selected node)
      await this.updateTreeSelectionFromRoute(keyToSelect, typeToSelect);

      // After loading and selecting, scroll to the node if tree is visible
      if (
        !this.treeCollapsed() &&
        typeToSelect === ADMIN_TREE.NODE_TYPES.CLIENT
      ) {
        // Small delay to ensure the node is rendered in the tree
        setTimeout(() => {
          this.scrollManagement.scrollToClient(keyToSelect);
        }, 300);
      }
    } else {
      console.warn(
        '[Route Handler] prepareTreeDataForNodeType returned null, cannot proceed',
      );
    }
  }

  /**
   * Prepares tree data based on node type
   */
  private async prepareTreeDataForNodeType(
    keyToSelect: string,
    typeToSelect: string | null,
  ): Promise<unknown> {
    if (typeToSelect === ADMIN_TREE.NODE_TYPES.CLIENT) {
      return this.prepareClientTreeData(keyToSelect);
    }

    return this.prepareNonClientTreeData(typeToSelect);
  }

  /**
   * Prepares tree data for client nodes with distributor context
   */
  private async prepareClientTreeData(
    clientId?: string,
  ): Promise<TreeSearchResult | null> {
    let currentClient = this.adminState.selectedClient();

    // If client is specified but not in state (or different one is in state), load it first
    // This is crucial for page refresh/deep-linking
    if (isDefined(clientId) && currentClient?.id !== clientId) {
      try {
        const fetchedClient = await this.adminTreeService.fetchClient(clientId);
        if (fetchedClient) {
          this.setClientAndSyncServices(fetchedClient);
          // Crucial: ensure context is set so search service can use it
          await this.setVendorContextForClient(fetchedClient);
          currentClient = fetchedClient;
        } else {
          return null;
        }
      } catch (error) {
        console.error(
          `[prepareClientTreeData] Failed to fetch client ${clientId}:`,
          error,
        );
        return null; // Prevent fallback to mismatched client
      }
    }

    if (isNullOrUndefined(currentClient)) {
      return null;
    }

    if (isDefined(clientId) && currentClient.id !== clientId) {
      console.error(
        `[prepareClientTreeData] Client mismatch: requested ${clientId}, but currentClient is ${currentClient.id}`,
      );
      return null;
    }

    // Ensure client data service is updated with latest client
    this.clientDataService.setSelectedClient(currentClient);

    // Try to prepare hierarchy using owner info (targeted lookup)
    let result =
      await this.adminSearchService.prepareClientForTreeSelectionWithOwner(
        currentClient,
      );

    // Fallback: If targeted lookup fails, try global search as a recovery mechanism
    // This handles scenarios where client owner/group info might be inconsistent with loaded tree
    if (!isDefined(result) && isDefined(currentClient.id)) {
      result = await this.adminSearchService.prepareClientForTreeSelection(
        currentClient.id,
      );
    }

    return result;
  }

  /**
   * Prepares tree data for non-client nodes
   */
  private async prepareNonClientTreeData(
    typeToSelect: string | null,
  ): Promise<{ success: boolean } | null> {
    try {
      const treeNodes = this.hierarchyTree();
      if (treeNodes.length > 0) {
        // For non-client nodes, we need to ensure the tree hierarchy is properly loaded
        // This is especially important for vendor/distributor nodes that might not be expanded
        return { success: true };
      }

      console.warn('Tree not loaded for non-client node type:', typeToSelect);
      return null;
    } catch (error) {
      console.warn(
        'Error checking tree state for node type:',
        typeToSelect,
        error,
      );
      return null;
    }
  }

  /**
   * Updates tree selection based on route parameters with distributor context
   */
  private async updateTreeSelectionFromRoute(
    keyToSelect: string,
    typeToSelect: string | null,
  ): Promise<void> {
    await this.searchNavigation.scheduleSelection(
      keyToSelect,
      typeToSelect ?? 'client',
    );

    // Update state for client nodes
    if (typeToSelect === ADMIN_TREE.NODE_TYPES.CLIENT) {
      await this.updateClientStateFromRoute(keyToSelect);
    }
  }

  /**
   * Updates client state when navigating via route
   */
  private async updateClientStateFromRoute(keyToSelect: string): Promise<void> {
    const currentClient = this.adminState.selectedClient();

    if (currentClient?.id === keyToSelect) {
      this.syncClientDataService(currentClient);
    } else {
      await this.loadAndSetClientFromRoute(keyToSelect);
    }
  }

  /**
   * Syncs client data service with current client
   */
  private syncClientDataService(currentClient: Partial<ClientResponse>): void {
    this.clientDataService.setSelectedClient(currentClient);
  }

  /**
   * Loads client data from API and updates state
   */
  private async loadAndSetClientFromRoute(keyToSelect: string): Promise<void> {
    try {
      const client = await this.adminTreeService.fetchClient(keyToSelect);
      if (isDefined(client)) {
        this.setClientAndSyncServices(client);
        await this.setVendorContextForClient(client);
      }
    } catch (error) {
      console.warn('Error loading client data for selection:', error);
    }
  }

  /**
   * Sets client in both admin state and client data service
   */
  private setClientAndSyncServices(client: ClientResponse): void {
    this.adminState.setSelectedClient(client);
    this.clientDataService.setSelectedClient(client);
  }

  /**
   * Sets vendor distributor context for client if available
   */
  private async setVendorContextForClient(
    client: ClientResponse,
  ): Promise<void> {
    if (!client.owner?.id) return;

    const contextId = this.adminState.getActiveContextId();
    if (!isDefined(contextId)) return;

    const vendor = this.adminState.getVendorById(client.owner.id, contextId);
    if (isDefined(vendor)) {
      this.adminState.setClientVendor(vendor as VendorResponse);
    }
  }

  /**
   * Updates application state based on the selected node type
   */
  private updateStateBasedOnNodeType(node: TreeNode): void {
    if (!isDefined(node.type) || !isDefined(node.key)) return;

    if (node.type === ADMIN_TREE.NODE_TYPES.CLIENT) {
      // For clients, ensure clientDataService is updated
      // Note: node.data may be incomplete (ClientListItem), but ClientDataService
      // will automatically fetch complete data if needed via the effect
      const clientData = node.data as Partial<ClientResponse>;
      if (isDefined(clientData)) {
        this.adminState.setSelectedClient(clientData as ClientResponse);
      }
    }
    // Handle other node types as needed
  }
}
