import { inject, Injectable } from '@angular/core';
import { firstValueFrom, map, catchError, of } from 'rxjs';
import {
  AccountsService,
  ClientListItem,
  ClientListResponse,
  ClientResponse,
} from '../../../../api';
import { UtilityService } from '../../../../core/services/utility.service';
import {
  ADMIN_TREE,
  ClientFilters,
} from '../../models/tree-node-data.interface';
import { isDefined } from '../../../../shared/utils';
import { RequestCacheService } from '../../../../shared/services/request-cache.service';
import { ClientDataService } from '../../../clients/services/client-data.service';

/**
 * Service responsible for handling client-related operations in the admin tree
 */
@Injectable({
  providedIn: 'root',
})
export class AdminTreeClientService {
  private readonly accountsService = inject(AccountsService);
  private readonly clientDataService = inject(ClientDataService);
  private readonly utilityService = inject(UtilityService);
  private readonly cacheService = inject(RequestCacheService);
  /**
   * Lists all clients for a given vendor and optional group
   *
   * This is the core method for loading paginated client data. It's used throughout
   * the tree system for:
   * - Initial vendor expansion (first 100 clients)
   * - "Load More" button clicks (next 100 clients)
   * - Automatic pagination during client search (loadClientsUntilFound)
   *
   * Features:
   * - Pagination support (offset/limit)
   * - Filtering by group (direct vendor clients or group clients)
   * - Optional search query for name filtering
   * - Excludes deleted clients (state != DELETED_STATE)
   * - Caching for performance (10 seconds)
   *
   * The returned hasMore flag is critical for pagination logic - it tells the
   * caller whether there are more clients to load beyond the current page.
   *
   * @param vendorId The ID of the vendor to fetch clients for
   * @param groupId Optional group ID to filter clients (undefined = direct vendor clients)
   * @param offset Starting offset for pagination (0-based)
   * @param limit Maximum number of results to return (default: 100)
   * @param searchQuery Optional search query to filter clients by name
   * @returns Promise resolving to {clients, total, hasMore}
   * @throws Error if vendorId is not provided
   */
  async listVendorClients(
    vendorId: string,
    groupId?: string,
    offset?: number,
    limit?: number,
    searchQuery?: string,
  ): Promise<{ clients: ClientListItem[]; total: number; hasMore: boolean }> {
    if (!vendorId) {
      throw new Error('Vendor ID is required');
    }
    const filters: ClientFilters = {
      group: { value: groupId ?? ADMIN_TREE.DEFAULT_GROUP_ID, operator: '=' },
      state: { value: ADMIN_TREE.DELETED_STATE, operator: '!=' },
    };

    // Add search query filter if provided
    if (isDefined(searchQuery) && searchQuery.trim().length > 0) {
      filters['name'] = `*${searchQuery.trim()}*`;
    }

    const rqlFilter = this.utilityService.constructRqlFilter(filters);

    // Include search query in cache key for proper caching
    const searchSuffix = isDefined(searchQuery) ? `-search-${searchQuery}` : '';
    const cacheKey = `admin-tree-clients-${vendorId}-${groupId ?? 'all'}-${offset ?? 0}-${limit ?? ADMIN_TREE.PAGINATION.DEFAULT_PAGE_SIZE}${searchSuffix}`;

    return this.cacheService.getOrFetchAsync(
      cacheKey,
      () =>
        firstValueFrom(
          this.accountsService
            .listClients(
              vendorId,
              offset ?? ADMIN_TREE.PAGINATION.INITIAL_PAGE,
              limit ?? ADMIN_TREE.PAGINATION.DEFAULT_PAGE_SIZE,
              'name',
              rqlFilter,
            )
            .pipe(
              map((response: ClientListResponse) => {
                return {
                  clients: response.items,
                  total: response.count,
                  hasMore:
                    response.count >
                    (offset ?? 0) + (response.items.length ?? 0),
                };
              }),
            ),
        ),
      10000,
    );
  }

  /**
   * Fetches a single client by ID
   * @param clientId - The ID of the client to fetch
   * @returns Promise resolving to ClientResponse or null if not found
   * @throws Error if clientId is not provided
   */
  async fetchClient(clientId: string): Promise<ClientResponse | null> {
    if (!clientId) {
      throw new Error('Client ID is required');
    }

    return firstValueFrom(
      this.clientDataService
        .fetchClientWithCache(clientId)
        .pipe(catchError(() => of(null))),
    );
  }

  /**
   * Attempts to load a client using saved ID or default ID
   * @param savedClientId - Previously saved client ID
   * @param defaultClientId - Fallback client ID
   * @returns Promise resolving to ClientResponse or null if neither client exists
   */
  async tryLoadClient(
    savedClientId?: string,
    defaultClientId?: string,
  ): Promise<ClientResponse | null> {
    if (isDefined(savedClientId)) {
      const client = await this.fetchClient(savedClientId);
      if (client) return client;
    }

    if (isDefined(defaultClientId)) {
      return this.fetchClient(defaultClientId);
    }

    return null;
  }
}
