import { Injectable, inject } from '@angular/core';
import { VersionUpdateService } from '../../../core/services/version-update.service';

@Injectable({
  providedIn: 'root',
})
export class AdminTreeErrorHandlerService {
  private readonly versionUpdateService = inject(VersionUpdateService);

  /**
   * Handles errors from admin tree operations
   * Returns true if error was a version mismatch (reload triggered)
   * Returns false if error should be handled locally
   *
   * IMPORTANT: If version mismatch detected, this method handles the reload.
   * If not a version mismatch, the error should be re-thrown or handled locally.
   */
  handleAdminTreeError(error: unknown, context: string): boolean {
    console.error(`Admin tree error (${context}):`, error);

    // Check if this is a version mismatch
    if (this.versionUpdateService.isVersionMismatchError(error)) {
      console.log('Version mismatch detected in admin tree context:', context);
      this.versionUpdateService.handleVersionMismatch(`admin tree: ${context}`);
      return true;
    }

    // Not a version mismatch, return false so component handles it locally
    console.log('Not a version mismatch, returning for local handling');
    return false;
  }
}
