import { CommonModule } from '@angular/common';
import {
  ChangeDetectorRef,
  Component,
  DestroyRef,
  OnInit,
  TemplateRef,
  effect,
  inject,
  signal,
  viewChild,
} from '@angular/core';
import { takeUntilDestroyed } from '@angular/core/rxjs-interop';
import { ReactiveFormsModule } from '@angular/forms';

import { ButtonModule } from 'primeng/button';
import { DynamicDialogRef, DynamicDialogConfig } from 'primeng/dynamicdialog';

import {
  EntitiesService,
  SimCardCreateRequest,
  SimCardResponse,
} from '../../../../../api';
import { WizardComponent } from '../../../../../shared/components/wizard/wizard.component';
import { isDefined, isNullOrUndefined } from '../../../../../shared/utils';
import { SimCardDetailsSectionComponent } from '../sim-card-details-section/sim-card-details-section.component';
import { Step } from '../../../../../shared/models';
import { BroadcastEventMessageService } from '../../../services/broadcast-event-messages.service';
import { finalize } from 'rxjs';

@Component({
  selector: 'app-sim-card-create-wizard',
  imports: [
    CommonModule,
    ReactiveFormsModule,
    WizardComponent,
    ButtonModule,
    SimCardDetailsSectionComponent,
  ],
  templateUrl: './sim-card-create-wizard.component.html',
  styleUrl: './sim-card-create-wizard.component.scss',
})
export class SimCardCreateWizardComponent implements OnInit {
  simCardDetailsStep = viewChild<
    TemplateRef<{
      $implicit: Step;
    }>
  >('simCardDetailsStep');
  confirmationTemplate = viewChild<TemplateRef<{ $implicit: Step }>>(
    'confirmationTemplate',
  );

  // References to form components
  simCardDetailsForm =
    viewChild<SimCardDetailsSectionComponent>('simCardDetailsForm');

  private readonly entitiesService = inject(EntitiesService);
  private readonly broadcastService = inject(BroadcastEventMessageService);
  protected readonly destroyRef = inject(DestroyRef);
  private readonly dialogRef = inject(DynamicDialogRef);
  private readonly dialogConfig = inject(DynamicDialogConfig);
  private readonly cdr = inject(ChangeDetectorRef);

  // SimCard model to collect data across steps
  newSimCard = signal<Partial<SimCardCreateRequest>>({});

  // Form validity state
  formIsValid = signal<boolean>(false);

  displayWizard = false;
  steps: Step[] = [];
  previousStepIndex = 0;
  currentStepIndex = 0;
  ownerId: string | null = null;
  isSubmitting = signal<boolean>(false);

  constructor() {
    effect(() => {
      const simCardDetailsStep = this.simCardDetailsStep();
      const confirmationTemplate = this.confirmationTemplate();

      if (
        isNullOrUndefined(simCardDetailsStep) ||
        isNullOrUndefined(confirmationTemplate)
      ) {
        return;
      }
      this.steps = [
        {
          value: 1,
          header: 'Sim Card Details',
          content: simCardDetailsStep,
          complete: false,
          validate: () => this.simCardDetailsForm()?.validate() ?? false,
        },
        {
          value: 2,
          header: 'Confirmation',
          content: confirmationTemplate,
          complete: true,
        },
      ];
    });
  }

  ngOnInit(): void {
    if (isDefined(this.dialogConfig.data)) {
      this.ownerId = this.dialogConfig.data.ownerId ?? null;

      // Initialize simCard with ownerId
      this.newSimCard.update((current) => ({
        ...current,
        ownerId: this.ownerId ?? '',
      }));
    }
  }

  openWizard() {
    this.displayWizard = true;
    this.newSimCard.set({});
  }

  // Form validity handlers
  onDetailsFormValidityChange(isValid: boolean) {
    this.steps[0].disableNext = !isValid;
    this.formIsValid.set(isValid);
  }

  // Update the simCard state with form data
  updateSimCardDetails(formData: Record<string, unknown>) {
    this.newSimCard.update((current) => {
      const updated = { ...current };

      Object.keys(formData).forEach((key) => {
        if (key !== 'ownerId') {
          updated[key] = formData[key];
        }
      });

      if (isNullOrUndefined(updated.ownerId)) {
        updated.ownerId = this.ownerId ?? '';
      }

      return updated;
    });
  }

  onWizardComplete(): void {
    // Process the final data from all steps
    const simCardData = this.newSimCard() as SimCardCreateRequest;
    this.isSubmitting.set(true);

    this.entitiesService
      .createSimCard(simCardData)
      .pipe(
        takeUntilDestroyed(this.destroyRef),
        finalize(() => this.isSubmitting.set(false)),
      )
      .subscribe({
        next: (response: SimCardResponse) => {
          this.broadcastService.broadcast('entity-updated', {
            entityType: 'simcard',
            operation: 'create',
            entityId: response.id,
          });
          this.dialogRef.close(response);
        },
        error: (error: Error) => {
          // Handle error, could show message and keep dialog open
          console.error('Error creating sim card:', error);
        },
      });
  }

  // Update cancel to close dialog with no result
  onWizardCancel(): void {
    this.newSimCard.set({});
    this.dialogRef.close();
  }
}
