import { Injectable } from '@angular/core';
import { TableLazyLoadEvent } from 'primeng/table';
import { DataGridParams, FilterCriteria, FilterValue } from '../models';
import { DEFAULT_PAGE_SIZE, DEFAULT_SYSTEM_ID } from '../../core/models';
import { isDefined } from '../utils';

type FilterTransformation = (field: string) => string;

@Injectable({
  providedIn: 'root',
})
export class GridBaseService {
  handleFilters(
    currentFilters: Record<string, FilterCriteria>,
    field: string,
    value: FilterValue,
    transformations?: Record<string, FilterTransformation>,
    filterMatchMode?: string,
  ): Record<string, FilterCriteria> {
    if (field === '*' && value === null) {
      return {};
    }

    const newFilters = { ...currentFilters };

    if (isDefined(value) && value.toString().length > 0) {
      // Apply field transformations if provided
      const transformedField = transformations?.[field]
        ? transformations[field](field)
        : field;

      let processedValue = value;
      let operator: '=' | '!=' | '>' | '<' | '>=' | '<=' | 'like' | 'in' = '=';

      // Handle different filter match modes
      if (filterMatchMode === 'contains' && typeof value === 'string') {
        processedValue = `*${value}*`;
      } else if (
        filterMatchMode === 'startsWith' &&
        typeof value === 'string'
      ) {
        processedValue = `${value}*`;
      } else if (filterMatchMode === 'endsWith' && typeof value === 'string') {
        processedValue = `*${value}`;
      } else if (filterMatchMode === 'in') {
        operator = 'in';
      }

      newFilters[transformedField] = { value: processedValue, operator };
    } else {
      delete newFilters[field];
    }

    return newFilters;
  }

  constructGridParams(
    baseParams: Record<string, unknown>,
    event?: TableLazyLoadEvent,
    defaultPageSize = DEFAULT_PAGE_SIZE,
  ): DataGridParams {
    return {
      ...baseParams,
      owner: (baseParams['owner'] as string) || DEFAULT_SYSTEM_ID,
      offset: isDefined(event?.first) ? event.first : 0,
      limit: isDefined(event?.rows) ? event.rows : defaultPageSize,
      sortField: isDefined(event?.sortField) ? event.sortField : 'name',
      sortOrder: isDefined(event?.sortOrder) ? event.sortOrder : 'asc',
    };
  }
}
