import { Injectable, inject } from '@angular/core';
import {
  HttpClient,
  HttpContext,
  HttpEvent,
  HttpHeaders,
  HttpResponse,
} from '@angular/common/http';
import { Observable } from 'rxjs';
import { Configuration } from '../../../api/configuration';
import { BulkImportResponse } from '../../../api/model/bulkImportResponse';

@Injectable({
  providedIn: 'root',
})
export class BulkImportService {
  private readonly httpClient = inject(HttpClient);
  private readonly configuration = inject(Configuration);

  /**
   * Bulk imports devices, SIM cards, and assets from a CSV file.
   * @param ownerId The owner ID (Client/Company) for the entities being imported.
   * @param file The CSV file containing the devices, SIM cards, and assets to import.
   * @param observe Set whether or not to return the data Observable as the body, response or events.
   * @param reportProgress Flag to report request and response progress.
   */
  public bulkImportDevices(
    ownerId: string,
    file: File,
    observe?: 'body',
    reportProgress?: boolean,
    options?: { httpHeaderAccept?: 'application/json'; context?: HttpContext },
  ): Observable<BulkImportResponse>;
  public bulkImportDevices(
    ownerId: string,
    file: File,
    observe?: 'response',
    reportProgress?: boolean,
    options?: { httpHeaderAccept?: 'application/json'; context?: HttpContext },
  ): Observable<HttpResponse<BulkImportResponse>>;
  public bulkImportDevices(
    ownerId: string,
    file: File,
    observe?: 'events',
    reportProgress?: boolean,
    options?: { httpHeaderAccept?: 'application/json'; context?: HttpContext },
  ): Observable<HttpEvent<BulkImportResponse>>;
  public bulkImportDevices(
    ownerId: string,
    file: File,
    observe: 'body' | 'response' | 'events' = 'body',
    reportProgress = false,
    options?: { httpHeaderAccept?: 'application/json'; context?: HttpContext },
  ): Observable<
    | BulkImportResponse
    | HttpResponse<BulkImportResponse>
    | HttpEvent<BulkImportResponse>
  > {
    if (ownerId === null || ownerId === undefined) {
      throw new Error(
        'Required parameter ownerId was null or undefined when calling bulkImportDevices.',
      );
    }
    if (file === null || file === undefined) {
      throw new Error(
        'Required parameter file was null or undefined when calling bulkImportDevices.',
      );
    }

    const queryParameters = new URLSearchParams();
    queryParameters.append('ownerId', ownerId);

    let localVarHeaders = new HttpHeaders();

    // authentication (access_token) required
    const localVarCredential =
      this.configuration.lookupCredential('access_token');
    if (
      localVarCredential !== null &&
      localVarCredential !== undefined &&
      localVarCredential !== ''
    ) {
      localVarHeaders = localVarHeaders.set(
        'x-access-token',
        localVarCredential,
      );
    }

    let localVarHttpHeaderAcceptSelected: string | undefined =
      options && options.httpHeaderAccept;
    if (localVarHttpHeaderAcceptSelected === undefined) {
      const httpHeaderAccepts: string[] = ['application/json'];
      localVarHttpHeaderAcceptSelected =
        this.configuration.selectHeaderAccept(httpHeaderAccepts);
    }
    if (localVarHttpHeaderAcceptSelected !== undefined) {
      localVarHeaders = localVarHeaders.set(
        'Accept',
        localVarHttpHeaderAcceptSelected,
      );
    }

    let localVarHttpContext: HttpContext | undefined =
      options && options.context;
    if (localVarHttpContext === undefined) {
      localVarHttpContext = new HttpContext();
    }

    // Create FormData for multipart upload
    const formData = new FormData();
    formData.append('file', file, file.name);

    const localVarPath = `/entities/devices/bulk-import?${queryParameters.toString()}`;

    if (observe === 'response') {
      return this.httpClient.request<BulkImportResponse>(
        'post',
        `${this.configuration.basePath}${localVarPath}`,
        {
          context: localVarHttpContext,
          body: formData,
          responseType: 'json',
          withCredentials: this.configuration.withCredentials,
          headers: localVarHeaders,
          observe: 'response',
          reportProgress: reportProgress,
        },
      );
    } else if (observe === 'events') {
      return this.httpClient.request<BulkImportResponse>(
        'post',
        `${this.configuration.basePath}${localVarPath}`,
        {
          context: localVarHttpContext,
          body: formData,
          responseType: 'json',
          withCredentials: this.configuration.withCredentials,
          headers: localVarHeaders,
          observe: 'events',
          reportProgress: reportProgress,
        },
      );
    } else {
      return this.httpClient.request<BulkImportResponse>(
        'post',
        `${this.configuration.basePath}${localVarPath}`,
        {
          context: localVarHttpContext,
          body: formData,
          responseType: 'json',
          withCredentials: this.configuration.withCredentials,
          headers: localVarHeaders,
          observe: 'body',
          reportProgress: reportProgress,
        },
      );
    }
  }
}
