import { inject } from '@angular/core';
import { CanActivateFn, Router, ActivatedRouteSnapshot } from '@angular/router';
import { AuthDataService } from '../services/auth-data.service';
import { OwnerType } from '../models/permission.model';

const ownerTierRank: Record<OwnerType, number> = {
  client: 0,
  vendor: 1,
  distributor: 2,
  system: 3,
};

/**
 * Guard to enforce minimum owner tier for route access.
 *
 * Usage:
 * data: { requiredOwnerType: 'vendor' }
 */
export const ownerTierGuard: CanActivateFn = (
  route: ActivatedRouteSnapshot,
) => {
  const authDataService = inject(AuthDataService);
  const router = inject(Router);
  const requiredOwnerType = route.data['requiredOwnerType'] as
    | OwnerType
    | undefined;

  if (!requiredOwnerType) {
    return true;
  }

  const currentOwnerType =
    authDataService.getUserOwnerType() as OwnerType | null;
  if (!currentOwnerType) {
    router
      .navigate(['/unauthorized'], {
        queryParams: { reason: 'owner_type_missing' },
      })
      .catch(() => {});
    return false;
  }

  const allowed =
    ownerTierRank[currentOwnerType] >= ownerTierRank[requiredOwnerType];

  if (!allowed) {
    router
      .navigate(['/unauthorized'], {
        queryParams: { reason: 'owner_type_restricted' },
      })
      .catch(() => {});
    return false;
  }

  return true;
};
