/**
 * Type-safe entity type keys
 */
export type GridEntityType =
  (typeof GRID_ENTITY_TYPES)[keyof typeof GRID_ENTITY_TYPES];

/**
 * Helper function to create a grid configuration entry
 */
function createGridConfigEntry<T>(
  entityType: GridEntityType,
  config: Type<GridConfiguration<T>>,
): GridConfigurationEntry {
  return { entityType, config: config as Type<GridConfiguration<unknown>> };
}

