/**
 * Provider for all administration grid configurations
 * Organized by scope (client, vendor, distributor, system) for better maintainability
 */
export const ADMIN_GRID_CONFIGS_PROVIDER: Provider = {
  provide: GRID_CONFIGS,
  useValue: ALL_GRID_CONFIGS,
  multi: true,
};
