import { ResolveFn } from '@angular/router';
import { AccountsService, VendorResponse } from '../../../api';
import { inject } from '@angular/core';
import { catchError, of } from 'rxjs';
import { MessageService } from 'primeng/api';
import { isNullOrUndefined } from '../../../shared/utils';

export const vendorResolver: ResolveFn<VendorResponse | null> = (
  route,
  _state,
) => {
  const accountsService = inject(AccountsService);
  const messageService = inject(MessageService);
  const vendorId = route.paramMap.get('vendorId');
  if (isNullOrUndefined(vendorId)) {
    messageService.add({
      severity: 'error',
      summary: 'Error',
      detail: 'Vendor ID not found',
    });
    return of(null);
  }
  return accountsService.getVendor(vendorId).pipe(
    catchError(() => {
      messageService.add({
        severity: 'error',
        summary: 'Error',
        detail: 'Failed to load vendor data',
      });
      return of(null);
    }),
  );
};
