import {
  DestroyRef,
  Injectable,
  computed,
  effect,
  inject,
  signal,
} from '@angular/core';

import { TableLazyLoadEvent } from 'primeng/table';
import { DialogService, DynamicDialogRef } from 'primeng/dynamicdialog';
import { catchError, combineLatest, EMPTY } from 'rxjs';
import { takeUntilDestroyed } from '@angular/core/rxjs-interop';

import {
  GridConfiguration,
  GridDataService,
  DataGridParams,
  GridColumn,
  FilterCriteria,
  FilterValue,
  DATE_FORMATS,
  AssetGroupingItem,
} from '../../../shared/models';
import { UtilityService } from '../../../core/services/utility.service';
import { GridBaseService } from '../../../shared/services/grid-base.service';
import { isDefined } from '../../../shared/utils';
import { AssetsDataAdapter } from '../services/adapters';
import { AssetListItem, EntitiesService, ClientResponse } from '../../../api';
import { DEFAULT_PAGE_SIZE, DEFAULT_SYSTEM_ID } from '../../../core/models';
import { AssetCreateWizardComponent } from '../components/assets/asset-create-wizard/asset-create-wizard.component';
import { MeasurementDateTimeService } from '../../../shared/services/measurement-units/measurement-date-time.service';
import { AdminStateService } from '../services/state/admin-state.service';
import { AssetGroupingService } from '../../../shared/services/asset-grouping.service';
import { BulkImportDialogComponent } from '../components/shared/bulk-import-dialog/bulk-import-dialog.component';
@Injectable({
  providedIn: 'root',
})
export class AssetsGridConfig implements GridConfiguration<AssetListItem> {
  entityType = 'assets';

  private readonly assetsService = inject(AssetsDataAdapter);
  private readonly utilityService = inject(UtilityService);
  private readonly gridBaseService = inject(GridBaseService);
  private readonly entitiesService = inject(EntitiesService);
  private readonly adminStateService = inject(AdminStateService);
  private readonly assetGroupingService = inject(AssetGroupingService);
  private readonly defaultStateValues = ['active', 'suspended', 'inactive'];
  private readonly filterState = signal<Record<string, FilterCriteria>>({
    state: {
      value: this.defaultStateValues,
      operator: 'in',
    },
  });
  private readonly paramsSignal = signal<Record<string, string>>({
    clientId: '',
  });

  private readonly destroyRef = inject(DestroyRef);
  private dialogRef: DynamicDialogRef | null = null;
  private readonly dialogService = inject(DialogService);
  private readonly measurementDateTimeService = inject(
    MeasurementDateTimeService,
  );

  private readonly rqlFilter = computed(() =>
    this.utilityService.constructRqlFilter({
      ...this.filterState(),
      ...{
        'owner.id': {
          value: this.paramsSignal()['clientId'],
          operator: '=',
        },
      },
    }),
  );
  private readonly fieldTransformations: Record<string, string> = {
    costCentre: 'costCentre.id',
    assetType: 'assetType.id',
    groups: 'groups.id',
    categories: 'categories.id',
  };

  columnsSignal = signal<GridColumn<AssetListItem>[]>([]);

  get columns(): GridColumn<AssetListItem>[] {
    return this.columnsSignal();
  }

  private readonly baseColumns: GridColumn<AssetListItem>[] = [
    {
      field: 'name',
      header: 'Name',
      filter: true,
      filterType: 'text',
      filterMatchMode: 'contains',
      transform: 'text-ellipsis',
    },
    {
      field: 'assetType',
      header: 'Type',
      valueGetter: (rowData: AssetListItem) =>
        isDefined(rowData.assetType?.name) ? rowData.assetType?.name : '',
      filterType: 'multiselect',
      filter: true,
      filterMatchMode: 'in',
      filterConfig: {
        type: 'multiselect',
        options: [],
      },
    },
    {
      field: 'state',
      header: 'State',
      filter: true,
      filterType: 'multiselect',
      filterMatchMode: 'in',
      filterConfig: {
        type: 'multiselect',
        options: [
          { label: 'Active', value: 'active' },
          { label: 'Suspended', value: 'suspended' },
          { label: 'Deleted', value: 'deleted' },
          { label: 'Inactive', value: 'inactive' },
        ],
        defaultSelected: this.defaultStateValues,
      },
      transform: 'capitalize',
    },
    {
      field: 'costCentre',
      header: 'Cost Centre',
      valueGetter: (rowData: AssetListItem) =>
        isDefined(rowData.costCentre?.name) ? rowData.costCentre?.name : '',
      filter: true,
      filterType: 'multiselect',
      filterMatchMode: 'in',
      filterConfig: {
        type: 'multiselect',
        options: [],
      },
    },
    {
      field: 'groups',
      header: 'Groups',
      valueGetter: (rowData: AssetListItem) =>
        isDefined(rowData.groups)
          ? rowData.groups.map((g) => g.name).join(', ')
          : '',
      filter: true,
      filterType: 'multiselect',
      filterMatchMode: 'in',
      filterConfig: {
        type: 'multiselect',
        options: [],
      },
    },
    {
      field: 'devices',
      header: 'Devices',
      valueGetter: (rowData: AssetListItem) =>
        isDefined(rowData.devices)
          ? rowData.devices.map((d) => d.name).join(', ')
          : '',
      filter: true,
      filterMatchMode: 'contains',
      filterType: 'text',
    },
    {
      field: 'cameras',
      header: 'Cameras',
      filter: false,
    },
    {
      field: 'linkedFrom',
      header: 'Linked From Client',
      valueGetter: (rowData: AssetListItem) => {
        return isDefined(rowData['linkedFrom'])
          ? rowData['linkedFrom'].map((l: string) => l).join(', ')
          : '';
      },
      filter: true,
      filterType: 'text',
    },
    {
      field: 'linkedTo',
      header: 'Linked To Client',
      valueGetter: (rowData: AssetListItem) => {
        return isDefined(rowData['linkedTo'])
          ? rowData['linkedTo'].map((l: string) => l).join(', ')
          : '';
      },
      filter: true,
      filterType: 'text',
    },
    {
      field: 'modifiedDate',
      header: 'Modified',
      filter: false,
      valueGetter: (rowData: AssetListItem) => {
        return isDefined(rowData['modifiedDate'])
          ? this.measurementDateTimeService.formatDateTime(
              this.measurementDateTimeService.createDateAssumingUtc(
                rowData['modifiedDate'],
              ),
              DATE_FORMATS.DATE_TIME_SECONDS,
            )
          : rowData['modifiedDate'];
      },
    },
  ];

  defaultVisibleColumns = [
    'name',
    'assetType',
    'state',
    'costCentre',
    'groups',
    'devices',
    'cameras',
    'modifiedDate',
  ];

  dataService: GridDataService<AssetListItem> = this.assetsService;

  constructor() {
    effect(() => {
      const client = this.adminStateService.selectedClient();
      if (isDefined(client?.id)) {
        void this.buildColumns(client);
      }
    });
  }

  private async buildColumns(client: ClientResponse): Promise<void> {
    const clientId = client.id;

    // Fetch categories and build complete column list
    await this.assetGroupingService
      .getAssetCategories(clientId)
      .then((assetCategories) => {
        const newColumns: GridColumn<AssetListItem>[] = [...this.baseColumns];

        // Add category columns
        this.addCategoryColumns(newColumns, assetCategories);

        // Add custom field columns
        this.addCustomFieldColumns(newColumns, client);

        this.columnsSignal.set(newColumns);
      });
  }

  private addCategoryColumns(
    columns: GridColumn<AssetListItem>[],
    assetCategories: AssetGroupingItem[],
  ): void {
    const deviceIndex = columns.findIndex((col) => col.field === 'devices');
    const insertIndex = deviceIndex !== -1 ? deviceIndex : columns.length - 2;
    const categoryColumns = assetCategories
      .filter((category) => category.parent === DEFAULT_SYSTEM_ID)
      .map((category) => this.createCategoryColumn(category, assetCategories));

    columns.splice(insertIndex, 0, ...categoryColumns);
  }

  private createCategoryColumn(
    category: AssetGroupingItem,
    assetCategories: AssetGroupingItem[],
  ): GridColumn<AssetListItem> {
    return {
      field: `category.${category.index}`,
      header: isDefined(category.name)
        ? category.name
        : `Category ${category.index}`,
      valueGetter: (rowData: AssetListItem) => {
        const cat = isDefined(category?.index)
          ? rowData.categories[category.index]
          : undefined;
        return isDefined(cat) ? (cat.name as string) : '';
      },
      filter: true,
      filterType: 'multiselect',
      filterMatchMode: 'in',
      filterConfig: {
        type: 'multiselect',
        options: assetCategories
          .filter((c) => c.parent === category.id)
          .map((c) => ({ label: isDefined(c.name) ? c.name : '', value: c.id }))
          .sort((a, b) => a.label.localeCompare(b.label)),
      },
    };
  }

  private addCustomFieldColumns(
    columns: GridColumn<AssetListItem>[],
    client: { customFields?: { asset?: { id: string; title: string }[] } },
  ): void {
    const modifiedIndex = columns.findIndex(
      (col) => col.field === 'modifiedDate',
    );
    const customFieldInsertIndex =
      modifiedIndex !== -1 ? modifiedIndex : columns.length;

    const customFieldColumns = (client?.customFields?.asset ?? []).map(
      (customField) => ({
        field: `customFields.${customField.id}`,
        header: customField.title,
        filter: true,
        filterType: 'text' as const,
        filterMatchMode: 'contains' as const,
      }),
    );

    columns.splice(customFieldInsertIndex, 0, ...customFieldColumns);
  }

  getFilters(): Record<string, FilterCriteria> {
    return this.filterState();
  }

  clearFilters(): void {
    this.filterState.set({
      state: {
        value: this.defaultStateValues,
        operator: 'in',
      },
    });
  }

  initialize(params: Record<string, string>): void {
    this.paramsSignal.set(params);
    this.loadFilterOptions();
    // Trigger column rebuild when params change
    const client = this.adminStateService.selectedClient();
    if (isDefined(client?.id)) {
      void this.buildColumns(client);
    }
  }

  getTitle(): string {
    return 'Assets';
  }

  getRequestParams(event?: TableLazyLoadEvent): DataGridParams {
    let sortField = 'name:asc'; // Default

    if (isDefined(event?.sortField)) {
      const sortOrder = event.sortOrder === 1 ? 'asc' : 'desc';
      sortField = `${event.sortField}:${sortOrder}`;
    }

    return {
      owner: this.paramsSignal()['clientId'],
      offset: isDefined(event?.first) ? event.first : 0,
      recurse: false,
      limit: isDefined(event?.rows) ? event.rows : DEFAULT_PAGE_SIZE,
      sortField: sortField,
      filters: this.rqlFilter(),
    };
  }

  onFilterChange(field: string, value: FilterValue): void {
    let actualField = this.fieldTransformations[field] || field;

    // Transform category.* fields to categories.id for filtering
    if (field.startsWith('category.')) {
      actualField = 'categories.id';
    }

    const column = this.columnsSignal().find((col) => col.field === field);

    if (Array.isArray(value)) {
      const newFilters = this.gridBaseService.handleFilters(
        this.filterState(),
        actualField,
        value,
        undefined,
        column?.filterMatchMode,
      );

      this.filterState.set(newFilters);
      return;
    }

    // For non-array values, use the grid base service
    const newFilters = this.gridBaseService.handleFilters(
      this.filterState(),
      actualField,
      value,
      undefined,
      column?.filterMatchMode,
    );

    this.filterState.set(newFilters);
  }

  private loadFilterOptions(): void {
    combineLatest([
      this.entitiesService.listAssetTypes(this.paramsSignal()['clientId']),
      this.entitiesService.listCostCentres(this.paramsSignal()['clientId']),
      this.entitiesService.listAssetGroups(this.paramsSignal()['clientId']),
    ])
      .pipe(
        takeUntilDestroyed(this.destroyRef),
        catchError(() => {
          return EMPTY;
        }),
      )
      .subscribe(([types, costCentres, listAssetGroups]) => {
        const currentColumns = this.columnsSignal();

        const typeColumn = currentColumns.find(
          (col) => col.field === 'assetType',
        );
        if (typeColumn?.filterConfig) {
          typeColumn.filterConfig.options = types.items
            .map((type) => ({
              label: type.name,
              value: type.id,
            }))
            .sort((a, b) => (a.label ?? '').localeCompare(b.label ?? ''));
        }

        const costCentreColumn = currentColumns.find(
          (col) => col.field === 'costCentre',
        );
        if (costCentreColumn?.filterConfig) {
          costCentreColumn.filterConfig.options = costCentres.items
            .map((costCentre) => ({
              label: costCentre.name ?? '',
              value: costCentre.id,
            }))
            .sort((a, b) => a.label.localeCompare(b.label));
        }

        const groupColumn = currentColumns.find(
          (col) => col.field === 'groups',
        );
        if (groupColumn?.filterConfig) {
          groupColumn.filterConfig.options = listAssetGroups.items
            .map((group) => ({
              label: group.name ?? '',
              value: group.id,
            }))
            .sort((a, b) => a.label.localeCompare(b.label));
        }

        // Update the signal with the modified columns
        this.columnsSignal.set([...currentColumns]);
      });
  }

  onAdd(): void {
    const clientId = this.paramsSignal()['clientId'];

    this.openAssetCreationDialog(clientId).subscribe((createdAsset) => {
      if (isDefined(createdAsset)) {
        // Refresh your asset list or take any other necessary actions
        // this.refreshAssets();
      }
    });
  }

  openAssetCreationDialog(ownerId: string) {
    this.dialogRef = this.dialogService.open(AssetCreateWizardComponent, {
      header: 'Asset Wizard',
      contentStyle: { overflow: 'auto' },
      baseZIndex: 10000,
      data: {
        ownerId: ownerId,
      },
      position: 'top',
      modal: true,
    });

    return (this.dialogRef as DynamicDialogRef).onClose;
  }

  onBulkImport(): void {
    const clientId = this.paramsSignal()['clientId'];

    this.openBulkImportDialog(clientId);
  }

  openBulkImportDialog(ownerId: string) {
    this.dialogRef = this.dialogService.open(BulkImportDialogComponent, {
      header: 'Bulk Import Assets & Devices',
      baseZIndex: 10000,
      data: {
        ownerId: ownerId,
      },
      position: 'top',
      modal: true,
      width: '800px',
    });

    return (this.dialogRef as DynamicDialogRef).onClose;
  }
}
