import { Injectable } from '@angular/core';
import { TreeNode } from 'primeng/api';
import { ContextExtractor } from '../../../../shared/components/tree-panel/tree-panel.component';
import { isDefined } from '../../../../shared/utils';

/**
 * Context extractor for admin tree that understands vendor/group/client relationships
 */
@Injectable({
  providedIn: 'root',
})
export class AdminTreeContextExtractorService implements ContextExtractor {
  /**
   * Extracts search context by looking at the current tree structure
   * to determine which vendor/group might contain the missing client
   */
  extractContext(
    _nodeKey: string,
    nodes: TreeNode[],
  ): Record<string, unknown> | undefined {
    // Look through the current tree to find potential vendor/group contexts
    // This is a best-effort attempt to provide context for progressive loading
    const context: { vendorId?: string; groupId?: string } = {};

    const extractFromNodes = (
      treeNodes: TreeNode[],
      currentVendorId?: string,
      currentGroupId?: string,
    ): void => {
      for (const node of treeNodes) {
        if (node.type === 'vendor') {
          currentVendorId = node.key as string;
          currentGroupId = undefined; // Reset group when entering new vendor
        } else if (node.type === 'group') {
          currentGroupId = node.key as string;
        }

        // If we find any client in this context, use this vendor/group as potential context
        if (
          node.type === 'client' &&
          isDefined(currentVendorId) &&
          currentVendorId !== ''
        ) {
          context.vendorId = currentVendorId;
          if (isDefined(currentGroupId) && currentGroupId !== '') {
            context.groupId = currentGroupId;
          }
        }

        if (node.children && node.children.length > 0) {
          extractFromNodes(node.children, currentVendorId, currentGroupId);
        }
      }
    };

    extractFromNodes(nodes);

    // Return context if we found at least a vendor
    return isDefined(context.vendorId) && context.vendorId !== ''
      ? context
      : undefined;
  }
}
