import { inject, Injectable } from '@angular/core';
import { firstValueFrom, map, catchError, of } from 'rxjs';
import { AccountsService, CompanyGroupResponse } from '../../../../api';
import { RequestCacheService } from '../../../../shared/services/request-cache.service';
import { UtilityService } from '../../../../core/services/utility.service';
import { isDefined } from '../../../../shared/utils';

/**
 * Service responsible for managing company group operations in the admin tree.
 * Handles fetching and listing company groups for vendors.
 */
@Injectable({
  providedIn: 'root',
})
export class AdminTreeGroupService {
  private readonly accountsService = inject(AccountsService);
  private readonly cacheService = inject(RequestCacheService);
  private readonly utilityService = inject(UtilityService);

  /**
   * Lists all company groups for a given vendor
   * @param vendorId - The ID of the vendor to fetch groups for
   * @param searchQuery - Optional search query to filter groups by name
   * @returns Promise resolving to array of CompanyGroupResponse
   * @throws Error if vendorId is empty or undefined
   */
  async listCompanyGroups(
    vendorId: string,
    searchQuery?: string,
  ): Promise<CompanyGroupResponse[]> {
    if (!vendorId?.trim()) {
      throw new Error('Vendor ID is required');
    }

    // Build filter for search query if provided
    let rqlFilter = '';
    if (isDefined(searchQuery) && searchQuery.trim().length > 0) {
      const filters = {
        name: `*${searchQuery.trim()}*`,
      };
      rqlFilter = this.utilityService.constructRqlFilter(filters);
    }

    // Include search query in cache key for proper caching
    const searchSuffix = isDefined(searchQuery) ? `-search-${searchQuery}` : '';
    const cacheKey = `admin-tree-groups-${vendorId}${searchSuffix}`;

    return this.cacheService.getOrFetchAsync(
      cacheKey,
      () =>
        firstValueFrom(
          this.accountsService
            .listCompanyGroups(
              vendorId,
              undefined, // offset
              undefined, // limit
              'name', // sort
              rqlFilter, // filter
            )
            .pipe(
              map((response) => response.items ?? []),
              catchError(() => of([])),
            ),
        ),
      30000,
    );
  }

  /**
   * Fetches a single company group by ID
   * @param groupId - The ID of the group to fetch
   * @returns Promise resolving to CompanyGroupResponse or null if not found
   */
  async fetchCompanyGroup(
    groupId: string,
  ): Promise<CompanyGroupResponse | null> {
    if (!groupId?.trim()) {
      return null;
    }

    const cacheKey = `admin-tree-group-${groupId}`;
    return this.cacheService.getOrFetchAsync(
      cacheKey,
      () =>
        firstValueFrom(
          this.accountsService
            .getCompanyGroup(groupId)
            .pipe(catchError(() => of(null))),
        ),
      30000,
    );
  }
}
