// admin-data.service.ts - Updated implementation

import { Injectable, inject } from '@angular/core';
import {
  ClientResponse,
  VendorListItem,
  VendorResponse,
  DistributorResponse,
} from '../../../api';
import { AdminTreeService } from './tree/admin-tree.service';
import { AdminStateService } from './state/admin-state.service';
import { AdminSearchService } from './admin-search.service';
import { MessageService, TreeNode } from 'primeng/api';
import {
  DEFAULT_LIST_SIZE,
  DEFAULT_SYSTEM_ID,
  LOCAL_STORAGE_KEY_SELECTED_CLIENT,
} from '../../../core/models';
import { AuthDataService } from '../../../core/services/auth-data.service';
import { LocalStorageService } from '../../../shared/services/local-storage.service';
import { ADMIN_TREE } from '../models/tree-node-data.interface';
import { isDefined, isNullOrUndefined } from '../../../shared/utils';
import { HttpErrorResponse } from '@angular/common/http';
import { ApiSchemaService } from '../../../core/services/api-schema.service';
import { UserPermissionLevel } from '../models';

/**
 * Service responsible for orchestrating the loading and management of administration data.
 * Coordinates between AdminTreeService (API), AdminStateService (State), and AdminSearchService.
 */
@Injectable({
  providedIn: 'root',
})
export class AdminDataService {
  private readonly adminTreeService = inject(AdminTreeService);
  private readonly adminState = inject(AdminStateService);
  private readonly adminSearchService = inject(AdminSearchService);
  private readonly messageService = inject(MessageService);
  private readonly localStorageService = inject(LocalStorageService);
  private readonly authDataService = inject(AuthDataService);
  private readonly apiSchemaService = inject(ApiSchemaService);

  private async loadClient(routeClientId?: string): Promise<void> {
    // Priority order: 1. Route client ID, 2. Saved client ID, 3. Default client ID
    const savedClientId = this.localStorageService.getItem<
      Partial<ClientResponse>
    >(LOCAL_STORAGE_KEY_SELECTED_CLIENT, true)?.id;
    const defaultClientId =
      this.authDataService.getUserProfile()?.defaultClient?.id;

    // Use route client ID if provided, otherwise fall back to saved or default
    const clientIdToLoad = routeClientId ?? savedClientId ?? defaultClientId;

    if (isNullOrUndefined(clientIdToLoad)) {
      const permissionLevel = this.adminState.userPermissionLevel();

      // CLIENT_ONLY users MUST have a client - this indicates a critical configuration error
      if (permissionLevel === UserPermissionLevel.CLIENT_ONLY) {
        this.messageService.add({
          severity: 'error',
          summary: 'Configuration Error',
          detail:
            'CLIENT_ONLY user has no default client assigned. Contact your administrator.',
        });
      } else {
        // For other levels (DISTRIBUTOR, VENDOR, SUPER_ADMIN), no client is acceptable
        this.messageService.add({
          severity: 'info',
          summary: 'No Client Selected',
          detail: 'Displaying data at your permission level.',
        });
      }
      return;
    }

    try {
      const client = await this.adminTreeService.tryLoadClient(
        clientIdToLoad,
        defaultClientId,
      );
      this.adminState.setSelectedClient(client);
      if (isNullOrUndefined(client)) {
        this.messageService.add({
          severity: 'warn',
          summary: 'Warning',
          detail: 'No client found for the user',
        });
      }
    } catch (error: unknown) {
      this.messageService.add({
        severity: 'error',
        summary: 'Error',
        detail: `An error occurred while initializing the client:  ${error instanceof Error ? error.message : 'Unknown error'}`,
      });
    }
  }

  /**
   * Loads vendor data for the given owner ID.
   */
  private async loadVendorDataById(
    vendorId: string,
  ): Promise<VendorResponse | null> {
    try {
      const vendor = await this.adminTreeService.fetchClientVendor(vendorId);
      if (isDefined(vendor?.id)) {
        this.adminState.setClientVendor(vendor);
        return vendor;
      }
      return null;
    } catch (error) {
      // Expected "not found" cases - safe to return null for permission-level fallback
      if (
        error instanceof HttpErrorResponse &&
        (error.status === 404 || error.status === 403)
      ) {
        console.warn(
          `Vendor ${vendorId} not found or access denied (${error.status})`,
        );
        return null;
      }

      // Unexpected errors (5xx, network, etc.) - re-throw for upstream handling
      console.error(`Unexpected error loading vendor ${vendorId}:`, error);
      throw error;
    }
  }

  /**
   * Loads distributor data for the given vendor.
   */
  private async loadDistributorDataById(
    distributorId: string,
  ): Promise<DistributorResponse | null> {
    try {
      const distributor =
        await this.adminTreeService.fetchVendorDistributor(distributorId);
      if (isDefined(distributor)) {
        this.adminState.setVendorDistributor(distributor);
        this.adminState.setExpandedDistributor(distributor.id, true);

        // Load vendors for this distributor
        const vendors = await this.adminTreeService.listDistributorVendors(
          distributor.id,
        );
        this.adminState.setDistributorVendors(distributor.id, vendors);
        return distributor;
      }
      return null;
    } catch (error) {
      // Expected "not found" cases - safe to return null for permission-level fallback
      if (
        error instanceof HttpErrorResponse &&
        (error.status === 404 || error.status === 403)
      ) {
        console.warn(
          `Distributor ${distributorId} not found or access denied (${error.status})`,
        );
        return null;
      }

      // Unexpected errors (5xx, network, etc.) - re-throw for upstream handling
      console.error(
        `Unexpected error loading distributor ${distributorId}:`,
        error,
      );
      throw error;
    }
  }

  /**
   * Loads super admin data (all distributors).
   */
  private async loadSuperAdminData(): Promise<void> {
    const usesModernSchema = this.apiSchemaService.usesModernSchema();
    if (!usesModernSchema) return;

    try {
      const distributors =
        await this.adminTreeService.listDistributors(DEFAULT_SYSTEM_ID);
      this.adminState.setDistributors(distributors);
    } catch (error) {
      console.error('Error loading distributors for super admin:', error);
    }
  }

  /**
   * Loads vendor clients and groups for initial data load.
   */
  private async loadInitialVendorClientsAndGroups(
    vendor: VendorResponse | VendorListItem,
  ): Promise<void> {
    const [vendorClientResponse, groups] = await Promise.all([
      this.adminTreeService.listVendorClients(
        vendor.id,
        undefined,
        0,
        DEFAULT_LIST_SIZE,
      ),
      this.adminTreeService.listCompanyGroups(vendor.id).catch((error) => {
        // Groups are optional - log but don't fail
        if (error instanceof HttpErrorResponse && error.status === 403) {
          console.warn(
            'Access denied to vendor groups (403) - continuing with direct clients only',
          );
        } else {
          console.error('Error loading groups:', error);
        }
        return []; // Return empty array on error
      }),
    ]);

    // Add clients to state
    this.adminState.addDistributorVendorClients(
      vendor.owner.id,
      vendor.id,
      vendorClientResponse.clients,
      {
        offset: 0,
        limit: DEFAULT_LIST_SIZE,
        total: vendorClientResponse.total,
        hasMore: vendorClientResponse.hasMore,
      },
      false,
    );

    // Add groups to state
    this.adminState.setDistributorVendorGroups(
      vendor.owner.id,
      vendor.id,
      groups,
    );
  }

  /**
   * Loads group clients if the selected client belongs to a group.
   */
  private async loadInitialGroupClients(
    vendor: VendorResponse | VendorListItem,
    selectedClient: ClientResponse,
  ): Promise<void> {
    if (!isDefined(selectedClient.group)) {
      return;
    }

    try {
      const groupClientResponse = await this.adminTreeService.listVendorClients(
        vendor.id,
        selectedClient.group,
        0,
        DEFAULT_LIST_SIZE,
      );
      this.adminState.addGroupClients(
        vendor.owner.id,
        vendor.id,
        selectedClient.group,
        groupClientResponse.clients,
        {
          offset: 0,
          limit: DEFAULT_LIST_SIZE,
          total: groupClientResponse.total,
          hasMore: groupClientResponse.hasMore,
        },
        false,
      );
      this.adminState.setDistributorExpandedGroup(
        vendor.owner.id,
        selectedClient.group,
        true,
      );
    } catch (error) {
      // Log but don't throw - group clients are supplementary
      console.error('Error loading group clients:', error);
    }
  }

  /**
   * Maps owner type to permission level.
   */
  private getPermissionLevelFromOwnerType(
    ownerType: string | null,
  ): UserPermissionLevel {
    switch (ownerType) {
      case 'system':
        return UserPermissionLevel.SUPER_ADMIN;
      case 'distributor':
        return UserPermissionLevel.DISTRIBUTOR;
      case 'vendor':
        return UserPermissionLevel.VENDOR;
      case 'client':
        return UserPermissionLevel.CLIENT_ONLY;
      default:
        return UserPermissionLevel.CLIENT_ONLY;
    }
  }

  /**
   * Loads the initial hierarchical data for the administration tree.
   * Uses ownerType from UserProfile to determine access level and permission boundaries.
   *
   * **Permission Level Guarantees:**
   * - SUPER_ADMIN: Loads all distributors; doesn't require a specific client to be set
   * - DISTRIBUTOR: Loads self and vendors; fallback to permission-level-only if no client selected
   * - VENDOR: Loads self and clients; fallback to permission-level-only if no client selected
   * - CLIENT_ONLY: Must load a specific client; if none available, displays warning and shows empty tree
   *
   * If loadClient fails to retrieve a client (and one is expected), loadDataByPermissionLevelOnly
   * is called as a graceful fallback.
   *
   * @param routeClientId Optional client ID from the route parameters
   * @returns Promise<void> Resolves when all accessible data is loaded
   */
  async loadInitialData(routeClientId?: string): Promise<void> {
    this.adminState.setInitialLoading(true);

    try {
      // Step 1: Determine permission level from AuthDataService
      const ownerType = this.authDataService.getUserOwnerType();
      const permissionLevel = this.getPermissionLevelFromOwnerType(ownerType);
      this.adminState.setUserPermissionLevel(permissionLevel);

      // Step 2: Load the current client context
      await this.loadClient(routeClientId);
      const selectedClient = this.adminState.selectedClient();

      if (isNullOrUndefined(selectedClient)) {
        // Handle scenario where no client can be loaded
        await this.loadDataByPermissionLevelOnly(permissionLevel);
        return;
      }

      // Step 3: Load hierarchy data based on permission level
      await this.loadHierarchyFromClient(selectedClient, permissionLevel);
    } catch (error: unknown) {
      this.handleInitialDataError(error);
    } finally {
      this.adminState.setInitialLoading(false);
      this.preloadSearchDataInBackground();
    }
  }

  /**
   * Loads data when no specific client is selected, based only on permission level.
   * Note: For CLIENT_ONLY users, this scenario indicates the client should have been
   * loaded in loadClient(). If we reach here, it's a configuration issue.
   */
  private async loadDataByPermissionLevelOnly(
    permissionLevel: UserPermissionLevel,
  ): Promise<void> {
    const ownerId = this.authDataService.getUserOwnerId();
    if (
      isNullOrUndefined(ownerId) &&
      permissionLevel !== UserPermissionLevel.SUPER_ADMIN
    )
      return;

    switch (permissionLevel) {
      case UserPermissionLevel.SUPER_ADMIN:
        await this.loadSuperAdminData();
        break;
      case UserPermissionLevel.DISTRIBUTOR:
        if (isDefined(ownerId)) await this.loadDistributorDataById(ownerId);
        break;
      case UserPermissionLevel.VENDOR:
        if (isDefined(ownerId)) await this.loadVendorDataById(ownerId);
        break;
      case UserPermissionLevel.CLIENT_ONLY:
        // CLIENT_ONLY users have no hierarchy to load beyond a specific client
        // The client should have been loaded in loadClient() above.
        // If we reach here, it means no client could be determined.
        this.messageService.add({
          severity: 'warn',
          summary: 'No Client Available',
          detail: 'Unable to load a client. Contact your administrator.',
        });
        break;
    }
  }

  /**
   * Loads the full hierarchy path for a chosen client based on permission level
   */
  private async loadHierarchyFromClient(
    client: ClientResponse,
    permissionLevel: UserPermissionLevel,
  ): Promise<void> {
    const vendorId = client.owner?.id;

    if (
      permissionLevel === UserPermissionLevel.CLIENT_ONLY ||
      isNullOrUndefined(vendorId)
    ) {
      this.adminState.setFallbackToClient(client);
      return;
    }

    try {
      // Load Vendor
      const vendor = await this.loadVendorDataById(vendorId);
      if (!vendor) {
        this.adminState.setFallbackToClient(client);
        return;
      }

      // Set up vendor level data (groups/clients)
      await this.setupVendorLevelContext(vendor, client);

      if (permissionLevel === UserPermissionLevel.VENDOR) {
        this.adminState.setFallbackToVendor(vendor);
        return;
      }

      // For DISTRIBUTOR and SUPER_ADMIN, try to load distributor
      const distributorId = vendor.owner?.id;
      if (!distributorId) return;

      const distributor = await this.loadDistributorDataById(distributorId);
      if (!distributor && permissionLevel !== UserPermissionLevel.SUPER_ADMIN) {
        this.adminState.setFallbackToVendor(vendor);
        return;
      }

      if (permissionLevel === UserPermissionLevel.SUPER_ADMIN) {
        await this.loadSuperAdminData();
      }
    } catch (error) {
      // Re-thrown unexpected errors caught here and user is notified
      this.messageService.add({
        severity: 'error',
        summary: 'Hierarchy Load Failed',
        detail: `Unable to load administrative hierarchy: ${error instanceof Error ? error.message : 'Unknown error'}`,
      });
      this.adminState.setFallbackToClient(client);
    }
  }

  /**
   * Sets up vendor-level data including clients, groups, and initial expansion
   */
  private async setupVendorLevelContext(
    vendor: VendorResponse | VendorListItem,
    selectedClient: ClientResponse,
  ): Promise<void> {
    const distributorId = vendor.owner?.id ?? 'none';

    // Mark vendor as loaded and expanded
    this.adminState.markDistributorVendorAsLoaded(distributorId, vendor.id);
    this.adminState.setDistributorNodeExpanded(distributorId, vendor.id, true);

    // Load vendor data (groups/clients)
    await this.loadInitialVendorClientsAndGroups(vendor);
    await this.loadInitialGroupClients(vendor, selectedClient);
  }

  /**
   * Handles errors during initial data loading
   */
  private handleInitialDataError(error: unknown): void {
    if (!(error instanceof HttpErrorResponse && error?.status === 403)) {
      this.messageService.add({
        severity: 'error',
        summary: 'Error',
        detail: `Failed to load initial data:  ${error instanceof Error ? error.message : 'Unknown error'}`,
      });
    }
  }
  /**
   * Preloads search data in the background to improve search performance
   * This method runs asynchronously and doesn't block the UI
   */
  private preloadSearchDataInBackground(): void {
    // Run preloading in background without waiting
    this.adminSearchService.preloadSearchData().catch((error: unknown) => {
      console.error('Error preloading search data:', error);
      // Silent failure - this is background optimization
    });
  }

  /**
   * Checks if the user has permission to access vendor-level data.
   * This is a guard method that can be used before attempting to fetch vendor data.
   *
   * @returns boolean indicating if vendor-level access is likely available
   */
  canAccessVendorLevel(): boolean {
    const permissionLevel = this.adminState.userPermissionLevel();
    return (
      permissionLevel === UserPermissionLevel.VENDOR ||
      permissionLevel === UserPermissionLevel.DISTRIBUTOR ||
      permissionLevel === UserPermissionLevel.SUPER_ADMIN
    );
  }

  /**
   * Checks if the user has permission to access distributor-level data.
   * This is a guard method that can be used before attempting to fetch distributor data.
   *
   * @returns boolean indicating if distributor-level access is likely available
   */
  canAccessDistributorLevel(): boolean {
    const permissionLevel = this.adminState.userPermissionLevel();
    return (
      permissionLevel === UserPermissionLevel.DISTRIBUTOR ||
      permissionLevel === UserPermissionLevel.SUPER_ADMIN
    );
  }

  /**
   * Checks if the user has permission to access super admin data.
   * This is a guard method that can be used before attempting to fetch super admin data.
   *
   * @returns boolean indicating if super admin access is likely available
   */
  canAccessSuperAdminLevel(): boolean {
    return (
      this.adminState.userPermissionLevel() ===
        UserPermissionLevel.SUPER_ADMIN &&
      this.authDataService.isSuperAdmin() &&
      this.apiSchemaService.usesModernSchema()
    );
  }

  /**
   * Expands a tree node and loads its children data.
   * For vendor nodes, loads company groups and clients.
   * For group nodes, loads the associated clients.
   *
   * @param node - The TreeNode to expand
   * @returns Promise<void> Resolves when child data is loaded
   * @throws Displays error message via MessageService if loading fails
   */
  private async expandVendorNode(node: TreeNode): Promise<void> {
    if (
      isNullOrUndefined(node.data?.id) ||
      node.type !== ADMIN_TREE.NODE_TYPES.VENDOR
    )
      return;
    const vendorId = node.data.id;

    // Get the distributor context for this vendor node
    const distributorId = this.getDistributorContextFromNode(node);
    if (isNullOrUndefined(distributorId)) {
      console.error('No distributor context found for vendor node');
      return;
    }

    // Check if already loaded AND expanded
    const distributorLoadedVendors =
      this.adminState.loadedVendors()[distributorId];
    const isAlreadyLoaded =
      isDefined(distributorLoadedVendors) &&
      distributorLoadedVendors.has(vendorId) === true;
    const isAlreadyExpanded = this.adminState.isDistributorNodeExpanded(
      distributorId,
      vendorId,
    );

    // If already loaded and expanded, nothing to do
    if (isAlreadyLoaded && isAlreadyExpanded) {
      return;
    }

    // If already loaded but not expanded, just set expansion
    if (isAlreadyLoaded && !isAlreadyExpanded) {
      this.adminState.setDistributorNodeExpanded(distributorId, vendorId, true);
      return;
    }

    // Not loaded yet, load the data
    try {
      this.adminState.setDistributorNodeLoading(distributorId, vendorId);

      // Mark as loaded IMMEDIATELY to prevent race conditions
      this.adminState.markDistributorVendorAsLoaded(distributorId, vendorId);

      // Set expansion BEFORE loading data so tree knows to expect children
      this.adminState.setDistributorNodeExpanded(distributorId, vendorId, true);

      const [groups, clients] = await Promise.all([
        this.adminTreeService.listCompanyGroups(vendorId),
        this.adminTreeService.listVendorClients(
          vendorId,
          undefined,
          0,
          DEFAULT_LIST_SIZE,
        ),
      ]);

      this.adminState.setDistributorVendorGroups(
        distributorId,
        vendorId,
        groups,
      );
      this.adminState.addDistributorVendorClients(
        distributorId,
        vendorId,
        clients.clients,
        {
          offset: 0,
          limit: DEFAULT_LIST_SIZE,
          total: clients.total,
          hasMore: clients.hasMore,
        },
      );
    } catch (error: unknown) {
      // On error, UNMARK as loaded so it can be retried
      this.adminState.removeDistributorLoadedVendor(distributorId, vendorId);
      this.adminState.markDistributorVendorAsFailedToLoad(
        distributorId,
        vendorId,
      );

      this.messageService.add({
        severity: 'error',
        summary: 'Error',
        detail: `Failed to load vendor data: ${error instanceof Error ? error.message : 'Unknown error'}`,
      });
    } finally {
      this.adminState.clearDistributorNodeLoading(distributorId, vendorId);
    }
  }

  /**
   * Helper method to get distributor context from a tree node by walking up the tree
   * @param node - The tree node to get context from
   * @returns The distributor ID or null if not found
   */
  getDistributorContextFromNode(node: TreeNode): string | null {
    if (
      node.type === ADMIN_TREE.NODE_TYPES.VENDOR &&
      isDefined(node.data?.owner?.id)
    ) {
      return node.data.owner.id;
    }
    let current = node.parent;
    while (current) {
      if (current.type === ADMIN_TREE.NODE_TYPES.DISTRIBUTOR) {
        return current.data?.id ?? null;
      }
      current = current.parent;
    }

    // Fallback to current context from state (works for both distributor and vendor-only)
    return this.adminState.getActiveContextId();
  }

  /**
   * Expands a group node and loads its associated clients.
   *
   * @param groupNode - The group TreeNode to expand
   * @returns Promise<void> Resolves when client data is loaded
   * @throws Displays error message via MessageService if loading fails
   */
  private async expandGroupNode(groupNode: TreeNode): Promise<void> {
    if (
      isNullOrUndefined(groupNode.data?.id) ||
      isNullOrUndefined(groupNode.parent?.data?.id)
    )
      return;

    const groupId = groupNode.data.id;
    const vendorId = groupNode.parent.data.id;

    // Get distributor context from the tree hierarchy
    const distributorId = this.getDistributorContextFromNode(groupNode);
    if (isNullOrUndefined(distributorId)) {
      console.error('No distributor context found for group node');
      return;
    }

    // Check if already loaded using distributor-scoped pagination
    const paginationKey = this.adminState.getDistributorPaginationKey(
      distributorId,
      vendorId,
      groupId,
    );
    const distributorPagination =
      this.adminState.clientPagination()[distributorId];
    if (isDefined(distributorPagination?.[paginationKey])) {
      // Data already loaded, just ensure expansion state is set
      this.adminState.setDistributorExpandedGroup(distributorId, groupId, true);
      return;
    }

    try {
      const clients = await this.adminTreeService.listVendorClients(
        vendorId,
        groupId,
        0,
        DEFAULT_LIST_SIZE,
      );
      this.adminState.addGroupClients(
        distributorId,
        vendorId,
        groupId,
        clients.clients,
        {
          offset: 0,
          limit: DEFAULT_LIST_SIZE,
          total: clients.total,
          hasMore: clients.hasMore,
        },
      );
      this.adminState.setDistributorExpandedGroup(distributorId, groupId, true);
    } catch (error: unknown) {
      this.messageService.add({
        severity: 'error',
        summary: 'Error',
        detail: `Failed to load group clients: ${error instanceof Error ? error.message : 'Unknown error'}`,
      });
      this.adminState.setDistributorExpandedGroup(
        distributorId,
        groupId,
        false,
      );
    }
  }

  private async expandDistributorNode(
    distributorNode: TreeNode,
  ): Promise<void> {
    const distributorId = distributorNode.data.id;
    if (isNullOrUndefined(distributorId)) return;

    try {
      const vendors =
        await this.adminTreeService.listDistributorVendors(distributorId);
      this.adminState.setDistributorVendors(distributorId, vendors);
      this.adminState.setExpandedDistributor(distributorId, true);
    } catch (error: unknown) {
      this.messageService.add({
        severity: 'error',
        summary: 'Error',
        detail: `Failed to load distributor vendors: ${error instanceof Error ? error.message : 'Unknown error'}`,
      });
    }
  }

  private collapseVendorNode(node: TreeNode): void {
    const contextId = this.adminState.getActiveContextId();
    if (isNullOrUndefined(contextId)) {
      return;
    }
    this.adminState.setDistributorNodeExpanded(contextId, node.data.id, false);
  }

  private collapseGroupNode(groupNode: TreeNode): void {
    const distributorId = this.getDistributorContextFromNode(groupNode);
    if (isDefined(distributorId)) {
      this.adminState.setDistributorExpandedGroup(
        distributorId,
        groupNode.data.id,
        false,
      );
    }
  }

  private collapseDistributorNode(distributorNode: TreeNode): void {
    this.adminState.setExpandedDistributor(distributorNode.data.id, false);
  }

  /**
   * Loads the next page of clients for a given vendor or group within a distributor context.
   * @param vendorId - The ID of the vendor.
   * @param groupId - Optional group ID.
   * @param distributorId - Optional distributor ID. If not provided, will try to get from current context.
   */
  async loadMoreClients(
    vendorId: string,
    groupId?: string,
    distributorId?: string,
  ): Promise<void> {
    // Get context - use provided ID or try to get current context (works for both distributor and vendor-only)
    const contextDistributorId =
      distributorId ?? this.adminState.getActiveContextId();

    if (isNullOrUndefined(contextDistributorId)) {
      return;
    }

    const paginationKey = this.adminState.getDistributorPaginationKey(
      contextDistributorId,
      vendorId,
      groupId,
    );

    const currentPagination =
      this.adminState.clientPagination()[contextDistributorId]?.[paginationKey];

    if (
      isNullOrUndefined(currentPagination) ||
      isNullOrUndefined(currentPagination.hasMore) ||
      currentPagination.loadingMore === true
    ) {
      return;
    }

    const nextOffset = currentPagination.offset + currentPagination.limit;
    this.adminState.setLoadingMoreClients(
      contextDistributorId,
      vendorId,
      groupId,
      true,
    );

    try {
      const clientResponse = await this.adminTreeService.listVendorClients(
        vendorId,
        groupId,
        nextOffset,
        DEFAULT_LIST_SIZE,
      );

      if (isDefined(groupId)) {
        this.adminState.addGroupClients(
          contextDistributorId,
          vendorId,
          groupId,
          clientResponse.clients,
          {
            offset: nextOffset,
            limit: DEFAULT_LIST_SIZE,
            total: clientResponse.total,
            hasMore: clientResponse.hasMore,
          },
          true,
        );
      } else {
        this.adminState.addDistributorVendorClients(
          contextDistributorId,
          vendorId,
          clientResponse.clients,
          {
            offset: nextOffset,
            limit: DEFAULT_LIST_SIZE,
            total: clientResponse.total,
            hasMore: clientResponse.hasMore,
          },
          true,
        );
      }
    } catch (error: unknown) {
      this.messageService.add({
        severity: 'error',
        summary: 'Error',
        detail: `Failed to load more clients: ${error instanceof Error ? error.message : 'Unknown error'}`,
      });
    } finally {
      // Reset loading state in ALL cases (success or error)
      this.adminState.setLoadingMoreClients(
        contextDistributorId,
        vendorId,
        groupId,
        false,
      );
    }
  }

  /**
   * Expands a super admin node and loads distributor data
   * @param _superAdminNode - The super admin node to expand
   */
  private expandSuperAdminNode(_superAdminNode: TreeNode): void {
    // Legacy method - expansion state management moved to distributor-scoped pattern
    // Implementation would require distributor context to properly manage state
  }

  /**
   * Collapses a super admin node
   * @param _superAdminNode - The super admin node to collapse
   */
  private collapseSuperAdminNode(_superAdminNode: TreeNode): void {
    // Legacy method - expansion state management moved to distributor-scoped pattern
    // Implementation would require distributor context to properly manage state
  }

  async expandNode(node: TreeNode): Promise<void> {
    switch (node.type) {
      case ADMIN_TREE.NODE_TYPES.VENDOR:
        await this.expandVendorNode(node);
        break;
      case ADMIN_TREE.NODE_TYPES.GROUP:
        await this.expandGroupNode(node);
        break;
      case ADMIN_TREE.NODE_TYPES.DISTRIBUTOR:
        await this.expandDistributorNode(node);
        break;
      case ADMIN_TREE.NODE_TYPES.SUPER_ADMIN:
        this.expandSuperAdminNode(node);
        break;
    }
  }

  collapseNode(node: TreeNode): void {
    switch (node.type) {
      case ADMIN_TREE.NODE_TYPES.VENDOR:
        this.collapseVendorNode(node);
        break;
      case ADMIN_TREE.NODE_TYPES.GROUP:
        this.collapseGroupNode(node);
        break;
      case ADMIN_TREE.NODE_TYPES.DISTRIBUTOR:
        this.collapseDistributorNode(node);
        break;
      case ADMIN_TREE.NODE_TYPES.SUPER_ADMIN:
        this.collapseSuperAdminNode(node);
        break;
    }
  }
}
