import { Injectable, inject } from '@angular/core';

import { TableLazyLoadEvent } from 'primeng/table';

import {
  DataGridParams,
  DATE_FORMATS,
  TreeTableColumn,
  TreeTableConfiguration,
} from '../../../shared/models';
import { isDefined } from '../../../shared/utils';

import { AssetGroupResponse } from '../../../api';
import { AssetGroupsDataAdapter } from '../services/adapters';
import { MeasurementDateTimeService } from '../../../shared/services/measurement-units/measurement-date-time.service';
import { DynamicDialogRef, DialogService } from 'primeng/dynamicdialog';
import { AssetGroupCreateWizardComponent } from '../components/asset-groups/asset-group-create-wizard/asset-group-create-wizard.component';
import { DEFAULT_LIST_SIZE } from '../../../core/models';

@Injectable({ providedIn: 'root' })
export class AssetGroupsGridConfig
  implements TreeTableConfiguration<AssetGroupResponse>
{
  private readonly assetGroupsService = inject(AssetGroupsDataAdapter);
  private readonly measurementDateTimeService = inject(
    MeasurementDateTimeService,
  );
  private dialogRef: DynamicDialogRef | null = null;
  private readonly dialogService = inject(DialogService);
  private params: Record<string, string> = { clientId: '' };

  entityType = 'asset-groups';
  columns: TreeTableColumn<AssetGroupResponse>[] = [
    {
      field: 'name',
      header: 'Name',
      filterType: 'text',
    },

    {
      field: 'modifiedDate',
      header: 'Modified',
      valueGetter: (rowData: AssetGroupResponse) =>
        isDefined(rowData?.entity?.modifiedDate)
          ? this.measurementDateTimeService.formatDateTime(
              this.measurementDateTimeService.createDateAssumingUtc(
                rowData?.entity?.modifiedDate,
              ),
              DATE_FORMATS.DATE_TIME_SECONDS,
            )
          : '',
    },
  ];

  dataService = this.assetGroupsService;
  ownerId = '';

  initialize(params: Record<string, string>): void {
    this.params = params;
    if (isDefined(this.params['clientId'])) {
      this.ownerId = String(this.params['clientId']);
    } else if (isDefined(this.params['vendorId'])) {
      this.ownerId = String(this.params['vendorId']);
    }
  }

  getTitle(): string {
    return 'Asset Groups';
  }

  getRequestParams(event?: TableLazyLoadEvent): DataGridParams {
    // Add sorting support
    let sortField = 'name:asc'; // Default

    if (isDefined(event?.sortField)) {
      const sortOrder = event.sortOrder === 1 ? 'asc' : 'desc';
      sortField = `${event.sortField}:${sortOrder}`;
    }

    return {
      owner: this.ownerId,
      offset: isDefined(event?.first) ? event.first : 0,
      recurse: false,
      limit: DEFAULT_LIST_SIZE,
      sortField: sortField,
    };
  }

  onAdd(): void {
    const clientId = this.params['clientId'];

    this.openAssetGroupCreationDialog(clientId).subscribe();
  }

  openAssetGroupCreationDialog(ownerId: string) {
    this.dialogRef = this.dialogService.open(AssetGroupCreateWizardComponent, {
      header: 'Asset Group Wizard',
      contentStyle: { overflow: 'auto' },
      baseZIndex: 10000,
      data: { ownerId: ownerId },
      position: 'top',
      modal: true,
    });

    return (this.dialogRef as DynamicDialogRef).onClose;
  }
}
