import { Routes } from '@angular/router';
import { AdminContainerComponent } from './components/admin-container/admin-container.component';
import { clientResolver } from './resolvers/client.resolver';
import { distributorResolver } from './resolvers/distributor.resolver';
import { vendorResolver } from './resolvers/vendor.resolver';
import { ownerTierGuard } from '../../core/guards/owner-tier.guard';
export const ADMINISTRATION_ROUTES: Routes = [
  {
    path: '',
    component: AdminContainerComponent,
    children: [
      {
        path: 'super-admin/:superAdminId',
        canActivate: [ownerTierGuard],
        data: { requiredOwnerType: 'system' },
        loadChildren: () =>
          import('./components/super-admin/super-admin.routes').then(
            (m) => m.SUPER_ADMIN_ROUTES,
          ),
      },
      {
        path: 'vendor/:vendorId',
        canActivate: [ownerTierGuard],
        data: { requiredOwnerType: 'vendor' },
        resolve: {
          vendor: vendorResolver,
        },
        loadChildren: () =>
          import('./components/vendors/vendors.routes').then(
            (m) => m.ADMIN_VENDORS_ROUTES,
          ),
      },
      {
        path: 'distributor/:distributorId',
        canActivate: [ownerTierGuard],
        data: { requiredOwnerType: 'distributor' },
        resolve: {
          distributor: distributorResolver,
        },
        loadChildren: () =>
          import('./components/distributors/distributors.routes').then(
            (m) => m.ADMIN_DISTRIBUTOR_ROUTES,
          ),
      },
      {
        path: 'client/:clientId',
        canActivate: [ownerTierGuard],
        data: { requiredOwnerType: 'client' },
        resolve: {
          client: clientResolver,
        },
        loadChildren: () =>
          import('./components/clients/clients.routes').then(
            (m) => m.ADMIN_CLIENT_ROUTES,
          ),
      },
    ],
  },
];
