import { CommonModule } from '@angular/common';
import {
  ChangeDetectorRef,
  Component,
  DestroyRef,
  OnInit,
  TemplateRef,
  computed,
  effect,
  inject,
  signal,
  viewChild,
} from '@angular/core';
import { ReactiveFormsModule } from '@angular/forms';
import { WizardComponent } from '../../../../../shared/components/wizard/wizard.component';
import { AssetGroupingItem, Step } from '../../../../../shared/models';
import { ButtonModule } from 'primeng/button';
import { AssetDetailsSectionComponent } from '../asset-details-section/asset-details-section.component';
import {
  isDefined,
  isEmpty,
  isNullOrUndefined,
} from '../../../../../shared/utils';
import { AssetCategoriesSectionComponent } from '../asset-categories-section/asset-categories-section.component';
import {
  AssetCreateRequest,
  AssetResponse,
  EntitiesService,
  IdName,
} from '../../../../../api';
import { takeUntilDestroyed } from '@angular/core/rxjs-interop';
import { DynamicDialogRef, DynamicDialogConfig } from 'primeng/dynamicdialog';
import { BroadcastEventMessageService } from '../../../services/broadcast-event-messages.service';
import { InfoSectionComponent } from '../../shared/info-section/info-section.component';
import { CacheInvalidationService } from '../../../../../shared/services/cache-invalidation.service';
import { AssetGroupingService } from '../../../../../shared/services/asset-grouping.service';
import { DEFAULT_SYSTEM_ID } from '../../../../../core/models';
import { finalize } from 'rxjs';

// Interface for asset form data structure
interface AssetFormDetails {
  name?: string;
  mapIconName?: string;
  assetType?: IdName;
  color?: AssetCreateRequest.ColorEnum | null;
  costCentre?: IdName;
  groups?: IdName[];
  tags?: string[];
  assetTag?: IdName;
  ownerId?: string;
}

// Interface for asset categories form data
interface AssetCategoriesFormData {
  categories?: IdName[] | null;
}

@Component({
  selector: 'app-asset-create-wizard',
  imports: [
    CommonModule,
    ReactiveFormsModule,
    WizardComponent,
    ButtonModule,
    AssetDetailsSectionComponent,
    AssetCategoriesSectionComponent,
    InfoSectionComponent,
  ],
  templateUrl: './asset-create-wizard.component.html',
  styleUrl: './asset-create-wizard.component.scss',
})
export class AssetCreateWizardComponent implements OnInit {
  // Signal-based ViewQueries
  // Using optional viewChild to allow safe checks in effect without throwing errors
  readonly assetDetailsStep =
    viewChild<TemplateRef<{ $implicit: Step }>>('assetDetailsStep');
  readonly assetCategoriesStep = viewChild<TemplateRef<{ $implicit: Step }>>(
    'assetCategoriesStep',
  );
  readonly confirmationTemplate = viewChild<TemplateRef<{ $implicit: Step }>>(
    'confirmationTemplate',
  );
  readonly infoSectionStep =
    viewChild<TemplateRef<{ $implicit: Step }>>('infoSectionStep');

  // References to form components
  readonly assetDetailsForm =
    viewChild<AssetDetailsSectionComponent>('assetDetailsForm');
  readonly assetCategoriesForm = viewChild<AssetCategoriesSectionComponent>(
    'assetCategoriesForm',
  );
  readonly infoSectionForm =
    viewChild<InfoSectionComponent<AssetResponse>>('infoSectionForm');

  private readonly entitiesService = inject(EntitiesService);
  private readonly broadcastService = inject(BroadcastEventMessageService);
  protected readonly destroyRef = inject(DestroyRef);
  private readonly dialogRef = inject(DynamicDialogRef);
  private readonly dialogConfig = inject(DynamicDialogConfig);
  private readonly cacheInvalidationService = inject(CacheInvalidationService);
  private readonly assetGroupingService = inject(AssetGroupingService);
  private readonly cdr = inject(ChangeDetectorRef);

  // Asset model to collect data across steps
  newAsset = signal<Partial<AssetCreateRequest>>({});

  readonly infoSectionFields = computed(() => {
    const asset = this.newAsset();
    if (!asset.fields) {
      return [];
    }

    // Get custom fields from the info section component if available
    const infoForm = this.infoSectionForm();
    const customFields = infoForm?.entityCustomFields?.();
    if (Array.isArray(customFields) && customFields.length > 0) {
      return customFields.map((field) => ({
        key: field['title'] ?? 'Field',
        value: this.formatFieldValue(
          asset.fields?.[field['id']],
          field['type'],
        ),
      }));
    }

    // Fallback: show all fields as key-value pairs
    return Object.entries(asset.fields).map(([key, value]) => ({
      key: key.charAt(0).toUpperCase() + key.slice(1),
      value: this.formatFieldValue(value, 'text'),
    }));
  });

  // Form validity state
  formIsValid = signal<boolean>(false);

  displayWizard = false;
  steps: Step[] = [];
  ownerId: string | null = null;
  assetCategories: AssetGroupingItem[] = [];
  isSubmitting = signal<boolean>(false);

  constructor() {
    // Initialize steps once view templates are ready
    effect(() => {
      const detailsStep = this.assetDetailsStep();
      const categoriesStep = this.assetCategoriesStep();
      const infoStep = this.infoSectionStep();
      const confirmStep = this.confirmationTemplate();

      if (detailsStep && categoriesStep && infoStep && confirmStep) {
        this.steps = [
          {
            value: 1,
            header: 'Asset Details',
            content: detailsStep,
            complete: false,
            validate: () => this.assetDetailsForm()?.validate() ?? false,
          },
          {
            value: 2,
            header: 'Categories',
            content: categoriesStep,
            complete: false,
            validate: () => this.assetCategoriesForm()?.validate() ?? false,
          },
          {
            value: 3,
            header: 'Info',
            content: infoStep,
            complete: false,
            validate: () =>
              this.infoSectionForm()?.validate() ?? Promise.resolve(true),
          },
          {
            value: 4,
            header: 'Confirmation',
            content: confirmStep,
            complete: true,
          },
        ];
        this.cdr.detectChanges();
      }
    });
  }

  ngOnInit(): void {
    if (isDefined(this.dialogConfig.data)) {
      this.ownerId = this.dialogConfig.data.ownerId ?? null;

      // Initialize asset with ownerId
      this.newAsset.update((current) => ({
        ...current,
        ownerId: this.ownerId ?? '',
      }));

      this.assetGroupingService
        .getAssetCategories(this.ownerId ?? DEFAULT_SYSTEM_ID)
        .then((assetCategories) => {
          this.assetCategories = assetCategories;
        })
        .catch(() => {
          this.assetCategories = [];
        });
    }
  }

  openWizard() {
    this.displayWizard = true;
    this.newAsset.set({});
  }

  // Form validity handlers
  onDetailsFormValidityChange(isValid: boolean) {
    if (isDefined(this.steps[0])) {
      this.steps[0].disableNext = !isValid;
    }
    this.formIsValid.set(isValid);
  }

  onCategoriesFormValidityChange(isValid: boolean) {
    if (isDefined(this.steps[1])) {
      this.steps[1].disableNext = !isValid;
    }
    this.formIsValid.set(isValid);
  }

  onInfoFormValidityChange(isValid: boolean) {
    if (isDefined(this.steps[2])) {
      this.steps[2].disableNext = !isValid;
    }
    this.formIsValid.set(isValid);
  }

  // Update the asset state with form data
  updateAssetDetails(formData: AssetFormDetails) {
    this.newAsset.update((current) => {
      // Create a filtered update object excluding ownerId initially
      const { ownerId: _ownerId, ...fieldsToUpdate } = formData;

      const updated: Partial<AssetCreateRequest> = {
        ...current,
        ...fieldsToUpdate,
      };

      if (isNullOrUndefined(updated.ownerId)) {
        updated.ownerId = this.ownerId ?? '';
      }

      return updated;
    });
  }

  updateAssetCategories(formData: AssetCategoriesFormData) {
    this.newAsset.update((current) => {
      const updated = { ...current };

      updated.categories = isEmpty(formData.categories)
        ? null
        : (formData.categories ?? null);

      if (isNullOrUndefined(updated.ownerId)) {
        updated.ownerId = this.ownerId ?? '';
      }

      return updated;
    });
  }

  updateInfoSection(formData: Record<string, unknown>) {
    this.newAsset.update((current) => {
      const updated = { ...current };
      Object.assign(updated, formData);
      return updated;
    });
  }

  onWizardComplete(): void {
    // Process the final data from all steps
    const assetData = this.newAsset() as AssetCreateRequest;
    this.isSubmitting.set(true);

    this.entitiesService
      .createAsset(assetData)
      .pipe(
        takeUntilDestroyed(this.destroyRef),
        finalize(() => this.isSubmitting.set(false)),
      )
      .subscribe({
        next: (response: AssetResponse) => {
          this.cacheInvalidationService.clearAssetCategoryCaches();
          this.broadcastService.broadcast('entity-updated', {
            entityType: 'asset',
            operation: 'create',
            entityId: response.id,
          });
          this.dialogRef.close(response);
        },
        error: (error: Error) => {
          console.error('Error creating asset:', error);
        },
      });
  }

  onWizardCancel(): void {
    this.newAsset.set({});
    this.dialogRef.close();
  }

  convertIdNameCollectionToNameStrings(
    collection: IdName[] | null | undefined,
  ): string {
    if (!Array.isArray(collection)) return '';
    return collection.map((item) => item.name).join(', ');
  }

  convertStringCollectionToNameStrings(
    collection: string[] | null | undefined,
  ): string {
    if (!Array.isArray(collection)) return '';
    return collection.join(', ');
  }

  // Get categories formatted as key-value pairs for display
  getCategoriesAsKeyValuePairs(): { key: string; value: string }[] {
    const asset = this.newAsset() as AssetCreateRequest;
    if (!asset.categories || asset.categories.length === 0) {
      return [];
    }

    // Group categories by their parent category
    const parentCategories = this.assetCategories.filter(
      (cat) => cat.parent === DEFAULT_SYSTEM_ID,
    );

    const result: { key: string; value: string }[] = [];

    parentCategories.forEach((parentCat) => {
      const selectedCategory = asset.categories?.find((selectedCat) => {
        const fullCategory = this.assetCategories.find(
          (cat) => cat.id === selectedCat.id,
        );
        return fullCategory?.index === parentCat.index;
      });

      if (selectedCategory) {
        result.push({
          key: parentCat.name ?? 'Category',
          value: selectedCategory.name ?? 'Selected',
        });
      }
    });

    return result;
  }

  private formatFieldValue(value: unknown, type: string): string {
    if (value === null || value === undefined) {
      return 'Not set';
    }

    switch (type) {
      case 'boolean':
        return value === true || value === 'true' ? 'Yes' : 'No';
      case 'date':
        if (value instanceof Date) {
          return value.toLocaleDateString();
        }
        return typeof value === 'string' ? value : JSON.stringify(value);
      case 'number':
        return typeof value === 'number'
          ? value.toString()
          : JSON.stringify(value);
      default:
        return typeof value === 'string' ? value : JSON.stringify(value);
    }
  }
}
