import { inject, Injectable, signal } from '@angular/core';
import { TreeNode } from 'primeng/api';

import { AdminDataService } from '../admin-data.service';
import { AdminStateService } from '../state/admin-state.service';
import { AdminSearchService } from '../admin-search.service';
import { AdminNavigationService } from '../navigation/admin-navigation.service';
import { AdminContainerStateService } from '../state/admin-container-state.service';
import { ADMIN_TREE } from '../../models/tree-node-data.interface';
import { EventSource } from '../../models/event-source.enum';
import { TreeSearchResult } from '../../../../shared/models';
import { isDefined } from '../../../../shared/utils';
import { NodeNotFoundEvent } from '../../../../shared/components/tree-panel/tree-panel.component';
import { AdminTreeClientLoaderService } from './admin-tree-client-loader.service';
import { AdminScrollManagementService } from './admin-scroll-management.service';
import { AdminTreeSelectionService } from './admin-tree-selection.service';
import { AdminTreeEntitySyncService } from './admin-tree-entity-sync.service';
import { AdminTreeSearchNavigationService } from './admin-tree-search-navigation.service';
import { AdminTreeErrorHandlerService } from '../admin-tree-error-handler.service';

// Constants for timing and configuration
const TIMING = {
  SCROLL_SUPPRESSION: 2000,
  NODE_SELECTION_DELAY: 100,
  NODE_MONITORING_INTERVAL: 150,
  NODE_SELECTION_TIMEOUT: 10000,
} as const;

@Injectable({
  providedIn: 'root',
})
export class AdminTreeEventHandlerService {
  private readonly adminDataService = inject(AdminDataService);
  private readonly adminState = inject(AdminStateService);
  private readonly adminSearchService = inject(AdminSearchService);
  private readonly navigationService = inject(AdminNavigationService);
  private readonly containerState = inject(AdminContainerStateService);
  private readonly clientLoader = inject(AdminTreeClientLoaderService);
  private readonly scrollManagement = inject(AdminScrollManagementService);
  private readonly selectionService = inject(AdminTreeSelectionService);
  private readonly entitySync = inject(AdminTreeEntitySyncService);
  private readonly searchNavigation = inject(AdminTreeSearchNavigationService);
  private readonly adminTreeErrorHandler = inject(AdminTreeErrorHandlerService);

  // Expose signals for component to use
  readonly selectedTreeNode = signal<TreeNode | null>(null);

  // Tree panel state (delegated to container state)
  readonly treeCollapsed = this.containerState.treeCollapsed;

  // Signal-based communication instead of callbacks
  readonly toggleTreePanel = signal<boolean>(false);
  readonly errorMessage = signal<{ error: unknown; message: string } | null>(
    null,
  );

  // Track current event source
  private currentEventSource: EventSource = EventSource.USER;

  // Required dependencies that will be injected via initialization
  private hierarchyTreeGetter?: () => TreeNode[];

  /**
   * Sets the event source for the next operation
   */
  setEventSource(source: EventSource): void {
    this.currentEventSource = source;
  }

  /**
   * Resets event source back to user after operation
   */
  private resetEventSource(): void {
    this.currentEventSource = EventSource.USER;
  }

  /**
   * Checks if current event allows auto-scrolling
   */
  shouldAllowAutoScroll(): boolean {
    return this.currentEventSource !== EventSource.USER;
  }

  /**
   * Initialize the service with required dependencies
   */
  initialize(dependencies: {
    getHierarchyTree: () => TreeNode[];
    findNodeByKeyRecursive: (nodes: TreeNode[], key: string) => TreeNode | null;
  }): void {
    this.hierarchyTreeGetter = dependencies.getHierarchyTree;

    // Initialize entity sync service
    this.entitySync.initialize({
      getHierarchyTree: dependencies.getHierarchyTree,
      findNodeByKeyRecursive: dependencies.findNodeByKeyRecursive,
      treeCollapsed: () => this.treeCollapsed(),
      toggleTreePanel: this.toggleTreePanel,
      setEventSource: (source) => this.setEventSource(source),
      resetEventSource: () => this.resetEventSource(),
      selectedTreeNode: this.selectedTreeNode,
      handleError: (err, msg) => this.handleError(err, msg),
    });

    // Initialize search navigation service
    this.searchNavigation.initialize({
      getHierarchyTree: dependencies.getHierarchyTree,
      findNodeByKeyRecursive: dependencies.findNodeByKeyRecursive,
      selectedTreeNode: this.selectedTreeNode,
    });

    // Initialize client loader
    this.clientLoader.initialize({
      hierarchyTreeGetter: this.hierarchyTreeGetter,
    });
  }

  /**
   * Cleanup method to clear any pending operations
   */
  cleanup(): void {
    this.searchNavigation.cleanup();
  }

  /**
   * Handles node expansion
   */
  async onNodeExpand(node: TreeNode): Promise<void> {
    try {
      this.handleScrollSuppressionForExpansion();
      await this.adminDataService.expandNode(node);
    } catch (error: unknown) {
      this.handleError(error, 'Failed to expand node');
    } finally {
      this.resetEventSource();
    }
  }

  /**
   * Handles scroll suppression logic for node expansion
   */
  private handleScrollSuppressionForExpansion(): void {
    // Only suppress scrolling temporarily if this is a system event that should allow scrolling
    // For user events, suppress scrolling completely to prevent auto-scroll during user interactions
    if (this.shouldAllowAutoScroll()) {
      this.scrollManagement.temporarilySuppressScrollToClient(
        TIMING.SCROLL_SUPPRESSION,
      );
    } else {
      // For user events, suppress scrolling indefinitely (until manually cleared)
      this.scrollManagement.temporarilySuppressScrollToClient(0);
    }
  }

  /**
   * Handles node collapse
   */
  onNodeCollapse(node: TreeNode): void {
    if (!isDefined(node.data?.id)) return;

    // Only suppress scrolling temporarily for system events
    // For user events, suppress scrolling completely
    if (this.shouldAllowAutoScroll()) {
      this.scrollManagement.temporarilySuppressScrollToClient(
        TIMING.SCROLL_SUPPRESSION,
      );
    } else {
      this.scrollManagement.temporarilySuppressScrollToClient(0);
    }

    this.adminDataService.collapseNode(node);

    this.resetEventSource();
  }

  /**
   * Handles node selection
   */
  async onNodeSelect(node: TreeNode): Promise<void> {
    this.selectedTreeNode.set(node);

    if (node.type === ADMIN_TREE.NODE_TYPES.LOAD_MORE) {
      // Load More is always user-initiated, so suppress scrolling
      this.scrollManagement.temporarilySuppressScrollToClient(0); // Indefinite suppression

      const loadData = node.data as {
        vendorId: string;
        groupId?: string;
        loading?: boolean;
      };
      if (isDefined(loadData.vendorId)) {
        await this.adminDataService.loadMoreClients(
          loadData.vendorId,
          loadData.groupId,
        );
      }
    } else if (isDefined(node.key) && isDefined(node.type)) {
      this.selectionService.selectNode(node);
      await this.navigationService.navigateToNode(node.type, node.key);

      // Only scroll for system-initiated selections
      if (
        node.type === ADMIN_TREE.NODE_TYPES.CLIENT &&
        this.shouldAllowAutoScroll()
      ) {
        this.scrollManagement.scrollToClient(node.key);
      }
    }

    this.resetEventSource();
  }

  /**
   * Updates tree selection programmatically without triggering navigation
   * Use this for syncing tree state with route changes or other non-user actions
   */
  setTreeSelection(node: TreeNode | null): void {
    this.selectedTreeNode.set(node);
    if (node) {
      this.selectionService.selectNode(node);
    }
  }

  /**
   * Handles node unselection
   */
  onNodeUnselect(node: TreeNode): void {
    if (this.selectedTreeNode()?.key === node.key) {
      this.selectedTreeNode.set(null);
      this.adminState.setSelectedClient(null);
    }
  }

  /**
   * Handles when a node is not found in the current tree data
   *
   * This is a critical method for handling deep links and search results where the target
   * client may not be in the currently loaded tree data. It implements several safeguards
   * to prevent infinite loops:
   *
   * 1. Duplicate Search Prevention: Checks if already searching for this node
   * 2. Delegates to AdminSearchService which has:
   *    - Maximum pagination attempts (50 attempts = 5000 clients max)
   *    - Error handling that breaks pagination loops
   *    - Fallback to global search API
   * 3. Clears scroll requests on failure to prevent retry loops
   *
   * Flow:
   * 1. Check if already searching (prevent duplicates)
   * 2. Set searching flags
   * 3. Fetch client using API with owner info
   * 4. Load parent hierarchy and paginate until client found
   * 5. Wait for node to appear in tree
   * 6. Select and scroll to node
   * 7. Clear searching flags and scroll requests
   *
   * @param event Contains the nodeKey (client ID) and optional context
   */
  onNodeNotFoundInData(event: NodeNotFoundEvent): void {
    const clientId = event.nodeKey;

    if (
      this.searchNavigation.searchingForNode() &&
      this.searchNavigation.searchingNodeKey() === clientId
    ) {
      return;
    }

    this.searchNavigation.searchingForNode.set(true);
    this.searchNavigation.searchingNodeKey.set(clientId);

    this.clientLoader
      .loadClientForTree(clientId)
      .then(async () => {
        const result = await this.clientLoader.waitAndSelect(clientId);
        if (result.found && result.node) {
          this.setEventSource(EventSource.SYSTEM);
          this.selectedTreeNode.set(result.node);
          this.searchNavigation.trySelectNode(clientId);
        } else {
          this.clientLoader.clearScrollRequests(clientId);
        }
      })
      .catch((error: unknown) => {
        this.clientLoader.clearScrollRequests(clientId);

        if (!(error instanceof Error && error.message.includes('not found'))) {
          this.handleError(error, 'Failed to load client for tree');
        }
      })
      .finally(() => {
        this.searchNavigation.searchingForNode.set(false);
        this.searchNavigation.searchingNodeKey.set(null);
        this.resetEventSource();
      });
  }

  /**
   * Handles search query changes
   */
  onSearchQueryChange(query: string): void {
    this.adminSearchService.updateQuery(query);
  }

  /**
   * Handles search result selection
   */
  async onSearchResultSelect(result: TreeSearchResult): Promise<void> {
    try {
      // Set event source to SEARCH to allow auto-scrolling
      this.setEventSource(EventSource.SEARCH);

      await this.adminSearchService.prepareResultForNavigation(result);
      await new Promise((resolve) => setTimeout(resolve, 100));

      // Delegate to search navigation service
      await this.searchNavigation.navigateToResult(result);
    } catch (error) {
      this.handleError(error, 'Failed to navigate to search result');
    } finally {
      this.resetEventSource();
    }
  }

  /**
   * Handles search clear
   */
  onSearchClear(): void {
    this.adminSearchService.clearSearch();
  }

  private handleError(error: unknown, message: string): void {
    console.error(message, error);

    // Check if this is a version mismatch error first
    const isVersionMismatch = this.adminTreeErrorHandler.handleAdminTreeError(
      error,
      message,
    );

    if (!isVersionMismatch) {
      // Set local error message for UI display
      this.errorMessage.set({ error, message });
    }
  }
}
