import { ResolveFn } from '@angular/router';
import { AccountsService, DistributorResponse } from '../../../api';
import { inject } from '@angular/core';
import { catchError, map, of } from 'rxjs';
import { MessageService } from 'primeng/api';
import { isDefined, isString } from '../../../shared/utils';

export const distributorResolver: ResolveFn<DistributorResponse | null> = (
  route,
  _state,
) => {
  const accountsService = inject(AccountsService);
  const messageService = inject(MessageService);
  const distributorId = route.paramMap.get('distributorId');

  if (!isDefined(distributorId) || !isString(distributorId)) {
    messageService.add({
      severity: 'error',
      summary: 'Error',
      detail: 'Invalid distributor ID',
    });
    return of(null);
  }

  return accountsService.getDistributor(distributorId).pipe(
    map((response) => {
      if (!isDefined(response)) {
        messageService.add({
          severity: 'error',
          summary: 'Error',
          detail: 'No distributor data found',
        });
        return null;
      }
      return response;
    }),
    catchError(() => {
      messageService.add({
        severity: 'error',
        summary: 'Error',
        detail: 'Failed to load distributor data',
      });
      return of(null);
    }),
  );
};
