import {
  Component,
  ElementRef,
  input,
  output,
  ViewChild,
  effect,
} from '@angular/core';
import { TreeNode } from 'primeng/api';
import { Tree, TreeModule, TreeNodeExpandEvent } from 'primeng/tree';
import { isNullOrUndefined } from '../../utils';

/**
 * Interface for handling node not found scenarios
 */
export interface NodeNotFoundEvent {
  nodeKey: string;
  context?: Record<string, unknown>;
}

/**
 * Interface for context extraction strategy
 */
export interface ContextExtractor {
  extractContext(
    nodeKey: string,
    nodes: TreeNode[],
  ): Record<string, unknown> | undefined;
}

@Component({
  selector: 'app-tree-panel',
  imports: [TreeModule],
  templateUrl: './tree-panel.component.html',
  styleUrl: './tree-panel.component.scss',
})
export class TreePanelComponent {
  @ViewChild('treeComponent') treeComponent!: Tree;
  @ViewChild('treeWrapper') treeWrapper!: ElementRef<HTMLDivElement>;

  readonly nodes = input<TreeNode[]>([]);
  readonly selectionMode = input<'single' | 'multiple' | 'checkbox'>('single');
  readonly loading = input(false);
  readonly searchingForNode = input(false);
  readonly searchingNodeKey = input<string | null>(null);
  readonly emptyMessage = input('No data available');
  readonly styleClass = input('');
  readonly selection = input<TreeNode | TreeNode[] | null>(null);
  readonly scrollToNodeKey = input<string | null>(null);
  readonly scrollToCenterNodeKey = input<string | null>(null);
  readonly contextExtractor = input<ContextExtractor | null>(null);

  readonly nodeSelect = output<TreeNode>();
  readonly nodeUnselect = output<TreeNode>();
  readonly nodeExpand = output<TreeNode>();
  readonly nodeCollapse = output<TreeNode>();
  readonly nodeNotFoundInData = output<NodeNotFoundEvent>();

  readonly loadingMessage = 'Loading...';

  constructor() {
    // Effect to watch for scroll requests
    effect(() => {
      const nodeKey = this.scrollToNodeKey();
      if (nodeKey !== null && nodeKey !== '') {
        // Delay scrolling to allow DOM updates
        setTimeout(() => this.scrollToNode(nodeKey), 150);
      }
    });

    // Effect to watch for center scroll requests (for expanded nodes)
    effect(() => {
      const nodeKey = this.scrollToCenterNodeKey();
      if (nodeKey !== null && nodeKey !== '') {
        // Delay scrolling to allow DOM updates
        setTimeout(() => this.scrollToCenterNode(nodeKey), 150);
      }
    });
  }

  onNodeSelect(event: { node: TreeNode }): void {
    this.nodeSelect.emit(event.node);
  }

  onNodeUnselect(event: { node: TreeNode }): void {
    this.nodeUnselect.emit(event.node);
  }

  onNodeExpand(event: TreeNodeExpandEvent): void {
    this.nodeExpand.emit(event.node);
  }

  onNodeCollapse(event: { node: TreeNode }): void {
    this.nodeCollapse.emit(event.node);
  }

  /**
   * Scrolls to a specific node in the tree and centers it in the viewport
   * @param nodeKey The key of the node to scroll to
   */
  private scrollToNode(nodeKey: string): void {
    // Step 1: Search in the data first
    const targetNode = this.findNodeInData(nodeKey);

    if (targetNode === null) {
      // Use the context extractor if provided, otherwise pass undefined context
      const extractor = this.contextExtractor();
      const context = extractor?.extractContext(nodeKey, this.nodes());

      this.nodeNotFoundInData.emit({
        nodeKey,
        context,
      });
      return;
    }

    // Step 2: Ensure node path is expanded
    this.expandPathToNode(targetNode);

    // Step 3: Use improved scroll approach with PrimeNG native methods
    this.performOptimizedScroll(nodeKey);
  }

  /**
   * Performs optimized scrolling using PrimeNG's native methods with fallbacks
   */
  private performOptimizedScroll(nodeKey: string): void {
    // Use requestAnimationFrame to ensure DOM updates are complete
    requestAnimationFrame(() => {
      requestAnimationFrame(() => {
        // First attempt: Use PrimeNG native scroll
        const success = this.scrollToNodeInDOM(nodeKey);

        if (!success) {
          // Retry after short delay if first attempt fails
          setTimeout(() => {
            this.scrollToNodeInDOM(nodeKey);
          }, 300);
        }
      });
    });
  }

  /**
   * Scrolls to center a specific node (for expanded nodes visibility)
   * This is different from scrollToNode - it only centers without selecting
   * @param nodeKey The key of the node to center in the viewport
   */
  private scrollToCenterNode(nodeKey: string): void {
    // Step 1: Search in the data first to ensure node exists
    const targetNode = this.findNodeInData(nodeKey);

    if (targetNode === null) {
      // Use the context extractor if provided, otherwise pass undefined context
      const extractor = this.contextExtractor();
      const context = extractor?.extractContext(nodeKey, this.nodes());

      this.nodeNotFoundInData.emit({
        nodeKey,
        context,
      });
      return;
    }

    // Step 2: Find the DOM element without expanding paths or triggering node events
    const nodeElement = this.findNodeElementInDOM(nodeKey);
    if (nodeElement) {
      // For expanded nodes, we want to scroll to show the children area, not just center the parent
      this.scrollToShowExpandedChildren(nodeElement, targetNode);
    }
  }

  /**
   * Scrolls to position the expanded node and its children optimally in the viewport
   * @param nodeElement The DOM element of the parent node
   * @param targetNode The tree node data
   */
  private scrollToShowExpandedChildren(
    nodeElement: HTMLElement,
    targetNode: TreeNode,
  ): void {
    const treeWrapper = this.treeWrapper?.nativeElement;
    if (isNullOrUndefined(treeWrapper)) {
      return;
    }

    // Check if the node is actually expanded and has children
    if (
      isNullOrUndefined(targetNode.expanded) ||
      !targetNode.children ||
      targetNode.children.length === 0
    ) {
      // If not expanded or no children, just center the node itself
      this.scrollWithoutVirtualScroll(nodeElement);
      return;
    }

    // Calculate position to show the parent node and some of its children
    const elementRect = nodeElement.getBoundingClientRect();
    const wrapperRect = treeWrapper.getBoundingClientRect();

    // Position the parent node in the upper third of the viewport
    // This allows the children to be visible below it
    const elementTopRelativeToWrapper = elementRect.top - wrapperRect.top;
    const wrapperHeight = treeWrapper.clientHeight;

    // Position parent at 1/3 from top instead of center
    const offsetFromTop = wrapperHeight / 3;
    const targetScrollTop =
      treeWrapper.scrollTop + elementTopRelativeToWrapper - offsetFromTop;

    // Use scrollTo if available, otherwise set scrollTop directly
    if (typeof treeWrapper.scrollTo === 'function') {
      treeWrapper.scrollTo({
        top: Math.max(0, targetScrollTop),
        behavior: 'smooth',
      });
    } else {
      treeWrapper.scrollTop = Math.max(0, targetScrollTop);
    }

    // Highlight the parent node briefly to show what was expanded
    setTimeout(() => this.highlightElement(nodeElement), 300);
  }

  /**
   * Recursively searches for a node in the tree data
   */
  private findNodeInData(nodeKey: string): TreeNode | null {
    const searchInNodes = (nodes: TreeNode[]): TreeNode | null => {
      for (const node of nodes) {
        if (
          node.key === nodeKey ||
          node.data?.id === nodeKey ||
          node.label?.includes(nodeKey) === true
        ) {
          return node;
        }

        if (node.children && node.children.length > 0) {
          const found = searchInNodes(node.children);
          if (found) return found;
        }
      }
      return null;
    };

    return searchInNodes(this.nodes());
  }

  /**
   * Expands all parent nodes to make the target node visible
   */
  private expandPathToNode(targetNode: TreeNode): void {
    const pathToExpand: TreeNode[] = [];

    const findPath = (
      nodes: TreeNode[],
      target: TreeNode,
      path: TreeNode[],
    ): boolean => {
      for (const node of nodes) {
        const currentPath = [...path, node];

        if (node === target) {
          pathToExpand.push(...path);
          return true;
        }

        if (node.children && node.children.length > 0) {
          if (findPath(node.children, target, currentPath)) {
            return true;
          }
        }
      }
      return false;
    };

    findPath(this.nodes(), targetNode, []);

    // Expand all nodes in the path
    for (const nodeToExpand of pathToExpand) {
      if (nodeToExpand.expanded !== true) {
        nodeToExpand.expanded = true;
        this.nodeExpand.emit(nodeToExpand);
      }
    }
  }

  /**
   * Scrolls to a node using the appropriate method based on virtual scroll setting
   * @returns true if successful, false otherwise
   */
  private scrollToNodeInDOM(nodeKey: string): boolean {
    const treeComponent = this.treeComponent;

    if (isNullOrUndefined(treeComponent)) {
      return false;
    }

    try {
      // First, find the node in the data to ensure it exists
      const nodeData = this.findNodeInData(nodeKey);
      if (isNullOrUndefined(nodeData)) {
        return false;
      }

      // Find the DOM element
      const nodeElement = this.findNodeElementInDOM(nodeKey);
      if (isNullOrUndefined(nodeElement)) {
        return this.tryAlternativeScrollMethods(nodeKey);
      }

      // Choose scroll method based on virtual scroll setting
      if (treeComponent.virtualScroll === true) {
        return this.scrollWithVirtualScroll(
          treeComponent,
          nodeElement,
          nodeKey,
        );
      } else {
        return this.scrollWithoutVirtualScroll(nodeElement);
      }
    } catch (error) {
      console.error('Error scrolling to tree node:', error);
      return false;
    }
  }

  /**
   * Scrolls using virtual scroll method
   */
  private scrollWithVirtualScroll(
    treeComponent: Tree,
    nodeElement: HTMLElement,
    _nodeKey: string,
  ): boolean {
    const treeContainer = treeComponent.el?.nativeElement;
    if (isNullOrUndefined(treeContainer)) {
      return false;
    }

    // Calculate position to center the element within the virtual scroll container
    const elementRect = nodeElement.getBoundingClientRect();
    const containerRect = treeContainer.getBoundingClientRect();

    const elementTopRelativeToContainer = elementRect.top - containerRect.top;
    const containerHeight = treeContainer.clientHeight;
    const elementHeight = elementRect.height;

    // Center calculation for virtual scroll
    const centerOffset = (containerHeight - elementHeight) / 2;
    const targetScrollTop =
      treeContainer.scrollTop + elementTopRelativeToContainer - centerOffset;

    treeComponent.scrollTo({
      top: Math.max(0, targetScrollTop),
      behavior: 'smooth',
    });

    setTimeout(() => this.highlightElement(nodeElement), 300);
    return true;
  }

  /**
   * Scrolls without virtual scroll using wrapper element
   */
  private scrollWithoutVirtualScroll(nodeElement: HTMLElement): boolean {
    const treeWrapper = this.treeWrapper?.nativeElement;
    if (isNullOrUndefined(treeWrapper)) {
      return false;
    }

    // Calculate position to center the element within the tree container
    const elementRect = nodeElement.getBoundingClientRect();
    const wrapperRect = treeWrapper.getBoundingClientRect();

    // Calculate the scroll position to center the element
    const elementTopRelativeToWrapper = elementRect.top - wrapperRect.top;
    const wrapperHeight = treeWrapper.clientHeight;
    const elementHeight = elementRect.height;

    // Center calculation: position element in middle of visible area
    const centerOffset = (wrapperHeight - elementHeight) / 2;
    const targetScrollTop =
      treeWrapper.scrollTop + elementTopRelativeToWrapper - centerOffset;

    // Use scrollTo if available, otherwise set scrollTop directly
    if (typeof treeWrapper.scrollTo === 'function') {
      treeWrapper.scrollTo({
        top: Math.max(0, targetScrollTop),
        behavior: 'smooth',
      });
    } else {
      // For browsers that don't support scrollTo, use scrollTop directly
      treeWrapper.scrollTop = Math.max(0, targetScrollTop);
    }

    setTimeout(() => this.highlightElement(nodeElement), 300);
    return true;
  }

  /**
   * Try alternative scroll methods when primary methods fail
   */
  private tryAlternativeScrollMethods(nodeKey: string): boolean {
    // Try virtual scroll by index if available
    const treeComponent = this.treeComponent;
    if (treeComponent?.virtualScroll === true) {
      const nodeIndex = this.findNodeIndex(nodeKey);
      if (nodeIndex !== -1) {
        treeComponent.scrollToVirtualIndex(nodeIndex);
        return true;
      }
    }

    // Final fallback
    return this.fallbackDOMScroll(nodeKey);
  }

  /**
   * Finds the DOM element for a specific node key
   */
  private findNodeElementInDOM(nodeKey: string): HTMLElement | null {
    const treeWrapper = this.treeWrapper?.nativeElement;
    if (isNullOrUndefined(treeWrapper)) {
      return null;
    }

    return this.findNodeElement(treeWrapper, nodeKey);
  }

  /**
   * Finds the index of a node in the flattened tree structure
   */
  private findNodeIndex(nodeKey: string): number {
    let index = 0;

    const searchInNodes = (nodes: TreeNode[]): number => {
      for (const node of nodes) {
        if (
          node.key === nodeKey ||
          node.data?.id === nodeKey ||
          node.label?.includes(nodeKey) === true
        ) {
          return index;
        }

        index++;

        if (
          node.children &&
          node.children.length > 0 &&
          node.expanded === true
        ) {
          const foundIndex = searchInNodes(node.children);
          if (foundIndex !== -1) {
            return foundIndex;
          }
        }
      }
      return -1;
    };

    return searchInNodes(this.nodes());
  }

  /**
   * Fallback DOM-based scrolling method
   */
  private fallbackDOMScroll(nodeKey: string): boolean {
    const treeWrapper = this.treeWrapper?.nativeElement;

    if (isNullOrUndefined(treeWrapper)) {
      return false;
    }

    try {
      const nodeElement = this.findNodeElement(treeWrapper, nodeKey);
      if (nodeElement === null) {
        return false;
      }

      // Use custom centering logic instead of scrollIntoView
      const elementRect = nodeElement.getBoundingClientRect();
      const wrapperRect = treeWrapper.getBoundingClientRect();

      const elementTopRelativeToWrapper = elementRect.top - wrapperRect.top;
      const wrapperHeight = treeWrapper.clientHeight;
      const elementHeight = elementRect.height;

      const centerOffset = (wrapperHeight - elementHeight) / 2;
      const targetScrollTop =
        treeWrapper.scrollTop + elementTopRelativeToWrapper - centerOffset;

      if (typeof treeWrapper.scrollTo === 'function') {
        treeWrapper.scrollTo({
          top: Math.max(0, targetScrollTop),
          behavior: 'smooth',
        });
      } else {
        treeWrapper.scrollTop = Math.max(0, targetScrollTop);
      }

      this.highlightElement(nodeElement);
      return true;
    } catch (error) {
      console.error('Error in fallback DOM scroll:', error);
      return false;
    }
  }

  private findNodeElement(
    treeElement: HTMLElement,
    nodeKey: string,
  ): HTMLElement | null {
    // Strategy 1: Try PrimeNG-specific selectors
    let nodeElement = this.findBySelectors(treeElement, nodeKey);

    // Strategy 2: Search through all tree nodes
    nodeElement ??= this.findByNodeSearch(treeElement, nodeKey);

    // Strategy 3: Look for tree node content
    nodeElement ??= this.findByContentSearch(treeElement, nodeKey);

    return nodeElement;
  }

  private findBySelectors(
    treeElement: HTMLElement,
    nodeKey: string,
  ): HTMLElement | null {
    const selectors = [
      `[data-pc-section="node"][aria-label*="${nodeKey}"]`,
      `.p-treenode[data-key="${nodeKey}"]`,
      `[data-id="${nodeKey}"]`,
      `[aria-label*="${nodeKey}"]`,
    ];

    for (const selector of selectors) {
      const element = treeElement.querySelector(selector) as HTMLElement;
      if (element !== null) {
        return element;
      }
    }
    return null;
  }

  private findByNodeSearch(
    treeElement: HTMLElement,
    nodeKey: string,
  ): HTMLElement | null {
    const allNodes = treeElement.querySelectorAll(
      '.p-treenode, .p-treenode-content, [role="treeitem"]',
    );

    for (const node of Array.from(allNodes)) {
      const htmlNode = node as HTMLElement;

      if (this.isMatchingNode(htmlNode, nodeKey)) {
        return htmlNode;
      }
    }
    return null;
  }

  private findByContentSearch(
    treeElement: HTMLElement,
    nodeKey: string,
  ): HTMLElement | null {
    const contentNodes = treeElement.querySelectorAll(
      '.p-treenode-label, .p-treenode-content',
    );

    for (const node of Array.from(contentNodes)) {
      const htmlNode = node as HTMLElement;
      const textContent = htmlNode.textContent;

      if (textContent?.includes(nodeKey) === true) {
        const treeNode = htmlNode.closest('.p-treenode') as HTMLElement;
        if (treeNode !== null) {
          return treeNode;
        }
      }
    }
    return null;
  }

  private isMatchingNode(htmlNode: HTMLElement, nodeKey: string): boolean {
    const dataKey = htmlNode.getAttribute('data-key');
    const ariaLabel = htmlNode.getAttribute('aria-label');
    const textContent = htmlNode.textContent?.trim();

    return (
      dataKey === nodeKey ||
      ariaLabel?.includes(nodeKey) === true ||
      textContent?.includes(nodeKey) === true ||
      htmlNode.querySelector(`[data-key="${nodeKey}"]`) !== null
    );
  }

  private scrollToElement(
    treeElement: HTMLElement,
    nodeElement: HTMLElement,
  ): void {
    const treeRect = treeElement.getBoundingClientRect();
    const nodeRect = nodeElement.getBoundingClientRect();
    const treeScrollTop = treeElement.scrollTop;
    const nodeOffsetTop = nodeRect.top - treeRect.top + treeScrollTop;
    const centerPosition =
      nodeOffsetTop - treeRect.height / 2 + nodeRect.height / 2;

    treeElement.scrollTo({
      top: Math.max(0, centerPosition),
      behavior: 'smooth',
    });
  }

  private highlightElement(nodeElement: HTMLElement): void {
    nodeElement.style.transition = 'background-color 0.3s ease';
    nodeElement.style.backgroundColor = 'rgba(59, 130, 246, 0.1)';

    setTimeout(() => {
      if (nodeElement !== null) {
        nodeElement.style.backgroundColor = '';
        setTimeout(() => {
          if (nodeElement !== null) {
            nodeElement.style.transition = '';
          }
        }, 300);
      }
    }, 1500);
  }

  /**
   * Alternative method to find nodes using PrimeNG Tree component properties
   */
  private findNodeUsingPrimeNGTree(nodeKey: string): HTMLElement | null {
    if (isNullOrUndefined(this.treeComponent)) {
      return null;
    }

    try {
      // Get the PrimeNG Tree's native element
      const treeEl = this.treeComponent.el?.nativeElement;
      if (isNullOrUndefined(treeEl)) {
        return null;
      }

      // Try different strategies to find the node
      const strategies = [
        () => treeEl.querySelector(`[data-key="${nodeKey}"]`),
        () => treeEl.querySelector(`[aria-label*="${nodeKey}"]`),
        () => {
          // Search through all tree items
          const allItems = treeEl.querySelectorAll(
            '[role="treeitem"], .p-treenode',
          );
          for (const item of Array.from(allItems)) {
            const element = item as HTMLElement;
            if (element.textContent?.includes(nodeKey) === true) {
              return element;
            }
          }
          return null;
        },
      ];

      for (const element of strategies) {
        const result = element();
        if (result !== null) {
          return result as HTMLElement;
        }
      }

      return null;
    } catch (error) {
      console.error('Error in findNodeUsingPrimeNGTree:', error);
      return null;
    }
  }
}
