import { computed, inject, Injectable } from '@angular/core';
import { TreeNode } from 'primeng/api';
import { AdminStateService } from '../state/admin-state.service';
import { AdminTreeService } from './admin-tree.service';
import { ApiSchemaService } from '../../../../core/services/api-schema.service';
import { AdminSearchService } from '../admin-search.service';
import {
  ClientResponse,
  ClientListItem,
  DistributorResponse,
  DistributorListItem,
  VendorListItem,
  VendorResponse,
} from '../../../../api';
import { ADMIN_TREE } from '../../models/tree-node-data.interface';
import { isDefined, isNullOrUndefined } from '../../../../shared/utils';
import { UserPermissionLevel } from '../../models/admin-tree-state.interface';

@Injectable({
  providedIn: 'root',
})
export class AdminTreeManagementService {
  private readonly adminState = inject(AdminStateService);
  private readonly adminTreeService = inject(AdminTreeService);
  private readonly apiSchemaService = inject(ApiSchemaService);
  private readonly adminSearchService = inject(AdminSearchService);

  readonly hierarchyTree = computed(() => {
    const distributor = this.adminState.vendorDistributor();
    const vendor = this.adminState.clientVendor();
    const selectedClient = this.adminState.selectedClient();
    const usesModernSchema = this.apiSchemaService.usesModernSchema();
    const permissionLevel = this.adminState.userPermissionLevel();

    // Ensure computed re-runs when selection changes
    const _selKey = this.adminState.selectedNodeKey();
    const _selType = this.adminState.selectedNodeType();

    const searchExpandedNodes = this.adminSearchService.expandedForSearch();

    // Client-level users: no tree, just return empty
    if (permissionLevel === UserPermissionLevel.CLIENT_ONLY) {
      return [];
    }

    // System-level users (Super Admin): full hierarchy
    if (
      permissionLevel === UserPermissionLevel.SUPER_ADMIN &&
      usesModernSchema
    ) {
      const tree = this.buildSuperAdminHierarchy(selectedClient);
      return this.applySearchEnhancements(tree, searchExpandedNodes);
    }

    // Distributor-level users: start from distributor level
    if (permissionLevel === UserPermissionLevel.DISTRIBUTOR && distributor) {
      const tree = this.buildDistributorHierarchy(distributor, selectedClient);
      return this.applySearchEnhancements(tree, searchExpandedNodes);
    }

    // Vendor-level users: start from vendor level
    if (permissionLevel === UserPermissionLevel.VENDOR && vendor) {
      const tree = this.buildVendorOnlyHierarchy(vendor, selectedClient);
      return this.applySearchEnhancements(tree, searchExpandedNodes);
    }

    return [];
  });

  private buildSuperAdminHierarchy(
    selectedClient: ClientResponse | null,
  ): TreeNode[] {
    // Use distributor-scoped expanded nodes - super admin context uses 'super-admin' as distributor key
    const superAdminExpandedNodes =
      this.adminState.expandedNodes()['super-admin'] ?? new Set();
    const usesModernSchema = this.apiSchemaService.usesModernSchema();
    const allDistributors = this.adminState.distributors();

    const isSuperAdminExpanded =
      usesModernSchema || superAdminExpandedNodes.has('super-admin-root');

    const superAdminNode = this.adminTreeService.createSuperAdminNode(
      isSuperAdminExpanded,
      [],
      this.isNodeSelected(
        'super-admin-root',
        ADMIN_TREE.NODE_TYPES.SUPER_ADMIN,
      ),
    );

    if (isSuperAdminExpanded && allDistributors.length > 0) {
      // Build tree with all distributors using distributor-scoped data
      superAdminNode.children = allDistributors.map((dist) => {
        return this.buildDistributorNodeWithContext(dist, selectedClient);
      });
    } else {
      superAdminNode.children = [];
    }

    return [superAdminNode];
  }

  /**
   * Builds a distributor node with its own vendor hierarchy using distributor-scoped data
   */
  private buildDistributorNodeWithContext(
    distributor: DistributorListItem,
    selectedClient: ClientResponse | null,
  ): TreeNode {
    const distributorId = distributor.id;
    const expandedDistributors = this.adminState.expandedDistributors();
    const isDistributorExpanded = expandedDistributors.has(distributorId);

    const distributorNode = this.adminTreeService.createDistributorNode(
      {
        ...distributor,
        entity: {
          id: distributor.id,
          name: distributor.name,
          creationDate: new Date().toISOString(),
          modifiedDate: new Date().toISOString(),
        },
        owner: { id: '', name: '' },
        tags: null,
      } as DistributorResponse,
      isDistributorExpanded,
      this.isNodeSelected(distributor.id, ADMIN_TREE.NODE_TYPES.DISTRIBUTOR),
    );

    if (isDistributorExpanded) {
      // Get vendors specific to THIS distributor
      const distributorVendors =
        this.adminState.getDistributorVendors(distributorId);

      distributorNode.children = distributorVendors.map((vendor) =>
        this.buildVendorNodeWithDistributorContext(
          vendor,
          selectedClient,
          distributorId,
        ),
      );
    } else {
      distributorNode.children = [];
    }

    return distributorNode;
  }

  private buildDistributorHierarchy(
    distributor: DistributorResponse,
    selectedClient: ClientResponse | null,
  ): TreeNode[] {
    const distributorId = distributor.id;
    const expandedDistributors = this.adminState.expandedDistributors();
    const isDistributorExpanded = expandedDistributors.has(distributorId);

    const distributorNode = this.adminTreeService.createDistributorNode(
      distributor,
      isDistributorExpanded,
      this.isNodeSelected(distributorId, ADMIN_TREE.NODE_TYPES.DISTRIBUTOR),
    );

    if (isDistributorExpanded) {
      // Use distributor-scoped vendors instead of global vendors
      const distributorVendors =
        this.adminState.getDistributorVendors(distributorId);
      distributorNode.children = distributorVendors.map((vendor) =>
        this.buildVendorNodeWithDistributorContext(
          vendor,
          selectedClient,
          distributorId,
        ),
      );
    } else {
      distributorNode.children = [];
    }

    return [distributorNode];
  }

  private buildVendorOnlyHierarchy(
    vendor: VendorResponse | VendorListItem,
    selectedClient: ClientResponse | null,
  ): TreeNode[] {
    // For vendor-only hierarchy, use vendor.owner.id as the context
    const distributorId =
      vendor.owner?.id ?? this.adminState.getActiveContextId();

    if (isNullOrUndefined(distributorId)) {
      // Fallback: create a simple vendor node without distributor context
      const vendorNode = this.adminTreeService.createVendorNode(
        vendor,
        false,
        [],
        this.isNodeSelected(vendor.id, ADMIN_TREE.NODE_TYPES.VENDOR),
      );
      return [vendorNode];
    }

    const vendorNode = this.buildVendorNodeWithDistributorContext(
      vendor,
      selectedClient,
      distributorId,
    );

    return [vendorNode];
  }

  private createLoadMoreNode(
    vendorId: string,
    groupId: string | undefined,
    loading: boolean,
  ): TreeNode {
    const key = `loadmore_${vendorId}${isDefined(groupId) ? '_' + groupId : ''}`;
    return {
      key: key,
      label: 'Load More...',
      type: ADMIN_TREE.NODE_TYPES.LOAD_MORE,
      leaf: true,
      icon: loading ? 'pi pi-spin pi-spinner' : 'pi pi-plus',
      data: {
        type: ADMIN_TREE.NODE_TYPES.LOAD_MORE,
        vendorId: vendorId,
        groupId: groupId,
        loading: loading,
      },
      styleClass: 'load-more-node',
    };
  }

  /**
   * Builds a vendor node with distributor context, using distributor-scoped data
   */
  private buildVendorNodeWithDistributorContext(
    vendor: VendorResponse | VendorListItem,
    selectedClient: ClientResponse | null,
    distributorId: string,
  ): TreeNode {
    const vendorClients = this.adminState.getDistributorVendorClients(
      distributorId,
      vendor.id,
    );
    const vendorGroups = this.adminState.getDistributorVendorGroups(
      distributorId,
      vendor.id,
    );
    const isVendorExpanded = this.adminState.isDistributorNodeExpanded(
      distributorId,
      vendor.id,
    );

    const children: TreeNode[] = [];
    const vendorNode = this.adminTreeService.createVendorNode(
      vendor,
      isVendorExpanded,
      children,
      this.isNodeSelected(vendor.id, ADMIN_TREE.NODE_TYPES.VENDOR),
    );

    if (isVendorExpanded) {
      // Add group nodes if groups exist
      if (vendorGroups.length > 0) {
        children.push(
          ...vendorGroups.map((group) => {
            // Use distributor-scoped group expansion check
            const isGroupExpanded = this.adminState.isDistributorGroupExpanded(
              distributorId,
              group.id,
            );
            const groupChildren: TreeNode[] = [];
            const groupNode = this.adminTreeService.createGroupNode(
              group,
              isGroupExpanded,
              groupChildren,
              this.isNodeSelected(group.id, ADMIN_TREE.NODE_TYPES.GROUP),
            );

            if (isGroupExpanded) {
              const clientsInGroup = this.getGroupClientsForDistributor(
                distributorId,
                vendor.id,
                group.id,
              );
              groupChildren.push(
                ...clientsInGroup.map((client) =>
                  this.adminTreeService.createClientNode(
                    client,
                    client.id === selectedClient?.id,
                  ),
                ),
              );

              // Add "Load More" node if needed for this group
              const groupPagination =
                this.adminState.getDistributorPaginationState(
                  distributorId,
                  vendor.id,
                  group.id,
                );
              if (groupPagination?.hasMore === true) {
                groupChildren.push(
                  this.createLoadMoreNode(
                    vendor.id,
                    group.id,
                    groupPagination?.loadingMore ?? false,
                  ),
                );
              }
            }

            return groupNode;
          }),
        );
      }

      // Add direct clients (not in groups)
      children.push(
        ...vendorClients.map((client) =>
          this.adminTreeService.createClientNode(
            client,
            client.id === selectedClient?.id,
          ),
        ),
      );

      // Add "Load More" node for vendor if needed
      const vendorPagination = this.adminState.getDistributorPaginationState(
        distributorId,
        vendor.id,
      );
      if (vendorPagination?.hasMore === true) {
        children.push(
          this.createLoadMoreNode(
            vendor.id,
            undefined,
            vendorPagination?.loadingMore ?? false,
          ),
        );
      }
    }
    return vendorNode;
  }

  /**
   * Helper method to get group clients for a specific distributor
   */
  private getGroupClientsForDistributor(
    distributorId: string,
    vendorId: string,
    groupId: string,
  ): ClientListItem[] {
    return this.adminState.getGroupClients(distributorId, vendorId, groupId);
  }

  private applySearchEnhancements(
    nodes: TreeNode[],
    searchExpandedNodes: Set<string>,
  ): TreeNode[] {
    return nodes.map((node) =>
      this.enhanceNodeForSearch(node, searchExpandedNodes),
    );
  }

  private enhanceNodeForSearch(
    node: TreeNode,
    searchExpandedNodes: Set<string>,
  ): TreeNode {
    const enhanced = { ...node };

    if (this.adminSearchService.isNodeHighlighted(node.key as string)) {
      enhanced.styleClass = [enhanced.styleClass, 'search-highlighted']
        .filter(Boolean)
        .join(' ');
    }

    if (searchExpandedNodes.has(node.key as string)) {
      enhanced.expanded = true;
    }

    if (enhanced.children) {
      enhanced.children = enhanced.children.map((child) =>
        this.enhanceNodeForSearch(child, searchExpandedNodes),
      );
    }

    return enhanced;
  }

  /**
   * Helper method to check if a node is currently selected
   * @param key - The node key to check
   * @param type - The node type to check
   * @returns boolean indicating if node is selected
   */
  private isNodeSelected(key: string, type: string): boolean {
    return (
      this.adminState.selectedNodeKey() === key &&
      this.adminState.selectedNodeType() === type
    );
  }
}
