import {
  Component,
  inject,
  signal,
  DestroyRef,
  ChangeDetectionStrategy,
  OnInit,
} from '@angular/core';
import { CommonModule } from '@angular/common';
import { DynamicDialogConfig, DynamicDialogRef } from 'primeng/dynamicdialog';
import { ButtonModule } from 'primeng/button';
import { FileSelectEvent, FileUploadModule } from 'primeng/fileupload';
import { ProgressBarModule } from 'primeng/progressbar';
import { MessageModule } from 'primeng/message';
import { TableModule } from 'primeng/table';
import { takeUntilDestroyed } from '@angular/core/rxjs-interop';
import { finalize, forkJoin } from 'rxjs';
import * as ExcelJS from 'exceljs';

import { BulkImportService } from '../../../services/bulk-import.service';
import { BulkImportResponse } from '../../../../../api/model/bulkImportResponse';
import { BroadcastEventMessageService } from '../../../services/broadcast-event-messages.service';
import { ToastService } from '../../../../../core/services/toast.service';
import { EntitiesService } from '../../../../../api/api/entities.service';
import { AdminStateService } from '../../../services/state/admin-state.service';

@Component({
  selector: 'app-bulk-import-dialog',
  imports: [
    CommonModule,
    ButtonModule,
    FileUploadModule,
    ProgressBarModule,
    MessageModule,
    TableModule,
  ],
  templateUrl: './bulk-import-dialog.component.html',
  styleUrl: './bulk-import-dialog.component.scss',
  changeDetection: ChangeDetectionStrategy.OnPush,
})
export class BulkImportDialogComponent implements OnInit {
  private readonly bulkImportService = inject(BulkImportService);
  private readonly dialogRef = inject(DynamicDialogRef);
  private readonly dialogConfig = inject(DynamicDialogConfig);
  private readonly destroyRef = inject(DestroyRef);
  private readonly broadcastService = inject(BroadcastEventMessageService);
  private readonly toastService = inject(ToastService);
  private readonly entitiesService = inject(EntitiesService);
  private readonly adminStateService = inject(AdminStateService);

  readonly ownerId = signal<string>('');
  readonly clientName = signal<string>('');
  readonly selectedFile = signal<File | null>(null);
  readonly isUploading = signal(false);
  readonly uploadProgress = signal(0);
  readonly importResult = signal<BulkImportResponse | null>(null);
  readonly errorMessage = signal<string | null>(null);
  readonly isLoadingTemplate = signal(false);

  readonly maxFileSize = 10 * 1024 * 1024; // 10MB

  // Template data
  private deviceTypes: string[] = [];
  private assetTypes: string[] = [];
  private costCentres: string[] = [];
  private assetGroups: string[] = [];
  private primaryColor = '#1976d2'; // Default blue color

  constructor() {
    const ownerId = this.dialogConfig.data?.['ownerId'];
    if (ownerId !== null && ownerId !== undefined) {
      this.ownerId.set(ownerId);
    }

    const clientName = this.dialogConfig.data?.['clientName'];
    if (clientName !== null && clientName !== undefined) {
      this.clientName.set(clientName);
    } else {
      // Fallback to get from admin state
      const selectedClient = this.adminStateService.selectedClient();
      if (selectedClient?.name !== null && selectedClient?.name !== undefined) {
        this.clientName.set(selectedClient.name);
      }
    }

    // Get primary color from CSS variable or client theme
    this.getPrimaryColor();
  }

  private getPrimaryColor(): void {
    try {
      // Try to get the primary color from CSS variables
      const primaryColor = getComputedStyle(
        document.documentElement,
      ).getPropertyValue('--primary-color');
      if (
        primaryColor !== null &&
        primaryColor !== undefined &&
        primaryColor.trim() !== ''
      ) {
        this.primaryColor = primaryColor.trim();
      }
    } catch {
      console.warn('Could not get primary color, using default');
    }
  }

  ngOnInit(): void {
    this.loadTemplateData();
  }

  private loadTemplateData(): void {
    const ownerId = this.ownerId();
    if (ownerId === null || ownerId === undefined || ownerId === '') return;

    this.isLoadingTemplate.set(true);

    forkJoin({
      deviceTypes: this.entitiesService.listDeviceTypes(ownerId),
      assetTypes: this.entitiesService.listAssetTypes(ownerId),
      costCentres: this.entitiesService.listCostCentres(ownerId),
      assetGroups: this.entitiesService.listAssetGroups(ownerId),
    })
      .pipe(
        takeUntilDestroyed(this.destroyRef),
        finalize(() => this.isLoadingTemplate.set(false)),
      )
      .subscribe({
        next: (data) => {
          this.deviceTypes = data.deviceTypes.items
            .map((dt) => dt.name)
            .filter(
              (name): name is string => name !== null && name !== undefined,
            )
            .sort();
          this.assetTypes = data.assetTypes.items
            .map((at) => at.name)
            .filter(
              (name): name is string => name !== null && name !== undefined,
            )
            .sort();
          this.costCentres = data.costCentres.items
            .map((cc) => cc.name)
            .filter(
              (name): name is string => name !== null && name !== undefined,
            )
            .sort();
          this.assetGroups = data.assetGroups.items
            .map((ag) => ag.name)
            .filter(
              (name): name is string => name !== null && name !== undefined,
            )
            .sort();
        },
        error: () => {
          this.toastService.showWarning(
            'Template Data',
            'Could not load all dropdown options. Template will be generated with basic structure.',
          );
        },
      });
  }

  onFileSelect(event: FileSelectEvent): void {
    this.errorMessage.set(null);
    this.importResult.set(null);

    const file = event.files[0];
    if (file === null || file === undefined) {
      return;
    }

    // Validate file extension
    const extension = file.name.split('.').pop()?.toLowerCase();
    if (extension !== 'csv') {
      this.errorMessage.set('Only CSV files are supported');
      this.selectedFile.set(null);
      return;
    }

    // Validate file size
    if (file.size > this.maxFileSize) {
      this.errorMessage.set('File size cannot exceed 10MB');
      this.selectedFile.set(null);
      return;
    }

    this.selectedFile.set(file);
  }

  onUpload(): void {
    const file = this.selectedFile();
    const ownerId = this.ownerId();

    if (
      file === null ||
      file === undefined ||
      ownerId === null ||
      ownerId === undefined ||
      ownerId === ''
    ) {
      this.errorMessage.set('No file selected or owner ID not provided');
      return;
    }

    this.isUploading.set(true);
    this.uploadProgress.set(0);
    this.errorMessage.set(null);
    this.importResult.set(null);

    this.bulkImportService
      .bulkImportDevices(ownerId, file, 'body', true)
      .pipe(
        takeUntilDestroyed(this.destroyRef),
        finalize(() => {
          this.isUploading.set(false);
          this.uploadProgress.set(100);
        }),
      )
      .subscribe({
        next: (response: BulkImportResponse) => {
          this.importResult.set(response);

          if (response.successCount > 0) {
            this.toastService.showSuccess(
              'Import Complete',
              `Successfully imported ${response.successCount} device(s)`,
            );
            // Broadcast entity update to refresh grids
            this.broadcastService.broadcast('entity-updated', {
              entityType: 'devices',
              operation: 'create',
            });
          }

          if (response.failureCount > 0) {
            this.toastService.showWarning(
              'Import Completed with Errors',
              `${response.failureCount} row(s) failed to import. See details below.`,
            );
          }
        },
        error: (error: { error?: { Message?: string }; message?: string }) => {
          const errorMsg =
            (error?.error?.Message !== null &&
            error?.error?.Message !== undefined &&
            error?.error?.Message !== ''
              ? error.error.Message
              : error?.message !== null &&
                  error?.message !== undefined &&
                  error?.message !== ''
                ? error.message
                : null) ?? 'An error occurred during bulk import';
          this.errorMessage.set(errorMsg);
          this.toastService.showError('Import Failed', errorMsg);
          throw error;
        },
      });
  }

  onCancel(): void {
    this.dialogRef.close();
  }

  onClose(): void {
    const result = this.importResult();
    this.dialogRef.close(result);
  }

  async downloadTemplate(): Promise<void> {
    const clientName = this.clientName() || 'Client';
    const fileName = `${clientName} - Bulk Import Template.xlsx`;

    try {
      // Create workbook
      const workbook = new ExcelJS.Workbook();
      workbook.creator = 'CypherView';
      workbook.created = new Date();

      // Create main worksheet
      const worksheet = workbook.addWorksheet('Import Data', {
        properties: { tabColor: { argb: this.hexToArgb(this.primaryColor) } },
      });

      // Define headers
      const headers = [
        'DeviceSerialNr',
        'DeviceIMEI',
        'DeviceUnitType',
        'Device_PreTested',
        'Device_Comment',
        'SimCard_SIMNumber',
        'SimCard_VoiceNumber',
        'SimCard_Network',
        'SimCard_Comment',
        'AssetName',
        'AssetTypeName',
        'AssetCostCentreName',
        'AssetGroupNames',
        'AssetComment',
      ];

      // Add header row
      const headerRow = worksheet.addRow(headers);

      // Style header row
      const primaryColorArgb = this.hexToArgb(this.primaryColor);
      headerRow.eachCell((cell, colNumber) => {
        cell.font = { bold: true, color: { argb: 'FFFFFFFF' } };
        cell.fill = {
          type: 'pattern',
          pattern: 'solid',
          fgColor: { argb: primaryColorArgb },
        };
        cell.alignment = { horizontal: 'center', vertical: 'middle' };
        cell.border = {
          top: { style: 'thin' },
          left: { style: 'thin' },
          bottom: { style: 'thin' },
          right: { style: 'thin' },
        };

        // Add comment/note to AssetGroupNames column (column 13)
        if (colNumber === 13) {
          cell.note = {
            texts: [
              {
                font: { bold: true, size: 10, color: { argb: 'FF000000' } },
                text: 'Multiple Groups:\n',
              },
              {
                font: { size: 9, color: { argb: 'FF000000' } },
                text: 'You can enter multiple asset groups separated by commas.\n\nExample: Fleet,VIP,Premium\n\nUse the dropdown to see available groups, then manually type multiple values separated by commas.',
              },
            ],
            margins: {
              insetmode: 'custom',
              inset: [0.13, 0.13, 0.25, 0.25],
            },
          };
        }
      });

      // Set column widths
      worksheet.columns = [
        { width: 18 }, // DeviceSerialNr
        { width: 20 }, // DeviceIMEI
        { width: 20 }, // DeviceUnitType
        { width: 18 }, // Device_PreTested
        { width: 25 }, // Device_Comment
        { width: 24 }, // SimCard_SIMNumber
        { width: 24 }, // SimCard_VoiceNumber
        { width: 20 }, // SimCard_Network
        { width: 20 }, // SimCard_Comment
        { width: 15 }, // AssetName
        { width: 20 }, // AssetTypeName
        { width: 25 }, // AssetCostCentreName
        { width: 20 }, // AssetGroupNames
        { width: 25 }, // AssetComment
      ];

      // Add sample data rows
      worksheet.addRow([
        '1234567890',
        '123456789012345',
        this.deviceTypes[0] || 'GPS Tracker',
        '1',
        'Test device comment',
        '1234567890',
        '+27123456789',
        'Vodacom',
        'SIM comment',
        'Vehicle 1',
        this.assetTypes[0] || 'Vehicle',
        this.costCentres[0] || 'Default',
        this.assetGroups[0] || 'Fleet',
        'Asset comment',
      ]);

      worksheet.addRow([
        '1234567891',
        '123456789012346',
        this.deviceTypes[0] || 'GPS Tracker',
        '0',
        'Another device',
        '1234567891',
        '+27123456790',
        'MTN',
        'Another SIM',
        'Vehicle 2',
        this.assetTypes[1] || 'Trailer',
        this.costCentres[1] || 'Sales',
        `${this.assetGroups[0] || 'Fleet'},${this.assetGroups[1] || 'VIP'}`,
        'Second asset',
      ]);

      // Add data validations for dropdowns
      if (this.deviceTypes.length > 0) {
        worksheet
          .getColumn(3)
          .eachCell({ includeEmpty: true }, (cell, rowNumber) => {
            if (rowNumber > 1) {
              // Skip header row
              cell.dataValidation = {
                type: 'list',
                allowBlank: true,
                formulae: [`"${this.deviceTypes.join(',')}"`],
                showErrorMessage: true,
                errorStyle: 'warning',
                errorTitle: 'Invalid Value',
                error: 'Please select a valid Device Type from the dropdown',
              };
            }
          });
      }

      if (this.assetTypes.length > 0) {
        worksheet
          .getColumn(11)
          .eachCell({ includeEmpty: true }, (cell, rowNumber) => {
            if (rowNumber > 1) {
              cell.dataValidation = {
                type: 'list',
                allowBlank: true,
                formulae: [`"${this.assetTypes.join(',')}"`],
                showErrorMessage: true,
                errorStyle: 'warning',
                errorTitle: 'Invalid Value',
                error: 'Please select a valid Asset Type from the dropdown',
              };
            }
          });
      }

      if (this.costCentres.length > 0) {
        worksheet
          .getColumn(12)
          .eachCell({ includeEmpty: true }, (cell, rowNumber) => {
            if (rowNumber > 1) {
              cell.dataValidation = {
                type: 'list',
                allowBlank: true,
                formulae: [`"${this.costCentres.join(',')}"`],
                showErrorMessage: true,
                errorStyle: 'warning',
                errorTitle: 'Invalid Value',
                error: 'Please select a valid Cost Centre from the dropdown',
              };
            }
          });
      }

      if (this.assetGroups.length > 0) {
        worksheet
          .getColumn(13)
          .eachCell({ includeEmpty: true }, (cell, rowNumber) => {
            if (rowNumber > 1) {
              // Set data validation - simple dropdown, no hints in cells
              cell.dataValidation = {
                type: 'list',
                allowBlank: true,
                formulae: [`"${this.assetGroups.join(',')}"`],
                showErrorMessage: false, // Don't show error, allow custom values
              };
            }
          });
      }

      // Create Reference Data worksheet
      const refWorksheet = workbook.addWorksheet('Reference Data', {
        properties: {
          tabColor: {
            argb: this.hexToArgb(this.getLighterColor(this.primaryColor)),
          },
        },
      });

      const maxLength = Math.max(
        this.deviceTypes.length,
        this.assetTypes.length,
        this.costCentres.length,
        this.assetGroups.length,
      );

      // Add reference headers
      const refHeaderRow = refWorksheet.addRow([
        'Available Device Types',
        'Available Asset Types',
        'Available Cost Centres',
        'Available Asset Groups',
      ]);

      // Style reference headers
      const lighterColorArgb = this.hexToArgb(
        this.getLighterColor(this.primaryColor),
      );
      refHeaderRow.eachCell((cell) => {
        cell.font = { bold: true, color: { argb: 'FFFFFFFF' } };
        cell.fill = {
          type: 'pattern',
          pattern: 'solid',
          fgColor: { argb: lighterColorArgb },
        };
        cell.alignment = { horizontal: 'center', vertical: 'middle' };
        cell.border = {
          top: { style: 'thin' },
          left: { style: 'thin' },
          bottom: { style: 'thin' },
          right: { style: 'thin' },
        };
      });

      // Add reference data
      for (let i = 0; i < maxLength; i++) {
        refWorksheet.addRow([
          this.deviceTypes[i] || '',
          this.assetTypes[i] || '',
          this.costCentres[i] || '',
          this.assetGroups[i] || '',
        ]);
      }

      // Set column widths for reference sheet
      refWorksheet.columns = [
        { width: 25 },
        { width: 25 },
        { width: 25 },
        { width: 25 },
      ];

      // Generate Excel file and download
      const buffer = await workbook.xlsx.writeBuffer();
      const blob = new Blob([buffer], {
        type: 'application/vnd.openxmlformats-officedocument.spreadsheetml.sheet',
      });
      const url = window.URL.createObjectURL(blob);
      const link = document.createElement('a');
      link.href = url;
      link.download = fileName;

      try {
        link.click();
      } finally {
        window.URL.revokeObjectURL(url);
      }

      this.toastService.showSuccess(
        'Download Complete',
        'Template downloaded. Please save as CSV before uploading.',
      );
    } catch (error) {
      this.toastService.showError(
        'Template Generation Failed',
        'An error occurred while generating the template. Please try again.',
      );
      throw error;
    }
  }

  get hasErrors(): boolean {
    const result = this.importResult();
    return result ? result.errors.length > 0 : false;
  }

  get canUpload(): boolean {
    return !!this.selectedFile() && !this.isUploading();
  }

  /**
   * Convert hex color to ARGB format for ExcelJS (with alpha channel)
   * @param hex Hex color string (e.g., '#1976d2' or 'rgb(25, 118, 210)')
   * @returns ARGB string (e.g., 'FF1976D2')
   */
  private hexToArgb(hex: string): string {
    // Handle rgb/rgba format
    if (hex.startsWith('rgb')) {
      const match = hex.match(/\d+/g);
      if (match && match.length >= 3) {
        const rgb = match
          .slice(0, 3)
          .map((x) => parseInt(x).toString(16).padStart(2, '0'))
          .join('')
          .toUpperCase();
        return `FF${rgb}`; // FF for full opacity
      }
    }

    // Handle hex format
    hex = hex.replace('#', '');
    if (hex.length === 3) {
      hex = hex
        .split('')
        .map((x) => x + x)
        .join('');
    }
    return `FF${hex.toUpperCase()}`; // Add alpha channel
  }

  /**
   * Convert hex color to RGB format (without alpha)
   * @param hex Hex color string
   * @returns RGB string without # (e.g., '1976D2')
   */
  private hexToRgb(hex: string): string {
    return this.hexToArgb(hex).substring(2); // Remove FF alpha channel
  }

  /**
   * Get a lighter version of the color for the second tab
   * @param color Hex color string
   * @returns Lighter hex color string
   */
  private getLighterColor(color: string): string {
    // Convert to RGB
    const rgb = this.hexToRgb(color);
    const r = parseInt(rgb.substring(0, 2), 16);
    const g = parseInt(rgb.substring(2, 4), 16);
    const b = parseInt(rgb.substring(4, 6), 16);

    // Make it lighter by blending with white (increase by 40%)
    const lighter = (val: number) =>
      Math.min(255, Math.floor(val + (255 - val) * 0.4));

    const newR = lighter(r).toString(16).padStart(2, '0');
    const newG = lighter(g).toString(16).padStart(2, '0');
    const newB = lighter(b).toString(16).padStart(2, '0');

    return `#${newR}${newG}${newB}`;
  }
}
