import { computed, inject, Injectable, signal } from '@angular/core';

import { CustomFieldsDataAdapter } from '../services/adapters';
import {
  DataGridParams,
  FilterCriteria,
  FilterValue,
  GridColumn,
  GridConfiguration,
} from '../../../shared/models';
import { isDefined } from '../../../shared/utils';
import { CustomFieldResponse } from '../models';
import { DynamicDialogRef, DialogService } from 'primeng/dynamicdialog';
import { TableLazyLoadEvent } from 'primeng/table';
import { DEFAULT_PAGE_SIZE } from '../../../core/models';
import { CustomFieldCreateWizardComponent } from '../components/custom-fields/custom-field-create-wizard/custom-field-create-wizard.component';
import { UtilityService } from '../../../core/services/utility.service';
import { GridBaseService } from '../../../shared/services/grid-base.service';

@Injectable({
  providedIn: 'root',
})
export class CustomFieldsGridConfig
  implements GridConfiguration<CustomFieldResponse>
{
  private readonly customFieldsService = inject(CustomFieldsDataAdapter);
  private readonly assetTypes = this.customFieldsService.assetTypes;
  private readonly types = this.customFieldsService.types;
  private dialogRef: DynamicDialogRef | null = null;
  private readonly dialogService = inject(DialogService);
  private readonly utilityService = inject(UtilityService);
  private readonly gridBaseService = inject(GridBaseService);

  private params: Record<string, string> = {};
  private readonly filterState = signal<Record<string, FilterCriteria>>({});
  private readonly rqlFilter = computed(() =>
    this.utilityService.constructRqlFilter(this.filterState()),
  );
  columnsSignal = computed(() => this.createColumns());

  entityType = 'custom-fields';
  columns: GridColumn<CustomFieldResponse>[] = [];

  dataService = this.customFieldsService;
  ownerId = '';

  private createColumns(): GridColumn<CustomFieldResponse>[] {
    return [
      {
        field: 'name',
        header: 'Name',
        filter: true,
        filterType: 'text',
        filterMatchMode: 'contains',
      },
      {
        field: 'entityType',
        header: 'Applies To',
        valueGetter: (rowData: CustomFieldResponse): string =>
          isDefined(rowData?._entityType?.name) ? rowData._entityType.name : '',
        filter: true,
        filterType: 'multiselect',
        filterMatchMode: 'in',
        filterConfig: {
          type: 'multiselect',
          options: this.assetTypes().map((type) => ({
            label: type.name ?? '',
            value: type.id ?? '',
          })),
        },
      },
      {
        field: 'type',
        header: 'Type',
        valueGetter: (rowData: CustomFieldResponse) =>
          isDefined(rowData?.type?.name) ? rowData.type.name : '',
        filter: true,
        filterType: 'multiselect',
        filterMatchMode: 'in',
        filterConfig: {
          type: 'multiselect',
          options: this.types().map((type) => ({
            label: type.name ?? '',
            value: type.id ?? '',
          })),
        },
      },
    ];
  }

  clearFilters(): void {
    this.filterState.set({});
  }

  initialize(params: Record<string, string>): void {
    this.params = params;
    if (isDefined(this.params['clientId'])) {
      this.ownerId = String(this.params['clientId']);
    } else if (isDefined(this.params['vendorId'])) {
      this.ownerId = String(this.params['vendorId']);
    }
  }

  getTitle(): string {
    return 'Custom Fields';
  }

  getRequestParams(event?: TableLazyLoadEvent): DataGridParams {
    // Add sorting support
    let sortField = 'name:asc'; // Default

    if (isDefined(event?.sortField)) {
      const sortOrder = event.sortOrder === 1 ? 'asc' : 'desc';
      sortField = `${event.sortField}:${sortOrder}`;
    }

    return {
      owner: this.ownerId,
      offset: isDefined(event?.first) ? event.first : 0,
      recurse: false,
      limit: isDefined(event?.rows) ? event.rows : DEFAULT_PAGE_SIZE,
      sortField: sortField,
      filters: this.rqlFilter(),
    };
  }

  onFilterChange(field: string, value: FilterValue): void {
    const column = this.columns.find((col) => col.field === field);

    if (Array.isArray(value)) {
      const newFilters = this.gridBaseService.handleFilters(
        this.filterState(),
        field,
        value,
        undefined,
        column?.filterMatchMode,
      );

      this.filterState.set(newFilters);
      return;
    }

    // For non-array values, use the grid base service
    const newFilters = this.gridBaseService.handleFilters(
      this.filterState(),
      field,
      value,
      undefined,
      column?.filterMatchMode,
    );

    this.filterState.set(newFilters);
  }

  onAdd(): void {
    const clientId = this.ownerId;

    this.openCustomFieldCreationDialog(clientId).subscribe(
      (createdCustomField) => {
        if (isDefined(createdCustomField)) {
          // Refresh your customField list or take any other necessary actions
          // this.refreshCustomFields();
        }
      },
    );
  }

  openCustomFieldCreationDialog(ownerId: string) {
    this.dialogRef = this.dialogService.open(CustomFieldCreateWizardComponent, {
      header: 'Custom Field Wizard',
      contentStyle: { overflow: 'auto' },
      baseZIndex: 10000,
      data: { ownerId: ownerId },
      position: 'top',
      modal: true,
    });

    return (this.dialogRef as DynamicDialogRef).onClose;
  }
}
