import { inject, Injectable } from '@angular/core';
import { TreeNode } from 'primeng/api';

import { AdminSearchService } from '../admin-search.service';
import { AdminTreeClientService } from './admin-tree-client.service';
import { AdminTreeNodeService } from './admin-tree-node.service';
import { ClientResponse } from '../../../../api';
import { AdminScrollManagementService } from './admin-scroll-management.service';

// Custom error class
class ClientLoadFailureError extends Error {
  constructor(message: string) {
    super(message);
    this.name = 'ClientLoadFailureError';
  }
}

@Injectable({
  providedIn: 'root',
})
export class AdminTreeClientLoaderService {
  private readonly adminSearchService = inject(AdminSearchService);
  private readonly clientService = inject(AdminTreeClientService);
  private readonly treeNodeService = inject(AdminTreeNodeService);
  private readonly scrollManagement = inject(AdminScrollManagementService);

  // Dependencies injected from event handler
  private hierarchyTreeGetter?: () => TreeNode[];

  /**
   * Initializes the service with required dependencies
   */
  initialize(dependencies: { hierarchyTreeGetter: () => TreeNode[] }): void {
    this.hierarchyTreeGetter = dependencies.hierarchyTreeGetter;
  }

  /**
   * Loads client data for tree selection
   */
  async loadClientForTree(clientId: string): Promise<void> {
    await this.fetchClientForTreeSelection(clientId);
  }

  /**
   * Waits for node to appear in tree and attempts selection
   */
  async waitAndSelect(
    clientId: string,
  ): Promise<{ found: boolean; node?: TreeNode }> {
    const found = await this.waitForNodeInTree(clientId);

    if (found) {
      const tree = this.hierarchyTreeGetter?.() ?? [];
      const node = this.treeNodeService.findNodeByKey(tree, clientId);
      return { found: true, node: node ?? undefined };
    }

    return { found: false };
  }

  /**
   * Clears scroll requests for a specific client
   */
  clearScrollRequests(clientId: string): void {
    const currentScrollClient = this.scrollManagement.scrollToNodeKey();
    if (currentScrollClient === clientId) {
      this.scrollManagement.scrollToNodeKey.set(null);
    }
  }

  /**
   * Loads a client using the existing AdminSearchService methods
   */
  private async fetchClientForTreeSelection(clientId: string): Promise<void> {
    try {
      const client = await this.getClientWithOwnerInfo(clientId);

      if (client) {
        const result =
          await this.adminSearchService.prepareClientForTreeSelectionWithOwner(
            client,
          );
        if (result === null) {
          throw new ClientLoadFailureError(
            `Failed to prepare client ${clientId} for tree selection`,
          );
        }
      } else {
        const success =
          await this.adminSearchService.searchAndLoadClient(clientId);
        if (!success) {
          throw new ClientLoadFailureError(
            `Failed to search and load client ${clientId}`,
          );
        }
      }
    } catch (error) {
      if (error instanceof ClientLoadFailureError) {
        throw error;
      }
      console.error(
        `Failed to load client ${clientId} using existing methods:`,
        error,
      );
      throw new ClientLoadFailureError(
        `Failed to load client ${clientId} using existing methods`,
      );
    }
  }

  /**
   * Gets client information with owner details
   */
  private async getClientWithOwnerInfo(
    clientId: string,
  ): Promise<Partial<ClientResponse> | null> {
    try {
      return await this.clientService.fetchClient(clientId);
    } catch {
      return null;
    }
  }

  /**
   * Waits for a node to appear in the tree after data loading
   */
  async waitForNodeInTree(nodeId: string): Promise<boolean> {
    const maxAttempts = 30;
    const delayMs = 100;

    for (let i = 0; i < maxAttempts; i++) {
      const tree = this.hierarchyTreeGetter?.() ?? [];
      const node = this.treeNodeService.findNodeByKey(tree, nodeId);

      if (node) {
        return true;
      }

      await new Promise((resolve) => setTimeout(resolve, delayMs));
    }

    return false;
  }
}
