import { CommonModule } from '@angular/common';
import {
  ChangeDetectorRef,
  Component,
  DestroyRef,
  OnInit,
  TemplateRef,
  effect,
  inject,
  signal,
  viewChild,
} from '@angular/core';
import { takeUntilDestroyed } from '@angular/core/rxjs-interop';

import { ButtonModule } from 'primeng/button';
import { DynamicDialogRef, DynamicDialogConfig } from 'primeng/dynamicdialog';

import {
  AccountsService,
  DistributorCreateRequest,
  DistributorResponse,
} from '../../../../../api';
import { WizardComponent } from '../../../../../shared/components/wizard/wizard.component';
import { isDefined, isNullOrUndefined } from '../../../../../shared/utils';
import { BroadcastEventMessageService } from '../../../services/broadcast-event-messages.service';
import { DistributorDetailsComponent } from '../../distributors/distributor-details/distributor-details/distributor-details.component';
import { Step } from '../../../../../shared/models';
import { finalize } from 'rxjs';

@Component({
  selector: 'app-super-admin-distributors-create-wizard',
  imports: [
    CommonModule,
    WizardComponent,
    ButtonModule,
    DistributorDetailsComponent,
  ],
  templateUrl: './super-admin-distributors-create-wizard.component.html',
  styleUrl: './super-admin-distributors-create-wizard.component.scss',
})
export class SuperAdminDistributorsCreateWizardComponent implements OnInit {
  distributorDetailsStep = viewChild<
    TemplateRef<{
      $implicit: Step;
    }>
  >('distributorDetailsStep');
  confirmationTemplate = viewChild<TemplateRef<{ $implicit: Step }>>(
    'confirmationTemplate',
  );

  // References to form components
  distributorDetailsForm = viewChild<DistributorDetailsComponent>(
    'distributorDetailsForm',
  );

  private readonly accountsService = inject(AccountsService);
  private readonly broadcastService = inject(BroadcastEventMessageService);
  protected readonly destroyRef = inject(DestroyRef);
  private readonly dialogRef = inject(DynamicDialogRef);
  private readonly dialogConfig = inject(DynamicDialogConfig);
  private readonly cdr = inject(ChangeDetectorRef);

  // Distributor model to collect data across steps
  newDistributor = signal<Partial<DistributorCreateRequest>>({});

  // Form validity state
  formIsValid = signal<boolean>(false);

  steps: Step[] = [];
  ownerId: string | null = null;
  isSubmitting = signal<boolean>(false);

  constructor() {
    effect(() => {
      const distributorDetailsStep = this.distributorDetailsStep();
      const confirmationTemplate = this.confirmationTemplate();

      if (
        isNullOrUndefined(distributorDetailsStep) ||
        isNullOrUndefined(confirmationTemplate)
      ) {
        return;
      }
      this.steps = [
        {
          value: 1,
          header: 'Distributor Details',
          content: distributorDetailsStep,
          complete: false,
          validate: () => this.distributorDetailsForm()?.validate() ?? false,
        },
        {
          value: 2,
          header: 'Confirmation',
          content: confirmationTemplate,
          complete: true,
        },
      ];
    });
  }

  ngOnInit(): void {
    if (isDefined(this.dialogConfig.data)) {
      this.ownerId = this.dialogConfig.data.ownerId ?? null;

      // Initialize distributor with ownerId
      this.newDistributor.update((current) => ({
        ...current,
        ownerId: this.ownerId ?? '',
      }));
    }
  }

  openWizard() {
    this.newDistributor.set({});
  }

  // Form validity handlers
  onDetailsFormValidityChange(isValid: boolean) {
    this.steps[0].disableNext = !isValid;
    this.formIsValid.set(isValid);
  }

  // Update the distributor state with form data
  updateDistributorDetails(formData: Record<string, unknown>) {
    this.newDistributor.update((current) => {
      const updated = { ...current };

      Object.keys(formData).forEach((key) => {
        if (key !== 'ownerId') {
          updated[key] = formData[key];
        }
      });

      if (isNullOrUndefined(updated.ownerId)) {
        updated.ownerId = this.ownerId ?? '';
      }

      return updated;
    });
  }

  onWizardComplete(): void {
    // Process the final data from all steps
    const distributorData = this.newDistributor() as DistributorCreateRequest;
    this.isSubmitting.set(true);

    this.accountsService
      .createDistributor(distributorData)
      .pipe(
        takeUntilDestroyed(this.destroyRef),
        finalize(() => this.isSubmitting.set(false)),
      )
      .subscribe({
        next: (response: DistributorResponse) => {
          this.broadcastService.broadcast('entity-updated', {
            entityType: 'distributor',
            operation: 'create',
            entityId: response.id,
          });
          this.broadcastService.broadcast('tree-refresh-required', {
            entityType: 'distributor',
            operation: 'create',
            entityId: response.id,
          });
          this.dialogRef.close(response);
        },
        error: (error: Error) => {
          // Handle error, could show message and keep dialog open
          console.error('Error creating distributor:', error);
        },
      });
  }

  // Update cancel to close dialog with no result
  onWizardCancel(): void {
    this.newDistributor.set({});
    this.dialogRef.close();
  }
}
