import { DestroyRef, inject, Injectable, WritableSignal } from '@angular/core';
import {
  AccountsService,
  ClientListItem,
  ClientResponse,
  VendorListItem,
} from '../../../../api';
import { AdminDataService } from '../admin-data.service';
import { AdminNavigationService } from '../navigation/admin-navigation.service';
import { AdminScrollManagementService } from './admin-scroll-management.service';
import { AdminSearchService } from '../admin-search.service';
import { AdminStateService } from '../state/admin-state.service';
import { AdminTreeClientLoaderService } from './admin-tree-client-loader.service';
import { BroadcastEventMessageService } from '../broadcast-event-messages.service';
import { EntityDetailsService } from '../entity/entity-details.service';
import { firstValueFrom } from 'rxjs';
import { takeUntilDestroyed } from '@angular/core/rxjs-interop';
import { isDefined } from '../../../../shared/utils';
import { EventSource } from '../../models/event-source.enum';
import { MessageService, TreeNode } from 'primeng/api';
import { AdminTreeSelectionService } from './admin-tree-selection.service';
import { AdminTreeService } from './admin-tree.service';
import { TreeSearchResult } from '../../../../shared/models';
import { ADMIN_TREE } from '../../models';
import { AdminTreeClientService } from './admin-tree-client.service';

// Constants for timing
const TIMING = {
  NODE_SELECTION_DELAY: 100,
} as const;

// Interface for entity update messages
interface EntityUpdateData {
  entityType: string;
  operation: string;
  entityId: string;
}

@Injectable({
  providedIn: 'root',
})
export class AdminTreeEntitySyncService {
  private readonly broadcastService = inject(BroadcastEventMessageService);
  private readonly adminState = inject(AdminStateService);
  private readonly adminDataService = inject(AdminDataService);
  private readonly adminSearchService = inject(AdminSearchService);
  private readonly detailsService = inject(EntityDetailsService);
  private readonly accountsService = inject(AccountsService);
  private readonly navigationService = inject(AdminNavigationService);
  private readonly scrollManagement = inject(AdminScrollManagementService);
  private readonly clientLoader = inject(AdminTreeClientLoaderService);
  private readonly destroyRef = inject(DestroyRef);
  private readonly selectionService = inject(AdminTreeSelectionService);
  private readonly adminTreeService = inject(AdminTreeService);
  private readonly clientService = inject(AdminTreeClientService);
  private readonly messageService = inject(MessageService);

  // Callbacks to access tree data (set during initialization)
  private hierarchyTreeGetter?: () => TreeNode[];
  private findNodeByKeyRecursiveCallback?: (
    nodes: TreeNode[],
    key: string,
  ) => TreeNode | null;

  // Callbacks for parent component actions (set during initialization)
  private treeCollapsed!: () => boolean;
  private toggleTreePanel!: WritableSignal<boolean>;
  private setEventSource!: (source: EventSource) => void;
  private resetEventSource!: () => void;
  private selectedTreeNode!: WritableSignal<TreeNode | null>;
  private handleError!: (error: unknown, message: string) => void;

  /**
   * Initialize the service with required dependencies
   */
  initialize(dependencies: {
    getHierarchyTree: () => TreeNode[];
    findNodeByKeyRecursive: (nodes: TreeNode[], key: string) => TreeNode | null;
    treeCollapsed: () => boolean;
    toggleTreePanel: WritableSignal<boolean>;
    setEventSource: (source: EventSource) => void;
    resetEventSource: () => void;
    selectedTreeNode: WritableSignal<TreeNode | null>;
    handleError: (error: unknown, message: string) => void;
  }): void {
    this.hierarchyTreeGetter = dependencies.getHierarchyTree;
    this.findNodeByKeyRecursiveCallback = dependencies.findNodeByKeyRecursive;
    this.treeCollapsed = dependencies.treeCollapsed;
    this.toggleTreePanel = dependencies.toggleTreePanel;
    this.setEventSource = dependencies.setEventSource;
    this.resetEventSource = dependencies.resetEventSource;
    this.selectedTreeNode = dependencies.selectedTreeNode;
    this.handleError = dependencies.handleError;

    // Setup subscriptions after dependencies are set
    this.setupBroadcastSubscriptions();
  }

  /**
   * Gets client information with owner details from ClientService
   */
  private async getClientWithOwnerInfo(
    clientId: string,
  ): Promise<Partial<ClientResponse> | null> {
    try {
      return await this.clientService.fetchClient(clientId);
    } catch {
      return null;
    }
  }

  /**
   * Common method to navigate to a search result
   */
  private async navigateToSearchResult(
    result: TreeSearchResult,
  ): Promise<void> {
    const { type, id } = result;

    if (
      type === ADMIN_TREE.NODE_TYPES.CLIENT ||
      type === ADMIN_TREE.NODE_TYPES.VENDOR ||
      type === ADMIN_TREE.NODE_TYPES.DISTRIBUTOR
    ) {
      await this.navigationService.navigateToNode(type, id);
    }
  }

  /**
   * Tries to select a node by ID in the current tree
   */
  private trySelectPendingNode(nodeId: string): boolean {
    const finalTree = this.hierarchyTreeGetter?.() ?? [];
    const nodeToSelect = this.findNodeByKeyRecursiveCallback?.(
      finalTree,
      nodeId,
    );

    if (nodeToSelect) {
      this.selectedTreeNode.set(nodeToSelect);
      this.selectionService.selectNode(nodeToSelect);
      return true;
    }
    return false;
  }

  /**
   * Setup broadcast event subscriptions
   */
  private setupBroadcastSubscriptions(): void {
    // Handle 'view-entity-in-tree' events
    this.broadcastService
      .on('view-entity-in-tree')
      .pipe(takeUntilDestroyed(this.destroyRef))
      .subscribe((data) => {
        const item = data?.entity;
        if (isDefined(item?.id) && item.entityType === 'client') {
          this.setEventSource(EventSource.BROADCAST);
          this.handleClientInTree(
            item as ClientListItem,
            item.owner?.id as string,
            item['group'] as string | undefined,
          ).catch((err) => this.handleError(err, 'Error showing item in tree'));
        }

        if (isDefined(item?.id) && item.entityType === 'vendor') {
          // Set event source to BROADCAST to allow auto-scrolling
          this.setEventSource(EventSource.BROADCAST);
          this.handleVendorInTree(item as VendorListItem).catch((err) =>
            this.handleError(err, 'Error showing vendor in tree'),
          );
        }
      });

    // Handle entity updates to refresh tree state
    this.broadcastService
      .on('entity-updated')
      .pipe(takeUntilDestroyed(this.destroyRef))
      .subscribe((data: unknown) => {
        if (
          isDefined(data) &&
          typeof data === 'object' &&
          data !== null &&
          'entityType' in data &&
          'operation' in data &&
          'entityId' in data
        ) {
          const updateData = data as EntityUpdateData;

          if (
            updateData.entityType === 'client' &&
            updateData.operation === 'create' &&
            updateData.entityId
          ) {
            // Set event source to SYSTEM to allow auto-scrolling for created clients
            this.setEventSource(EventSource.SYSTEM);
            this.handleClientCreated(updateData.entityId).catch((err) =>
              this.handleError(
                err,
                'Error refreshing tree after client creation',
              ),
            );
          }
        }
      });
  }

  /**
   * Handles the 'view-entity-in-tree' event for a client
   */
  private async handleClientInTree(
    client: ClientListItem,
    vendorId: string,
    groupId?: string,
  ): Promise<void> {
    try {
      this.detailsService.closeDetailsPanel();

      // Only toggle if tree is currently collapsed (to open it)
      if (this.treeCollapsed()) {
        this.toggleTreePanel.set(true);
      }

      // CRITICAL: Ensure correct distributor is set BEFORE loading clients
      // Check if the client's distributor matches the current distributor
      await this.setDistributorContextForClient(client.id);

      // Use pagination-aware method that loads until client is found
      const result = await this.adminSearchService.handleClientInTree(
        client,
        vendorId,
        groupId,
      );

      await new Promise((resolve) => setTimeout(resolve, 200));

      // Wait for the node to actually appear in the tree (polls with retry logic)
      const nodeFound = await this.clientLoader.waitForNodeInTree(client.id);

      if (nodeFound) {
        // Find and expand the vendor parent node BEFORE navigation
        // This ensures the client is visible when the scroll effect fires
        const tree = this.hierarchyTreeGetter?.() ?? [];
        const vendorNode = this.findNodeByKeyRecursiveCallback?.(
          tree,
          vendorId,
        );

        if (vendorNode && vendorNode.expanded !== true) {
          await this.adminDataService.expandNode(vendorNode);
          // Wait for tree to update after expansion
          await new Promise((resolve) => setTimeout(resolve, 150));
        }

        // NOW navigate - after vendor is expanded and client is visible
        await this.navigateToSearchResult(result);

        // Node is in tree and vendor is expanded, select and scroll to it
        const selectionSuccess = this.trySelectPendingNode(client.id);
        if (selectionSuccess) {
          this.scrollManagement.scrollToClient(client.id);
        }
      }
    } catch (err) {
      this.handleError(err, 'Error showing item in tree');
    } finally {
      this.resetEventSource();
    }
  }

  /**
   * Handles the 'view-entity-in-tree' event for a vendor
   */
  private async handleVendorInTree(vendor: VendorListItem): Promise<void> {
    try {
      this.detailsService.closeDetailsPanel();

      if (this.treeCollapsed()) {
        this.toggleTreePanel.set(true);
      }

      const result = await this.adminSearchService.handleVendorInTree(vendor);
      await new Promise((resolve) =>
        setTimeout(resolve, TIMING.NODE_SELECTION_DELAY),
      );
      await this.navigateToSearchResult(result);
    } catch (err) {
      this.handleError(err, 'Error showing vendor in tree');
    } finally {
      this.resetEventSource();
    }
  }

  /**
   * Ensures the correct distributor is set in admin state before loading clients.
   * This prevents loading clients from the wrong distributor context.
   *
   * Strategy:
   * 1. Check if client is already in loaded tree data (has hierarchy info)
   * 2. If not, fetch client from API to get full details including distributor
   * 3. Set the correct distributor context before pagination begins
   *
   * @param clientId The ID of the client we're trying to load
   */
  private async setDistributorContextForClient(
    clientId: string,
  ): Promise<void> {
    // First, check if client is already in loaded data
    const hierarchy = this.adminSearchService.findClientHierarchy(clientId);

    if (hierarchy) {
      // Client is already in loaded data, we know its distributor
      await this.switchDistributor(hierarchy.distributorId);
      return;
    }

    // Client not in loaded data yet - fetch from API to determine distributor
    try {
      const clientDetails = await this.getClientWithOwnerInfo(clientId);

      if (clientDetails && isDefined(clientDetails['distributor']?.['id'])) {
        // Client response includes distributor - use it directly
        await this.switchDistributor(
          clientDetails['distributor']['id'] as string,
        );
        return;
      }

      // Fallback: Client might not have distributor in response, but has vendor
      // Fetch vendor to get its distributor
      if (isDefined(clientDetails?.owner?.id)) {
        const vendorId = clientDetails.owner.id;

        // Fetch vendor from API to get its distributor (owner field)
        try {
          const vendorDetails = await firstValueFrom(
            this.accountsService.getVendor(vendorId),
          );

          if (isDefined(vendorDetails?.owner?.id)) {
            await this.switchDistributor(vendorDetails.owner.id);
            return;
          }
        } catch (error) {
          console.warn(
            `Failed to fetch vendor ${vendorId} to determine distributor:`,
            error,
          );
        }
      }
    } catch (error) {
      console.warn(
        `Could not determine distributor for client ${clientId}:`,
        error,
      );
    }
  }

  /**
   * Sets the distributor in admin state if it's different from current
   */
  private async switchDistributor(distributorId: string): Promise<void> {
    const currentDistributorId = this.adminState.getActiveContextId();

    if (currentDistributorId !== distributorId) {
      try {
        // Fetch the distributor details
        const distributor = await firstValueFrom(
          this.accountsService.getDistributor(distributorId),
        );

        // Update the admin state with the correct distributor
        this.adminState.setVendorDistributor(distributor);
      } catch (error) {
        console.error(`Failed to fetch distributor ${distributorId}:`, error);
        throw error;
      }
    }
  }

  /**
   * Handles client creation event to refresh the tree state.
   * This method is called when a client is created, and it refreshes the relevant parts of the tree.
   */
  async handleClientCreated(clientId: string): Promise<ClientResponse | null> {
    try {
      // Load the new client to get its details
      const client = await this.adminTreeService.tryLoadClient(clientId);
      if (!client) {
        console.warn('Could not load newly created client:', clientId);
        return null;
      }

      const vendorId = client.owner?.id;
      if (!vendorId) {
        console.warn('Client does not have a vendor ID:', clientId);
        return null;
      }

      // Note: Legacy method that needs refactoring for distributor-scoped architecture
      // Currently disabled to avoid conflicts with new distributor-scoped state
      return client;
    } catch (_error) {
      this.messageService.add({
        severity: 'error',
        summary: 'Error',
        detail: 'Failed to refresh vendor data after client creation',
      });
      // Re-throw to maintain error propagation
      throw _error;
    }
  }

  /**
   * Manually trigger view client in tree (for programmatic use)
   */
  async viewClientInTree(
    client: ClientListItem,
    vendorId: string,
    groupId?: string,
  ): Promise<void> {
    this.setEventSource(EventSource.BROADCAST);
    await this.handleClientInTree(client, vendorId, groupId);
  }

  /**
   * Manually trigger view vendor in tree
   */
  async viewVendorInTree(vendor: VendorListItem): Promise<void> {
    this.setEventSource(EventSource.BROADCAST);
    await this.handleVendorInTree(vendor);
  }
}
