import { effect, inject, Injectable, signal } from '@angular/core';
import { TreeNode } from 'primeng/api';

import { AdminStateService } from '../state/admin-state.service';
import { AdminContainerStateService } from '../state/admin-container-state.service';
import { AdminTreeManagementService } from './admin-tree-management.service';
import { ADMIN_TREE } from '../../models/tree-node-data.interface';
import { AdminTreeNodeService } from './admin-tree-node.service';
import { AdminTreeSelectionService } from './admin-tree-selection.service';
import { isDefined } from '../../../../shared/utils';

@Injectable({
  providedIn: 'root',
})
export class AdminScrollManagementService {
  private readonly adminState = inject(AdminStateService);
  private readonly containerState = inject(AdminContainerStateService);
  private readonly treeManagementService = inject(AdminTreeManagementService);
  private readonly treeNodeService = inject(AdminTreeNodeService);
  private readonly selectionService = inject(AdminTreeSelectionService);

  // Track if the service has been initialized
  private initialized = false;

  // Dependencies for scroll effects
  private selectedTreeNode!: () => TreeNode | null;
  private setSelectedTreeNode!: (node: TreeNode | null) => void;
  readonly scrollToNodeKey = signal<string | null>(null);
  readonly suppressScrollToClient = signal<boolean>(false);
  readonly scrollToCenterNodeKey = signal<string | null>(null);

  /**
   * Initializes the scroll management service with required dependencies
   */
  initialize(dependencies: {
    selectedTreeNode: () => TreeNode | null;
    setSelectedTreeNode: (node: TreeNode | null) => void;
  }): void {
    if (this.initialized) {
      return;
    }

    this.selectedTreeNode = dependencies.selectedTreeNode;
    this.setSelectedTreeNode = dependencies.setSelectedTreeNode;

    this.setupScrollEffects();
    this.initialized = true;
  }

  /**
   * Sets up the reactive effects for scroll management
   */
  private setupScrollEffects(): void {
    // Effect to sync tree selection and scroll when adminState.selectedClient() changes from other sources
    effect(() => {
      const currentSelectedClient = this.adminState.selectedClient();
      const currentTreeNode = this.selectedTreeNode();
      const suppressScroll = this.suppressScrollToClient();

      // This effect syncs tree selection if adminState.selectedClient() changes from other sources.
      // Route-driven selection is handled elsewhere.
      if (
        isDefined(currentSelectedClient) &&
        (currentTreeNode?.key !== currentSelectedClient.id ||
          currentTreeNode?.data?.type !== ADMIN_TREE.NODE_TYPES.CLIENT)
      ) {
        const treeNodes = this.treeManagementService.hierarchyTree();
        if (treeNodes.length > 0) {
          const nodeToSelect = this.treeNodeService.findNodeByKey(
            treeNodes,
            currentSelectedClient.id,
            ADMIN_TREE.NODE_TYPES.CLIENT,
          );

          if (nodeToSelect) {
            // Update tree node selection
            this.setSelectedTreeNode(nodeToSelect);
            // Update global state
            this.selectionService.selectNode(nodeToSelect);
            // Only scroll if not suppressed (e.g., during load more operations)
            if (!suppressScroll) {
              this.scrollToClient(currentSelectedClient.id);
            }
          }
        }
      }
    });

    // Effect to handle initial scroll when tree first loads with data
    effect(() => {
      const treeNodes = this.treeManagementService.hierarchyTree();
      const selectedClient = this.adminState.selectedClient();
      const currentTreeNode = this.selectedTreeNode();
      const suppressScroll = this.suppressScrollToClient();

      // Only trigger on initial load when:
      // 1. Tree has nodes (just loaded)
      // 2. We have a selected client
      // 3. Tree node matches the selected client
      // 4. Tree is not collapsed (visible)
      // 5. Scrolling is not suppressed (e.g., during load more operations)
      // 6. Client actually exists in the tree data
      if (
        treeNodes.length > 0 &&
        isDefined(selectedClient) &&
        currentTreeNode?.key === selectedClient.id &&
        !this.containerState.treeCollapsed() &&
        !suppressScroll
      ) {
        // Verify the client exists in the tree before scrolling
        const nodeExists = this.treeNodeService.findNodeByKey(
          treeNodes,
          selectedClient.id,
          ADMIN_TREE.NODE_TYPES.CLIENT,
        );

        if (nodeExists) {
          // Small delay to ensure DOM is ready
          setTimeout(() => {
            // Double-check suppression before scrolling
            if (!this.suppressScrollToClient()) {
              this.scrollToClient(selectedClient.id);
            }
          }, 200);
        }
      }
    });
  }

  /**
   * Scrolls the tree to center on the specified client node
   * @param clientId The ID of the client to scroll to
   */
  scrollToClient(clientId: string): void {
    // Check if scrolling is suppressed (e.g., during user interactions)
    if (this.suppressScrollToClient()) {
      return;
    }

    // Prevent duplicate scroll requests for the same client
    if (this.scrollToNodeKey() === clientId) {
      return;
    }

    // Clear any previous scroll request
    this.scrollToNodeKey.set(null);

    // Use a small delay to ensure the tree has updated
    setTimeout(() => {
      // Double-check suppression before actually scrolling
      if (!this.suppressScrollToClient()) {
        this.scrollToNodeKey.set(clientId);
        // Clear the scroll signal after triggering
        setTimeout(() => {
          // Only clear if it's still the same client ID
          if (this.scrollToNodeKey() === clientId) {
            this.scrollToNodeKey.set(null);
          }
        }, 200);
      }
    }, 100);
  }

  /**
   * Scrolls to center a specific node (for expansion visibility)
   * This is different from scrollToClient which is for selection
   */
  scrollToCenterNode(nodeKey: string): void {
    // Set a different scroll target for centering vs selection
    this.scrollToCenterNodeKey.set(nodeKey);

    // Clear after a short delay to reset state
    setTimeout(() => this.scrollToCenterNodeKey.set(null), 200);
  }

  /**
   * Temporarily suppresses automatic scrolling to selected client during user interactions
   * @param durationMs Duration in milliseconds to suppress scrolling (default: 2000ms)
   */
  temporarilySuppressScrollToClient(durationMs = 2000): void {
    this.suppressScrollToClient.set(true);
    if (durationMs === 0) {
      return; // No need to set a timeout if duration is 0
    }
    // Clear suppression after the specified duration
    setTimeout(() => this.suppressScrollToClient.set(false), durationMs);
  }

  /**
   * Handles scroll behavior for search result navigation
   * @param entityId The ID of the entity to scroll to
   */
  handleSearchResultScroll(entityId: string): void {
    // Trigger tree scroll to center the selected entity
    this.scrollToClient(entityId);
  }

  /**
   * Handles scroll behavior for node selection with optional delay
   * @param nodeKey The key of the node to scroll to
   * @param delay Optional delay in milliseconds
   */
  handleNodeSelectionScroll(nodeKey: string, delay = 200): void {
    if (!this.containerState.treeCollapsed()) {
      setTimeout(() => {
        this.scrollToClient(nodeKey);
      }, delay);
    }
  }

  /**
   * Handles scroll behavior for client navigation with delay
   * @param clientId The ID of the client to scroll to
   * @param delay Delay in milliseconds (default: 300ms)
   */
  handleClientNavigationScroll(clientId: string, delay = 300): void {
    setTimeout(() => {
      this.scrollToClient(clientId);
    }, delay);
  }
}
