import {
  DestroyRef,
  Injectable,
  computed,
  inject,
  signal,
} from '@angular/core';

import { TableLazyLoadEvent } from 'primeng/table';

import {
  GridConfiguration,
  GridDataService,
  DataGridParams,
  GridColumn,
  FilterCriteria,
  FilterValue,
  DATE_FORMATS,
} from '../../../shared/models';
import { UtilityService } from '../../../core/services/utility.service';
import { GridBaseService } from '../../../shared/services/grid-base.service';
import { isDefined } from '../../../shared/utils';
import { SimCardsDataAdapter } from '../services/adapters';
import { SimCardListItem } from '../../../api';
import { DEFAULT_PAGE_SIZE } from '../../../core/models';
import { SimCardCreateWizardComponent } from '../components/sim-cards/sim-card-create-wizard/sim-card-create-wizard.component';
import { DialogService, DynamicDialogRef } from 'primeng/dynamicdialog';
import { MeasurementDateTimeService } from '../../../shared/services/measurement-units/measurement-date-time.service';

@Injectable({ providedIn: 'root' })
export class SimCardsGridConfig implements GridConfiguration<SimCardListItem> {
  private readonly simCardsService = inject(SimCardsDataAdapter);
  private readonly utilityService = inject(UtilityService);
  private readonly gridBaseService = inject(GridBaseService);
  private dialogRef: DynamicDialogRef | null = null;
  private readonly dialogService = inject(DialogService);
  private readonly measurementDateTimeService = inject(
    MeasurementDateTimeService,
  );

  private readonly defaultStateValues = [
    SimCardListItem.StateEnum.Active,
    SimCardListItem.StateEnum.Inactive,
    SimCardListItem.StateEnum.Suspended,
  ];
  private readonly filterState = signal<Record<string, FilterCriteria>>({
    state: { value: this.defaultStateValues, operator: 'in' },
  });
  private params: Record<string, string> = { clientId: '' };
  private readonly destroyRef = inject(DestroyRef);

  private readonly rqlFilter = computed(() =>
    this.utilityService.constructRqlFilter(this.filterState()),
  );
  private readonly fieldTransformations: Record<string, string> = {
    device: 'device.name',
  };

  private readonly stateLabels: Record<SimCardListItem.StateEnum, string> = {
    active: 'Active',
    deleted: 'Deleted',
    inactive: 'Inactive',
    suspended: 'Suspended',
  };

  entityType = 'sim-cards';
  columns: GridColumn<SimCardListItem>[] = [
    {
      field: 'name',
      header: 'Serial Number',
      filter: true,
      filterType: 'text',
      filterMatchMode: 'contains',
    },
    {
      field: 'networkName',
      header: 'Network',
      valueGetter: (rowData: SimCardListItem) => rowData.networkName ?? '',
      filter: true,
      filterType: 'text',
      filterMatchMode: 'contains',
    },
    {
      field: 'number',
      header: 'Number',
      filter: true,
      filterType: 'text',
      filterMatchMode: 'contains',
    },
    {
      field: 'state',
      header: 'State',
      valueGetter: (rowData: SimCardListItem) =>
        isDefined(rowData.state) ? this.stateLabels[rowData.state] : '',
      filter: true,
      filterType: 'multiselect',
      filterMatchMode: 'in',
      filterConfig: {
        type: 'multiselect',
        options: [
          { label: 'Active', value: SimCardListItem.StateEnum.Active },
          { label: 'Inactive', value: SimCardListItem.StateEnum.Inactive },
          { label: 'Suspended', value: SimCardListItem.StateEnum.Suspended },
          { label: 'Deleted', value: SimCardListItem.StateEnum.Deleted },
        ],
        defaultSelected: this.defaultStateValues,
      },
    },
    {
      field: 'description',
      header: 'Description',
      filter: true,
      filterType: 'text',
      filterMatchMode: 'contains',
    },
    {
      field: 'device',
      header: 'Device',
      valueGetter: (rowData: SimCardListItem) => rowData.device?.name ?? '',
      filter: true,
      filterType: 'text',
      filterMatchMode: 'contains',
    },
    {
      field: 'modifiedDate',
      header: 'Modified',
      filter: false,
      valueGetter: (rowData: SimCardListItem) =>
        isDefined(rowData['modifiedDate'])
          ? this.measurementDateTimeService.formatDateTime(
              this.measurementDateTimeService.createDateAssumingUtc(
                rowData['modifiedDate'],
              ),
              DATE_FORMATS.DATE_TIME_SECONDS,
            )
          : '',
    },
  ];

  dataService: GridDataService<SimCardListItem> = this.simCardsService;

  getFilters(): Record<string, FilterCriteria> {
    return this.filterState();
  }

  clearFilters(): void {
    this.filterState.set({
      state: { value: this.defaultStateValues, operator: 'in' },
    });
  }

  initialize(params: Record<string, string>): void {
    this.params = params;
  }

  getTitle(): string {
    return 'Sim Cards';
  }

  getRequestParams(event?: TableLazyLoadEvent): DataGridParams {
    // Add sorting support
    let sortField = 'name:asc'; // Default

    if (isDefined(event?.sortField)) {
      const sortOrder = event.sortOrder === 1 ? 'asc' : 'desc';
      sortField = `${event.sortField}:${sortOrder}`;
    }

    return {
      owner: this.params['clientId'],
      offset: isDefined(event?.first) ? event.first : 0,
      recurse: false,
      limit: isDefined(event?.rows) ? event.rows : DEFAULT_PAGE_SIZE,
      sortField: sortField,
      filters: this.rqlFilter(),
    };
  }

  onFilterChange(field: string, value: FilterValue): void {
    const actualField = this.fieldTransformations[field] || field;
    const column = this.columns.find((col) => col.field === field);

    if (Array.isArray(value)) {
      const newFilters = this.gridBaseService.handleFilters(
        this.filterState(),
        actualField,
        value,
        undefined,
        column?.filterMatchMode,
      );

      this.filterState.set(newFilters);
      return;
    }

    // For non-array values, use the grid base service
    const newFilters = this.gridBaseService.handleFilters(
      this.filterState(),
      actualField,
      value,
      undefined,
      column?.filterMatchMode,
    );

    this.filterState.set(newFilters);
  }

  onAdd(): void {
    const clientId = this.params['clientId'];

    this.openSimCardCreationDialog(clientId).subscribe((createdSimCard) => {
      if (isDefined(createdSimCard)) {
        // Refresh your simCard list or take any other necessary actions
        // this.refreshSimCards();
      }
    });
  }

  openSimCardCreationDialog(ownerId: string) {
    this.dialogRef = this.dialogService.open(SimCardCreateWizardComponent, {
      header: 'Sim Card Wizard',
      contentStyle: { overflow: 'auto' },
      baseZIndex: 10000,
      data: { ownerId: ownerId },
      position: 'top',
      modal: true,
    });

    return (this.dialogRef as DynamicDialogRef).onClose;
  }
}
