import { Component, inject, signal } from '@angular/core';
import { DynamicDialogConfig, DynamicDialogRef } from 'primeng/dynamicdialog';
import { AlertsDataAdapter } from '../../../services/adapters';
import { ExtendedAlertListItem } from '../../../models/alerts.model';
import { GridComponent } from '../../../../../shared/components/grid/grid.component';
import { AlertsGridConfig } from '../../../configs/alerts.grid.config';
import { DataGridParams } from '../../../../../shared/models';
import { CommonModule } from '@angular/common';
import {
  TableLazyLoadEvent,
  TableRowSelectEvent,
  TableRowUnSelectEvent,
} from 'primeng/table';
import { isDefined } from '../../../../../shared/utils';
import { ButtonModule } from 'primeng/button';
import { FormsModule } from '@angular/forms';
import { ChipModule } from 'primeng/chip';
import { InputTextModule } from 'primeng/inputtext';
import { Subject, debounceTime, distinctUntilChanged } from 'rxjs';
import { DEFAULT_PAGE_SIZE } from '../../../../../core/models';
import { IconField } from 'primeng/iconfield';
import { InputIcon } from 'primeng/inputicon';

@Component({
  selector: 'app-vendor-alert-picker',
  standalone: true,
  imports: [
    GridComponent,
    CommonModule,
    ButtonModule,
    FormsModule,
    ChipModule,
    InputTextModule,
    InputIcon,
    IconField,
  ],
  templateUrl: './vendor-alert-picker.component.html',
  styleUrls: ['./vendor-alert-picker.component.scss'],
})
export class VendorAlertPickerComponent {
  private readonly config = inject(DynamicDialogConfig);
  private readonly ref = inject(DynamicDialogRef);
  private readonly alertsDataAdapter = inject(AlertsDataAdapter);
  protected readonly alertsGridConfig = inject(AlertsGridConfig);

  protected vendorId: string = this.config.data.vendorId;
  protected selectedAlerts: ExtendedAlertListItem[] = [];
  protected vendorAlerts = signal<ExtendedAlertListItem[]>([]);
  protected isLoading = signal<boolean>(false);
  protected totalRecords = signal<number>(0);
  protected searchInput = signal<string>('');
  private readonly searchSubject = new Subject<string>();
  private lastLazyLoadEvent?: TableLazyLoadEvent;

  constructor() {
    this.searchSubject
      .pipe(debounceTime(300), distinctUntilChanged())
      .subscribe((query) => {
        this.searchInput.set(query);
        if (this.lastLazyLoadEvent) {
          // Reset to first page when search changes
          this.lastLazyLoadEvent = {
            ...this.lastLazyLoadEvent,
            first: 0,
          };
          this.loadVendorAlerts(this.lastLazyLoadEvent);
        }
      });
  }

  protected onSearch(query: string): void {
    this.searchSubject.next(query);
  }

  protected onLazyLoad(event: TableLazyLoadEvent): void {
    this.lastLazyLoadEvent = event;
    this.loadVendorAlerts(event);
  }

  private loadVendorAlerts(event: TableLazyLoadEvent): void {
    this.isLoading.set(true);

    // Construct RQL filter for search if query exists
    let filters = '';
    const query = this.searchInput().trim();
    if (query !== '') {
      filters = `name=like=*${query}*`;
    }

    const params: DataGridParams = {
      owner: this.vendorId,
      offset: isDefined(event.first) ? event.first : 0,
      limit: isDefined(event.rows) ? event.rows : DEFAULT_PAGE_SIZE,
      filters: filters,
    };

    this.alertsDataAdapter.getList(params).subscribe({
      next: (response) => {
        this.vendorAlerts.set(response.items);
        this.totalRecords.set(
          isDefined(response.count) ? response.count : response.items.length,
        );
        this.isLoading.set(false);
      },
      error: () => {
        this.isLoading.set(false);
      },
    });
  }

  protected onRowSelect(event: TableRowSelectEvent): void {
    const alert = event.data as ExtendedAlertListItem;
    if (isDefined(alert)) {
      this.selectedAlerts = [...this.selectedAlerts, alert];
    }
  }

  protected onRowUnselect(
    event: TableRowUnSelectEvent<ExtendedAlertListItem>,
  ): void {
    const alert = event.data as ExtendedAlertListItem | undefined;
    if (isDefined(alert)) {
      this.removeSelected(alert);
    }
  }

  protected removeSelected(alert: ExtendedAlertListItem): void {
    this.selectedAlerts = this.selectedAlerts.filter((a) => a.id !== alert.id);
  }

  protected onImport(): void {
    this.ref.close(this.selectedAlerts);
  }

  protected onCancel(): void {
    this.ref.close();
  }
}
