import { CommonModule } from '@angular/common';
import {
  ChangeDetectorRef,
  Component,
  DestroyRef,
  OnInit,
  TemplateRef,
  effect,
  inject,
  signal,
  viewChild,
} from '@angular/core';
import { takeUntilDestroyed } from '@angular/core/rxjs-interop';

import { ButtonModule } from 'primeng/button';
import { DynamicDialogRef, DynamicDialogConfig } from 'primeng/dynamicdialog';

import {
  DeviceTypeCreateRequest,
  DeviceTypeResponse,
  DeviceTypesService,
} from '../../../../../api';
import { WizardComponent } from '../../../../../shared/components/wizard/wizard.component';
import { isDefined, isNullOrUndefined } from '../../../../../shared/utils';
import { BroadcastEventMessageService } from '../../../services/broadcast-event-messages.service';
import { Step } from '../../../../../shared/models';
import { finalize } from 'rxjs';
import { DeviceTypesInformationSectionComponent } from '../device-types-information-section/device-types-information-section.component';

@Component({
  selector: 'app-device-types-create-wizard',
  imports: [
    CommonModule,
    WizardComponent,
    ButtonModule,
    DeviceTypesInformationSectionComponent,
  ],
  templateUrl: './device-types-create-wizard.component.html',
  styleUrl: './device-types-create-wizard.component.scss',
})
export class DeviceTypesCreateWizardComponent implements OnInit {
  deviceTypeDetailsStep = viewChild<
    TemplateRef<{
      $implicit: Step;
    }>
  >('deviceTypeDetailsStep');

  confirmationTemplate = viewChild<TemplateRef<{ $implicit: Step }>>(
    'confirmationTemplate',
  );

  // References to form components
  deviceTypeDetailsForm = viewChild<DeviceTypesInformationSectionComponent>(
    'deviceTypeDetailsForm',
  );

  private readonly deviceTypesService = inject(DeviceTypesService);
  private readonly broadcastService = inject(BroadcastEventMessageService);
  protected readonly destroyRef = inject(DestroyRef);
  private readonly dialogRef = inject(DynamicDialogRef);
  private readonly dialogConfig = inject(DynamicDialogConfig);
  private readonly cdr = inject(ChangeDetectorRef);

  // Device type model to collect data across steps
  newDeviceType = signal<Partial<DeviceTypeCreateRequest>>({});

  // Form validity state
  formIsValid = signal<boolean>(false);

  steps: Step[] = [];
  ownerId: string | null = null;
  isSubmitting = signal<boolean>(false);

  // Make Object available to template
  protected readonly Object = Object;

  constructor() {
    effect(() => {
      const deviceTypeDetailsStep = this.deviceTypeDetailsStep();
      const confirmationTemplate = this.confirmationTemplate();

      if (
        isNullOrUndefined(deviceTypeDetailsStep) ||
        isNullOrUndefined(confirmationTemplate)
      ) {
        return;
      }
      this.steps = [
        {
          value: 1,
          header: 'Device Type Details',
          content: deviceTypeDetailsStep,
          complete: false,
          validate: () => this.deviceTypeDetailsForm()?.validate() ?? false,
        },
        {
          value: 2,
          header: 'Confirmation',
          content: confirmationTemplate,
          complete: true,
        },
      ];
    });
  }

  ngOnInit(): void {
    if (isDefined(this.dialogConfig.data)) {
      this.ownerId = this.dialogConfig.data.ownerId ?? null;

      // Initialize device type with ownerId
      this.newDeviceType.update((current) => ({
        ...current,
        ownerId: this.ownerId ?? '',
      }));
    }

    // Initialize form validity state
    this.formIsValid.set(false);
  }

  openWizard() {
    this.newDeviceType.set({});
    // Reset form validity
    this.formIsValid.set(false);
    // Ensure the first step is initially disabled until validation passes
    if (this.steps.length > 0) {
      this.steps[0].disableNext = true;
      this.steps[0].complete = false;
    }
  }

  // Form validity handlers
  onDetailsFormValidityChange(isValid: boolean) {
    this.steps[0].disableNext = !isValid;
    this.formIsValid.set(isValid);

    // Emit validation state for parent components if needed
    if (isDefined(this.steps[0])) {
      this.steps[0].complete = isValid;
    }
  }

  // Update the device type state with form data
  updateDeviceTypeDetails(formData: Record<string, unknown>) {
    this.newDeviceType.update((current) => {
      const updated = { ...current };

      for (const key of Object.keys(formData)) {
        if (key !== 'ownerId') {
          updated[key] = formData[key];
        }
      }

      if (isNullOrUndefined(updated.ownerId)) {
        updated.ownerId = this.ownerId ?? '';
      }

      return updated;
    });
  }

  onWizardComplete(): void {
    // Process the final data from all steps
    const deviceTypeData = this.newDeviceType() as DeviceTypeCreateRequest;
    this.isSubmitting.set(true);

    this.deviceTypesService
      .createDeviceType(deviceTypeData)
      .pipe(
        takeUntilDestroyed(this.destroyRef),
        finalize(() => this.isSubmitting.set(false)),
      )
      .subscribe({
        next: (response: DeviceTypeResponse) => {
          this.broadcastService.broadcast('entity-updated', {
            entityType: 'device-types',
            operation: 'create',
            entityId: response.id,
          });
          this.broadcastService.broadcast('tree-refresh-required', {
            entityType: 'device-types',
            operation: 'create',
            entityId: response.id,
          });
          this.dialogRef.close(response);
        },
        error: (error: Error) => {
          // Handle error, could show message and keep dialog open
          console.error('Error creating device type:', error);
        },
      });
  }

  // Update cancel to close dialog with no result
  onWizardCancel(): void {
    this.newDeviceType.set({});
    this.dialogRef.close();
  }
}
