import { Type } from '@angular/core';
import { TREE_TABLE_CONFIGS } from '../../../core/models';
import {
  TreeTableConfiguration,
  TreeTableConfigurationEntry,
} from '../../../shared/models';
import { AssetCategoriesGridConfig } from './asset-categories.grid.config';
import { AssetGroupsGridConfig } from './asset-groups.grid.config';
import { CompanyGroupsGridConfig } from './company-groups.grid.config';
import { CostCentresGridConfig } from './cost-centres.grid.config';
import { GeofenceGroupsGridConfig } from './geofence-groups.grid.config';
import { DeviceTypesGridConfig } from './device-types.grid.config';

/**
 * Helper function to create a tree table configuration entry
 */
function createTreeTableConfigEntry<T>(
  entityType: string, // Changed from TreeTableEntityType to string
  config: Type<TreeTableConfiguration<T>>,
): TreeTableConfigurationEntry {
  return {
    entityType,
    config: config as Type<TreeTableConfiguration<unknown>>,
  };
}

export const ADMIN_TREE_TABLE_CONFIGS = {
  provide: TREE_TABLE_CONFIGS,
  useValue: [
    createTreeTableConfigEntry('client-cost-centres', CostCentresGridConfig),
    createTreeTableConfigEntry('client-asset-groups', AssetGroupsGridConfig),
    createTreeTableConfigEntry(
      'client-asset-categories',
      AssetCategoriesGridConfig,
    ),
    createTreeTableConfigEntry(
      'client-geofence-groups',
      GeofenceGroupsGridConfig,
    ),
    createTreeTableConfigEntry(
      'vendor-company-groups',
      CompanyGroupsGridConfig,
    ),
    createTreeTableConfigEntry(
      'distributor-company-groups',
      CompanyGroupsGridConfig,
    ),
    createTreeTableConfigEntry(
      'super-admin-device-types',
      DeviceTypesGridConfig,
    ),
  ],
  multi: true,
};
