import {
  ChangeDetectionStrategy,
  Component,
  computed,
  inject,
  input,
  output,
} from '@angular/core';
import { CommonModule } from '@angular/common';
import { TreeNode } from 'primeng/api';
import { ToolbarModule } from 'primeng/toolbar';
import { faAngleLeft, faAngleRight } from '@fortawesome/free-solid-svg-icons';
import { FontAwesomeModule } from '@fortawesome/angular-fontawesome';

import {
  TreePanelComponent,
  NodeNotFoundEvent,
} from '../../../../../shared/components/tree-panel/tree-panel.component';
import { TreeSearchComponent } from '../../../../../shared/components/tree-search/tree-search.component';
import { TreeSearchResult } from '../../../../../shared/models';
import { AdminStateService } from '../../../services/state/admin-state.service';
import { AdminTreeContextExtractorService } from '../../../services/tree/admin-tree-context-extractor.service';

@Component({
  selector: 'app-tree-navigation-panel',
  imports: [
    CommonModule,
    TreePanelComponent,
    TreeSearchComponent,
    ToolbarModule,
    FontAwesomeModule,
  ],
  templateUrl: './tree-navigation-panel.component.html',
  styleUrl: './tree-navigation-panel.component.scss',
  changeDetection: ChangeDetectionStrategy.OnPush,
})
export class TreeNavigationPanelComponent {
  private readonly adminState = inject(AdminStateService);
  readonly contextExtractor = inject(AdminTreeContextExtractorService);

  // Input properties
  readonly treeCollapsed = input.required<() => boolean>();
  readonly hierarchyTree = input.required<() => TreeNode[]>();
  readonly isLoading = input.required<() => boolean>();
  readonly searchingForNode = input.required<() => boolean>();
  readonly searchingNodeKey = input.required<() => string | null>();
  readonly selectedTreeNode = input.required<() => TreeNode | null>();
  readonly scrollToNodeKey = input.required<() => string | null>();
  readonly scrollToCenterNodeKey = input.required<() => string | null>();
  readonly searchResults = input.required<() => TreeSearchResult[]>();
  readonly isSearching = input.required<() => boolean>();

  // Output events
  readonly toggleTreePanel = output<void>();
  readonly navigateToSelectedClient = output<void>();
  readonly nodeExpand = output<TreeNode>();
  readonly nodeCollapse = output<TreeNode>();
  readonly nodeSelect = output<TreeNode>();
  readonly nodeNotFoundInData = output<NodeNotFoundEvent>();
  readonly searchQueryChange = output<string>();
  readonly searchResultSelect = output<TreeSearchResult>();
  readonly searchClear = output<void>();

  // Font Awesome icons
  readonly faAngleLeft = faAngleLeft;
  readonly faAngleRight = faAngleRight;

  /**
   * Computed property for the toggle button text
   */
  readonly toggleButtonText = computed(() => {
    return (
      this.adminState.selectedClient()?.name ??
      this.adminState.clientVendor()?.name ??
      this.adminState.vendorDistributor()?.name ??
      'COMPANY STRUCTURE'
    );
  });

  /**
   * Finds the currently selected node for the tree panel
   */
  findSelectedNode(): TreeNode | null {
    return this.selectedTreeNode()();
  }

  // Event handlers - these simply emit to parent
  onToggleTreePanel(): void {
    this.toggleTreePanel.emit();
  }

  onNavigateToSelectedClient(): void {
    this.navigateToSelectedClient.emit();
  }

  onNodeExpand(node: TreeNode): void {
    this.nodeExpand.emit(node);
  }

  onNodeCollapse(node: TreeNode): void {
    this.nodeCollapse.emit(node);
  }

  onNodeSelect(node: TreeNode): void {
    this.nodeSelect.emit(node);
  }

  onNodeNotFoundInData(event: NodeNotFoundEvent): void {
    this.nodeNotFoundInData.emit(event);
  }

  onSearchQueryChange(query: string): void {
    this.searchQueryChange.emit(query);
  }

  onSearchResultSelect(result: TreeSearchResult): void {
    this.searchResultSelect.emit(result);
  }

  onSearchClear(): void {
    this.searchClear.emit();
  }
}
