import { inject, Injectable, signal, computed } from '@angular/core';
import { HttpErrorResponse } from '@angular/common/http';
import { switchMap, of, Observable, catchError, firstValueFrom } from 'rxjs';
import { AdminTreeService } from './tree/admin-tree.service';
import { AdminStateService } from './state/admin-state.service';
import { RequestCacheService } from '../../../shared/services/request-cache.service';
import { isDefined, isNullOrUndefined } from '../../../shared/utils';
import {
  ClientListItem,
  VendorListItem,
  EntitySearchResponse,
  EntitySearchResult,
  ClientResponse,
  CompanyGroupResponse,
} from '../../../api';
import { ADMIN_TREE, UserPermissionLevel } from '../models';
import { TreeSearchResult } from '../../../shared/models';
import { SearchService } from '../../../api/api/search.service';
import { DEFAULT_SYSTEM_ID } from '../../../core/models';

export interface SearchState {
  query: string;
  results: TreeSearchResult[];
  isSearching: boolean;
  expandedForSearch: Set<string>;
  highlightedNodes: Set<string>;
}

@Injectable({
  providedIn: 'root',
})
export class AdminSearchService {
  private readonly adminTreeService = inject(AdminTreeService);
  private readonly adminState = inject(AdminStateService);
  private readonly cacheService = inject(RequestCacheService);
  private readonly searchService = inject(SearchService);

  private readonly searchState = signal<SearchState>({
    query: '',
    results: [],
    isSearching: false,
    expandedForSearch: new Set(),
    highlightedNodes: new Set(),
  });

  // Public computed values
  readonly query = computed(() => this.searchState().query);
  readonly results = computed(() => this.searchState().results);
  readonly isSearching = computed(() => this.searchState().isSearching);
  readonly expandedForSearch = computed(
    () => this.searchState().expandedForSearch,
  );
  readonly highlightedNodes = computed(
    () => this.searchState().highlightedNodes,
  );
  readonly hasResults = computed(() => this.searchState().results.length > 0);
  readonly hasQuery = computed(
    () => this.searchState().query.trim().length > 0,
  );
  searchTimeout: string | number | NodeJS.Timeout | undefined;

  // #region ========== CORE SEARCH OPERATIONS ==========

  private performSearch(query: string): Observable<TreeSearchResult[]> {
    if (!query || query.length < 2) {
      return of([]);
    }

    // Use the global search service instead of local tree search
    return this.cacheService.getOrFetch(
      `admin-search-${query}`,
      () =>
        this.searchService.searchEntities(query, false).pipe(
          switchMap((response: EntitySearchResponse) => {
            const results = this.mapEntitySearchToTreeResults(response.items);
            return of(results);
          }),
        ),
      30000,
    );
  }

  updateQuery(query: string): void {
    const trimmedQuery = query.trim();
    // Only update and trigger a search if the effective query has changed.
    if (trimmedQuery !== this.query()) {
      this.searchState.update((state) => ({
        ...state,
        query: trimmedQuery,
        isSearching: trimmedQuery.length > 0,
      }));

      // Perform search directly with debouncing handled by setTimeout
      this.performDebouncedSearch(trimmedQuery);
    }
  }

  private performDebouncedSearch(query: string): void {
    if (isDefined(this.searchTimeout)) {
      clearTimeout(this.searchTimeout);
    }

    this.searchTimeout = setTimeout(() => {
      this.performSearch(query).subscribe((results) => {
        this.updateTreeSearchResults(results);
      });
    }, 300);
  }

  clearSearch(): void {
    this.searchState.set({
      query: '',
      results: [],
      isSearching: false,
      expandedForSearch: new Set(),
      highlightedNodes: new Set(),
    });
    if (isDefined(this.searchTimeout)) {
      clearTimeout(this.searchTimeout);
    }
  }

  isNodeHighlighted(nodeId: string): boolean {
    return this.highlightedNodes().has(nodeId);
  }

  shouldExpandForSearch(nodeId: string): boolean {
    return this.expandedForSearch().has(nodeId);
  }
  // #endregion

  // #region ========== SEARCH RESULT MAPPING & HANDLING ==========

  /**
   * Maps EntitySearchResult[] to TreeSearchResult[]
   * Creates the tree structure path for each search result
   */
  private mapEntitySearchToTreeResults(
    entities: EntitySearchResult[],
  ): TreeSearchResult[] {
    return entities.map((entity) => this.mapEntityToTreeResult(entity));
  }

  /**
   * Maps a single EntitySearchResult to TreeSearchResult
   * Determines the tree path based on entity type and owner hierarchy
   */
  private mapEntityToTreeResult(entity: EntitySearchResult): TreeSearchResult {
    const path: string[] = [];
    const parentIds: string[] = [];

    // Start with distributor if it exists
    const distributor = this.adminState.vendorDistributor();
    if (distributor) {
      path.push(distributor.name ?? 'Distributor');
      parentIds.push(distributor.id);
    }

    // Add owner hierarchy based on entity type
    if (entity.owner?.id) {
      this.addOwnerHierarchy(entity, path, parentIds);
    }

    // Add the entity itself to the path
    path.push(entity.name);

    // Map entity type to tree node type
    const nodeType = this.getNodeType(entity.type);

    return {
      id: entity.id,
      name: entity.name,
      type: nodeType,
      path,
      parentIds,
      matched: true,
      owner: entity.owner,
      state: entity.state,
      fields: entity.fields,
    };
  }

  /**
   * Adds owner hierarchy to the path and parentIds arrays
   */
  private addOwnerHierarchy(
    entity: EntitySearchResult,
    path: string[],
    parentIds: string[],
  ): void {
    if (entity.type === 'client' && entity.owner?.type === 'vendor') {
      this.addVendorToPath(entity.owner.id, path, parentIds);
    } else if (entity.type === 'client' && entity.owner?.type === 'group') {
      this.addGroupHierarchy(
        entity.owner.id,
        entity.owner.name,
        path,
        parentIds,
      );
    } else if (entity.type === 'group' && entity.owner?.type === 'vendor') {
      this.addVendorToPath(entity.owner.id, path, parentIds);
    }
  }

  /**
   * Adds vendor to path and parentIds
   */
  private addVendorToPath(
    vendorId: string,
    path: string[],
    parentIds: string[],
  ): void {
    const vendor = this.adminState.getVendorById(vendorId);
    if (vendor) {
      path.push(vendor.name ?? 'Vendor');
      parentIds.push(vendor.id);
    }
  }

  /**
   * Adds group hierarchy (vendor + group) to path and parentIds
   */
  private addGroupHierarchy(
    groupId: string,
    groupName: string | null | undefined,
    path: string[],
    parentIds: string[],
  ): void {
    const vendorId = this.findVendorIdForGroup(groupId);

    if (isDefined(vendorId)) {
      this.addVendorToPath(vendorId, path, parentIds);

      // Add the group
      path.push(groupName ?? 'Group');
      parentIds.push(groupId);
    }
  }

  /**
   * Maps entity type string to tree node type
   */
  private getNodeType(entityType: string): string {
    switch (entityType) {
      case 'vendor':
        return ADMIN_TREE.NODE_TYPES.VENDOR;
      case 'group':
        return ADMIN_TREE.NODE_TYPES.GROUP;
      case 'client':
      default:
        return entityType;
    }
  }

  /**
   * Updates the search state with results and handles UI updates
   */
  private updateTreeSearchResults(results: TreeSearchResult[]): void {
    const highlightedNodes = new Set(results.map((r) => r.id));
    const expandedForSearch = new Set<string>();

    // Add parent IDs to expansion set
    for (const result of results) {
      for (const parentId of result.parentIds) {
        expandedForSearch.add(parentId);
      }
    }

    this.searchState.update((state) => ({
      ...state,
      results,
      isSearching: false,
      highlightedNodes,
      expandedForSearch,
    }));
  }
  // #endregion

  // #region ========== CONTEXT & PERMISSION MANAGEMENT ==========

  /**
   * Gets an effective distributor context ID that works across all permission levels.
   * Uses the active context ID which handles both distributor and vendor-only scenarios.
   *
   * @returns A context key to use for state management, or null if no context available
   */
  private getEffectiveDistributorId(): string | null {
    return this.adminState.getActiveContextId();
  }
  // #endregion

  // #region ========== NAVIGATION & PARENT NODE LOADING ==========

  async prepareResultForNavigation(result: TreeSearchResult): Promise<void> {
    // Ensure all parent nodes are loaded and expanded before navigation
    await this.ensureParentNodesLoaded(result);

    // Clear search after navigation
    this.clearSearch();
  }

  async ensureParentNodesLoaded(result: TreeSearchResult): Promise<void> {
    for (const parentId of result.parentIds) {
      const parentNode = this.findNodeInState(parentId);
      if (parentNode) {
        await this.loadParentNode(parentNode, parentId);
      } else {
        await this.handleUnknownParent(result.parentIds, parentId);
      }
    }
  }

  private async loadParentNode(
    parentNode: { id: string; type: string },
    parentId: string,
  ): Promise<void> {
    // Use effective distributor ID with fallback
    const effectiveDistributorId = this.getEffectiveDistributorId();
    if (isNullOrUndefined(effectiveDistributorId)) {
      console.warn('No context available for loading parent node');
      return;
    }

    switch (parentNode.type) {
      case ADMIN_TREE.NODE_TYPES.DISTRIBUTOR:
        await this.handleDistributorNode(parentId);
        break;

      case ADMIN_TREE.NODE_TYPES.VENDOR:
        await this.handleVendorNode(parentId, effectiveDistributorId);
        break;

      case ADMIN_TREE.NODE_TYPES.GROUP: {
        await this.handleGroupNode(parentId, effectiveDistributorId);
        break;
      }
    }
  }

  private async handleDistributorNode(parentId: string): Promise<void> {
    // Check permission before attempting to load distributor data
    if (!this.adminState.canAccessDistributorLevel()) {
      console.warn('User does not have distributor-level access');
      return;
    }
    // Ensure distributor is expanded (only if we have actual distributor-level access)
    if (this.adminState.hasDistributorData()) {
      this.adminState.setExpandedDistributor(parentId, true);
    }
  }

  private async handleVendorNode(
    parentId: string,
    effectiveDistributorId: string,
  ): Promise<void> {
    // Check permission before attempting to load vendor data
    if (!this.adminState.canAccessVendorLevel()) {
      console.warn('User does not have vendor-level access');
      return;
    }
    // Ensure vendor node is loaded and expanded using context-scoped methods
    if (
      !this.adminState.isDistributorVendorLoaded(
        effectiveDistributorId,
        parentId,
      )
    ) {
      try {
        await this.loadVendorData(parentId, effectiveDistributorId);
      } catch (error) {
        if (error instanceof HttpErrorResponse && error.status === 403) {
          console.warn('Access denied when loading vendor parent node');
          return;
        }
        // Re-throw other errors
        throw error;
      }
    }
    this.adminState.setDistributorNodeExpanded(
      effectiveDistributorId,
      parentId,
      true,
    );
  }

  private async handleGroupNode(
    parentId: string,
    effectiveDistributorId: string,
  ): Promise<void> {
    // Check permission before attempting to load group data (requires vendor access)
    if (!this.adminState.canAccessVendorLevel()) {
      console.warn('User does not have vendor-level access for groups');
      return;
    }
    // Ensure group is expanded and its clients are loaded
    const vendorId = this.findVendorIdForGroup(
      parentId,
      effectiveDistributorId,
    );
    if (isDefined(vendorId)) {
      try {
        await this.loadGroupData(vendorId, parentId, effectiveDistributorId);
        this.adminState.setDistributorExpandedGroup(
          effectiveDistributorId,
          parentId,
          true,
        );
      } catch (error) {
        if (error instanceof HttpErrorResponse && error.status === 403) {
          console.warn('Access denied when loading group data');
          return;
        }
        // Re-throw other errors
        throw error;
      }
    }
  }

  // #endregion

  // #region ========== DATA LOADING OPERATIONS ==========

  private async loadVendorData(
    vendorId: string,
    distributorId?: string,
  ): Promise<void> {
    // Use effective distributor ID with fallback to vendor ID for vendor-only users
    const contextDistributorId =
      distributorId ?? this.getEffectiveDistributorId();

    if (isNullOrUndefined(contextDistributorId)) {
      console.warn('No context available for loading vendor data');
      return;
    }

    try {
      // Load vendor groups and direct clients
      const [groups, clientResponse] = await Promise.all([
        this.adminTreeService.listCompanyGroups(vendorId),
        this.adminTreeService.listVendorClients(vendorId, undefined, 0, 100),
      ]);

      this.adminState.setDistributorVendorGroups(
        contextDistributorId,
        vendorId,
        groups,
      );
      this.adminState.addDistributorVendorClients(
        contextDistributorId,
        vendorId,
        clientResponse.clients,
        {
          offset: 0,
          limit: 100,
          total: clientResponse.total,
          hasMore: clientResponse.hasMore,
        },
      );
      this.adminState.markDistributorVendorAsLoaded(
        contextDistributorId,
        vendorId,
      );
    } catch (error) {
      // Handle 403 errors specifically - user doesn't have vendor access
      if (error instanceof HttpErrorResponse && error.status === 403) {
        console.warn(`Access denied to vendor data for ${vendorId} (403)`);
        // Update permission level if not already set
        if (this.adminState.userPermissionLevel() === null) {
          this.adminState.setUserPermissionLevel(
            UserPermissionLevel.CLIENT_ONLY,
          );
        }
        this.adminState.markDistributorVendorAsFailedToLoad(
          contextDistributorId,
          vendorId,
        );
        // Re-throw so caller knows to stop
        throw error;
      }

      console.error(`Failed to load vendor data for ${vendorId}:`, error);
      this.adminState.markDistributorVendorAsFailedToLoad(
        contextDistributorId,
        vendorId,
      );
      // Re-throw other errors
      throw error;
    }
  }

  private async loadGroupData(
    vendorId: string,
    groupId: string,
    distributorId?: string,
  ): Promise<void> {
    // Use effective distributor ID with fallback to vendor ID
    const contextDistributorId =
      distributorId ?? this.getEffectiveDistributorId();

    if (isNullOrUndefined(contextDistributorId)) {
      console.warn('No context available for loading group data');
      return;
    }

    try {
      // Ensure group object is in the state's vendorGroups list
      // This is CRITICAL for the tree builder to actually create the group node
      const vendorGroups =
        this.adminState.vendorGroups()[contextDistributorId]?.[vendorId] ?? [];
      let group: CompanyGroupResponse | null | undefined = vendorGroups.find(
        (g) => g.id === groupId,
      );

      if (!isDefined(group)) {
        group = await this.adminTreeService.fetchCompanyGroup(groupId);
        if (isDefined(group)) {
          this.adminState.addDistributorVendorGroup(
            contextDistributorId,
            vendorId,
            group,
          );
        } else {
          console.warn(
            `[Trace] loadGroupData: Failed to fetch group object for ${groupId}`,
          );
        }
      }

      // Check if group clients are already loaded using distributor-scoped pagination
      const paginationKey = this.adminState.getDistributorPaginationKey(
        contextDistributorId,
        vendorId,
        groupId,
      );
      const distributorPagination =
        this.adminState.clientPagination()[contextDistributorId];
      if (isDefined(distributorPagination?.[paginationKey])) {
        return; // Already loaded
      }
      const clientResponse = await this.adminTreeService.listVendorClients(
        vendorId,
        groupId,
        0,
        100,
      );

      this.adminState.addGroupClients(
        contextDistributorId,
        vendorId,
        groupId,
        clientResponse.clients,
        {
          offset: 0,
          limit: 100,
          total: clientResponse.total,
          hasMore: clientResponse.hasMore,
        },
      );
    } catch (error) {
      // Handle 403 errors specifically - user doesn't have group access
      if (error instanceof HttpErrorResponse && error.status === 403) {
        console.warn(
          `Access denied to group data for ${vendorId}/${groupId} (403)`,
        );
        // Update permission level if not already set
        if (this.adminState.userPermissionLevel() === null) {
          this.adminState.setUserPermissionLevel(
            UserPermissionLevel.CLIENT_ONLY,
          );
        }
        // Re-throw so caller knows to stop
        throw error;
      }

      console.error(
        `Failed to load group data for ${vendorId}/${groupId}:`,
        error,
      );
      // Re-throw other errors
      throw error;
    }
  }
  // #endregion

  // #region ========== NODE FINDING & HIERARCHY ==========

  private findNodeInState(nodeId: string): { id: string; type: string } | null {
    // Check distributor (only if user has distributor access)
    const distributor = this.adminState.vendorDistributor();
    if (distributor?.id === nodeId) {
      return { id: distributor.id, type: ADMIN_TREE.NODE_TYPES.DISTRIBUTOR };
    }

    // Get effective distributor context (may be vendor ID for vendor-only users)
    const effectiveDistributorId = this.getEffectiveDistributorId();
    if (isNullOrUndefined(effectiveDistributorId)) {
      return null;
    }

    // Check vendors using context-scoped data
    const distributorVendors =
      this.adminState.distributorVendors()[effectiveDistributorId] ?? [];
    const vendor = distributorVendors.find((v) => v.id === nodeId);
    if (vendor) {
      return { id: vendor.id, type: ADMIN_TREE.NODE_TYPES.VENDOR };
    }

    // Check groups using context-scoped data
    const vendorGroups =
      this.adminState.vendorGroups()[effectiveDistributorId] ?? {};
    for (const groups of Object.values(vendorGroups)) {
      const group = groups.find((g) => g.id === nodeId);
      if (group) {
        return { id: group.id, type: ADMIN_TREE.NODE_TYPES.GROUP };
      }
    }

    return null;
  }

  private async handleUnknownParent(
    parentIds: string[],
    parentId: string,
  ): Promise<void> {
    const vendorInParents = this.findVendorInParents(parentIds);
    if (isDefined(vendorInParents)) {
      const effectiveDistributorId = this.getEffectiveDistributorId();
      if (isDefined(effectiveDistributorId)) {
        await this.loadGroupData(
          vendorInParents,
          parentId,
          effectiveDistributorId,
        );
        this.adminState.setDistributorExpandedGroup(
          effectiveDistributorId,
          parentId,
          true,
        );
      }
    }
  }

  private findVendorInParents(parentIds: string[]): string | null {
    // Get effective distributor context
    const effectiveDistributorId = this.getEffectiveDistributorId();
    if (isNullOrUndefined(effectiveDistributorId)) {
      return null;
    }

    // Use context-scoped vendors
    const distributorVendors =
      this.adminState.distributorVendors()[effectiveDistributorId] ?? [];
    return (
      parentIds.find((id) => distributorVendors.some((v) => v.id === id)) ??
      null
    );
  }

  private findVendorIdForGroup(
    groupId: string,
    distributorId?: string,
  ): string | null {
    // Use effective distributor ID with fallback
    const contextDistributorId =
      distributorId ?? this.getEffectiveDistributorId();
    if (isNullOrUndefined(contextDistributorId)) {
      return null;
    }

    const vendorGroups =
      this.adminState.vendorGroups()[contextDistributorId] ?? {};
    for (const [vendorId, groups] of Object.entries(vendorGroups)) {
      if (groups.some((group) => group.id === groupId)) {
        return vendorId;
      }
    }
    return null;
  }

  /**
   * Extracts the hierarchy information (distributorId, vendorId, groupId) from a TreeSearchResult.
   * The parentIds array is ordered based on the entity type and hierarchy structure.
   *
   * For clients in distributor context:
   * - Vendor-owned: [distributorId, vendorId]
   * - Group-owned: [distributorId, vendorId, groupId]
   *
   * For clients without distributor:
   * - Vendor-owned: [vendorId]
   * - Group-owned: [vendorId, groupId]
   *
   * @param result The TreeSearchResult to extract hierarchy from
   * @returns Object containing distributorId, vendorId, and optional groupId
   */
  private extractHierarchyFromResult(result: TreeSearchResult): {
    vendorId?: string;
    groupId?: string;
    distributorId?: string;
  } {
    const parentIds = result.parentIds;

    if (parentIds.length === 0) {
      return {};
    }

    const distributor = this.adminState.vendorDistributor();
    let vendorId: string | undefined;
    let groupId: string | undefined;
    let distributorId: string | undefined;

    // Check if first parent is the current distributor
    if (isDefined(distributor) && parentIds[0] === distributor.id) {
      distributorId = parentIds[0];
      vendorId = parentIds[1];
      groupId = parentIds[2]; // May be undefined
    } else {
      // No distributor in path, or different context
      vendorId = parentIds[0];
      groupId = parentIds[1]; // May be undefined
    }

    return { vendorId, groupId, distributorId };
  }

  private findClientInDistributor(
    clientId: string,
    distributorId: string,
  ): { distributorId: string; vendorId: string; groupId?: string } | null {
    // Search in vendor clients for this distributor
    const vendorClients = this.adminState.vendorClients()[distributorId] ?? {};
    for (const [vendorId, clients] of Object.entries(vendorClients)) {
      if (clients.some((client) => client.id === clientId)) {
        return { distributorId, vendorId };
      }
    }

    // Search in group clients for this distributor
    const groupClients = this.adminState.groupClients()[distributorId] ?? {};
    for (const [vendorId, groups] of Object.entries(groupClients)) {
      for (const [groupId, clients] of Object.entries(groups)) {
        if (clients.some((client) => client.id === clientId)) {
          return { distributorId, vendorId, groupId };
        }
      }
    }

    return null;
  }

  /**
   * Finds the vendor and group IDs for a given client by searching through all loaded data.
   * Works across all permission levels (client-only, vendor, distributor, super admin).
   * @param clientId The ID of the client to find
   * @param distributorId Optional distributor ID to scope the search
   * @returns Object containing distributorId (or context key), vendorId and optional groupId, or null if not found
   */
  findClientHierarchy(
    clientId: string,
    distributorId?: string,
  ): { distributorId: string; vendorId: string; groupId?: string } | null {
    // If distributor/context is specified, search within that context's data
    if (isDefined(distributorId)) {
      return this.findClientInDistributor(clientId, distributorId);
    }

    // Otherwise, search across all available contexts (distributors, vendors used as context, etc.)
    const distributorVendorClients = this.adminState.vendorClients();
    for (const [contextId] of Object.entries(distributorVendorClients)) {
      const result = this.findClientInDistributor(clientId, contextId);
      if (result) return result;
    }

    // If not found in any context, try with the effective distributor ID
    const effectiveDistributorId = this.getEffectiveDistributorId();
    if (isDefined(effectiveDistributorId)) {
      return this.findClientInDistributor(clientId, effectiveDistributorId);
    }

    return null;
  }
  // #endregion

  // #region ========== TREE RESULT CREATION ==========

  /**
   * Helper methods for creating tree search results
   */
  private createClientTreeSearchResult(
    client: ClientListItem,
    vendorId: string,
  ): TreeSearchResult {
    const vendor = this.adminState.getVendorById(vendorId);
    const distributor = this.adminState.vendorDistributor();

    const path: string[] = [];
    const parentIds: string[] = [];

    if (distributor) {
      path.push(distributor.name ?? 'Distributor');
      parentIds.push(distributor.id);
    }

    if (vendor) {
      path.push(vendor.name ?? 'Vendor');
      parentIds.push(vendor.id);
    }

    path.push(client.name ?? 'Client');

    return {
      id: client.id,
      name: client.name ?? '',
      type: ADMIN_TREE.NODE_TYPES.CLIENT,
      path,
      parentIds,
      matched: true,
    };
  }

  private createGroupClientTreeSearchResult(
    client: ClientListItem,
    vendorId: string,
    groupId: string,
  ): TreeSearchResult {
    const vendor = this.adminState.getVendorById(vendorId);
    const distributor = this.adminState.vendorDistributor();

    const path: string[] = [];
    const parentIds: string[] = [];

    if (distributor) {
      path.push(distributor.name ?? 'Distributor');
      parentIds.push(distributor.id);
    }

    if (vendor) {
      path.push(vendor.name ?? 'Vendor');
      parentIds.push(vendor.id);
    }

    // Add the group to both path and parentIds using context-scoped data
    const effectiveDistributorId = this.getEffectiveDistributorId();
    let group = null;

    if (isDefined(effectiveDistributorId)) {
      const vendorGroups =
        this.adminState.vendorGroups()[effectiveDistributorId]?.[vendorId] ??
        [];
      group = vendorGroups.find((g) => g.id === groupId);
    }

    if (group) {
      path.push(group.name ?? 'Group');
      parentIds.push(group.id);
    } else {
      // If group is not in loaded state, use placeholder
      path.push('Group');
      parentIds.push(groupId);
    }

    path.push(client.name ?? 'Client');

    return {
      id: client.id,
      name: client.name ?? '',
      type: ADMIN_TREE.NODE_TYPES.CLIENT,
      path,
      parentIds,
      matched: true,
    };
  }

  private createVendorTreeSearchResult(vendor: {
    id: string;
    name?: string | null;
  }): TreeSearchResult {
    const distributor = this.adminState.vendorDistributor();

    const path: string[] = [];
    const parentIds: string[] = [];

    if (distributor) {
      path.push(distributor.name ?? 'Distributor');
      parentIds.push(distributor.id);
    }

    path.push(vendor.name ?? 'Vendor');

    return {
      id: vendor.id,
      name: vendor.name ?? '',
      type: ADMIN_TREE.NODE_TYPES.VENDOR,
      path,
      parentIds,
      matched: true,
    };
  }

  /**
   * Creates a TreeSearchResult object for a given client.
   * This is used to prepare for navigation from outside the search component,
   * e.g., from a 'View in Tree' action.
   * @param client The client list item.
   * @param vendorId The ID of the parent vendor.
   * @param groupId The optional ID of the parent group.
   * @returns A TreeSearchResult object.
   */
  createSearchResultForClient(
    client: ClientListItem,
    vendorId: string,
    groupId?: string,
  ): TreeSearchResult {
    if (isDefined(groupId)) {
      return this.createGroupClientTreeSearchResult(client, vendorId, groupId);
    }
    return this.createClientTreeSearchResult(client, vendorId);
  }

  /**
   * Creates a TreeSearchResult object for a given vendor.
   * @param vendor The vendor list item.
   * @returns A TreeSearchResult object.
   */
  createSearchResultForVendor(vendor: {
    id: string;
    name?: string | null;
  }): TreeSearchResult {
    return this.createVendorTreeSearchResult(vendor);
  }

  // #endregion

  // #region ========== PUBLIC API - TREE NAVIGATION ==========

  /**
   * Handles a client owned directly by a vendor
   */
  private async handleVendorOwnedClient(
    client: Partial<ClientResponse>,
  ): Promise<TreeSearchResult | null> {
    if (!isDefined(client.owner?.id) || !isDefined(client.id)) return null;

    const vendorId = client.owner.id;

    // Ensure vendor is loaded and expanded
    await this.ensureVendorLoadedAndExpanded(vendorId);

    // Load clients until we find the target client
    const foundClient = await this.loadClientsUntilFound(client.id, vendorId);

    if (foundClient) {
      const result = this.createSearchResultForClient(foundClient, vendorId);
      await this.ensureParentNodesLoaded(result);
      return result;
    }

    return null;
  }

  /**
   * Handles a client owned by a group
   */
  private async handleGroupOwnedClient(
    client: Partial<ClientResponse>,
  ): Promise<TreeSearchResult | null> {
    if (!isDefined(client.group) || !isDefined(client.id)) return null;
    const groupId = client.group;

    // Find the vendor that owns this group
    let vendorId = this.findVendorIdForGroup(groupId);

    // Fallback: If group not in state yet, trust the client's owner property if it's not the group itself
    if (
      !isDefined(vendorId) &&
      isDefined(client.owner?.id) &&
      client.owner.id !== groupId
    ) {
      vendorId = client.owner.id;
    }

    if (!isDefined(vendorId)) {
      return null;
    }

    // Ensure vendor is loaded and expanded
    await this.ensureVendorLoadedAndExpanded(vendorId);

    const effectiveDistributorId = this.getEffectiveDistributorId();
    if (isNullOrUndefined(effectiveDistributorId)) {
      return null;
    }
    // Ensure group is expanded
    this.adminState.setDistributorExpandedGroup(
      effectiveDistributorId,
      groupId,
      true,
    );

    // Load group clients until we find the target client
    const foundClient = await this.loadClientsUntilFound(
      client.id,
      vendorId,
      groupId,
    );

    if (foundClient) {
      const result = this.createSearchResultForClient(
        foundClient,
        vendorId,
        groupId,
      );
      await this.ensureParentNodesLoaded(result);
      return result;
    }

    return null;
  }

  /**
   * Ensures a vendor is loaded and expanded
   */
  private async ensureVendorLoadedAndExpanded(vendorId: string): Promise<void> {
    // Check permission before attempting to load vendor data
    if (!this.adminState.canAccessVendorLevel()) {
      console.warn('User does not have vendor-level access');
      return;
    }

    const effectiveDistributorId = this.getEffectiveDistributorId();
    if (isNullOrUndefined(effectiveDistributorId)) {
      console.warn('No context available for loading vendor');
      return;
    }

    // Load vendor data if not already loaded
    if (
      !this.adminState.isDistributorVendorLoaded(
        effectiveDistributorId,
        vendorId,
      )
    ) {
      try {
        await this.loadVendorData(vendorId, effectiveDistributorId);
      } catch (error) {
        if (error instanceof HttpErrorResponse && error.status === 403) {
          console.warn('Access denied to vendor data (403)');
          // Update permission level if not already set
          if (this.adminState.userPermissionLevel() === null) {
            this.adminState.setUserPermissionLevel(
              UserPermissionLevel.CLIENT_ONLY,
            );
          }
          return;
        }
        // Re-throw other errors
        throw error;
      }
    }

    // Ensure vendor is expanded
    this.adminState.setDistributorNodeExpanded(
      effectiveDistributorId,
      vendorId,
      true,
    );
  }

  /**
   * Handles showing a client in the tree by preparing the tree and returning navigation info.
   * @param client The client entity to show.
   * @param vendorId The ID of the parent vendor.
   * @param groupId The optional ID of the parent group.
   * @returns Promise that resolves to the search result for navigation
   */
  async handleClientInTree(
    client: ClientListItem,
    vendorId: string,
    groupId?: string,
  ): Promise<TreeSearchResult> {
    if (isDefined(groupId) && groupId !== DEFAULT_SYSTEM_ID) {
      // Group-owned client - paginate through group clients
      const foundClient = await this.loadClientsUntilFound(
        client.id,
        vendorId,
        groupId,
      );
      if (foundClient) {
        const result = this.createSearchResultForClient(
          foundClient,
          vendorId,
          groupId,
        );
        await this.ensureParentNodesLoaded(result);
        return result;
      }
    } else {
      // Vendor-owned client - paginate through vendor clients
      const foundClient = await this.loadClientsUntilFound(client.id, vendorId);
      if (foundClient) {
        const result = this.createSearchResultForClient(foundClient, vendorId);
        await this.ensureParentNodesLoaded(result);
        return result;
      }
    }

    throw new Error(`Client ${client.id} not found after pagination`);
  }

  /**
   * Handles showing a vendor in the tree by preparing the tree and returning navigation info.
   * @param vendor The vendor entity to show.
   * @returns Promise that resolves to the search result for navigation
   */
  async handleVendorInTree(vendor: VendorListItem): Promise<TreeSearchResult> {
    const result = this.createSearchResultForVendor(vendor);

    // Use the search service's utility to expand all parent nodes
    await this.ensureParentNodesLoaded(result);

    return result;
  }

  /**
   * Prepares a client for tree selection by ensuring its hierarchy is loaded and expanded.
   *
   * This method is used when you have just a client ID and need to make it visible and
   * selectable in the tree. It's typically called from deep links or when navigating
   * from another part of the application.
   *
   * Approach:
   * 1. First checks if client is already in loaded data (fast path)
   * 2. If not found, uses global search API to find the client
   * 3. Uses search result to determine parent hierarchy
   * 4. Ensures all parents are loaded and expanded
   * 5. Returns TreeSearchResult for navigation
   *
   * This method does NOT use pagination - it relies on global search which is more
   * efficient when you don't know where the client is in the hierarchy.
   *
   * @param clientId The ID of the client to select
   * @returns Promise that resolves with the TreeSearchResult if successful, null if client not found
   */
  async prepareClientForTreeSelection(
    clientId: string,
  ): Promise<TreeSearchResult | null> {
    // First, check if we can find the client in already loaded data
    const hierarchy = this.findClientHierarchy(clientId);

    if (hierarchy) {
      // Try to find the client in the loaded data
      const client = this.findClientInLoadedData(
        clientId,
        hierarchy.vendorId,
        hierarchy.groupId,
      );
      if (client) {
        const result = this.createSearchResultForClient(
          client,
          hierarchy.vendorId,
          hierarchy.groupId,
        );
        await this.ensureParentNodesLoaded(result);
        return result;
      }
    }

    // Client not found in loaded data, use global search to find it
    try {
      const searchResponse = await firstValueFrom(
        this.searchService
          .searchEntities(clientId)
          .pipe(catchError(() => of(null))),
      );

      if (searchResponse?.items) {
        const clientEntity = searchResponse.items.find(
          (item) => item.id === clientId,
        );
        if (clientEntity) {
          const result = this.mapEntityToTreeResult(clientEntity);
          await this.ensureParentNodesLoaded(result);

          // Ensure the client itself is loaded if it's a client entity
          // This handles deep pagination - client might be on page 10+
          if (result.type === ADMIN_TREE.NODE_TYPES.CLIENT) {
            const hierarchy = this.extractHierarchyFromResult(result);
            if (isDefined(hierarchy.vendorId)) {
              await this.loadClientsUntilFound(
                clientId,
                hierarchy.vendorId,
                hierarchy.groupId,
              );
            }
          }

          return result;
        }
      }
    } catch (error) {
      console.error('Failed to search for client:', error);
    }

    console.warn(`Client ${clientId} not found in any vendor hierarchy`);
    return null;
  }

  /**
   * Prepares a client for tree selection using owner information from ClientDataService.
   *
   * This is the PREFERRED method for loading a client into the tree when you have
   * the full client object with owner information. It's more efficient than
   * prepareClientForTreeSelection because:
   *
   * 1. No global search API call needed (saves network request)
   * 2. Owner info tells us exactly where in hierarchy to look
   * 3. Can use targeted pagination instead of broad search
   *
   * This method handles two scenarios:
   * 1. Vendor-owned clients (no group, or group = DEFAULT_SYSTEM_ID)
   *    - Loads vendor's direct clients with pagination
   * 2. Group-owned clients (has group ID)
   *    - Loads group's clients with pagination
   *
   * Both scenarios use loadClientsUntilFound which has safety limits to prevent
   * infinite loops (max 50 pagination attempts, error handling, etc.)
   *
   * @param client The client with owner information from ClientDataService
   * @returns Promise that resolves with the TreeSearchResult if successful, null if not found
   */
  async prepareClientForTreeSelectionWithOwner(
    client: Partial<ClientResponse>,
  ): Promise<TreeSearchResult | null> {
    if (!isDefined(client.id) || !isDefined(client.owner)) {
      console.warn(
        '[Trace] prepareClientForTreeSelectionWithOwner: Client missing ID or owner information',
      );
      return null;
    }

    try {
      // Determine hierarchy based on owner information
      if (
        client.group === DEFAULT_SYSTEM_ID ||
        isNullOrUndefined(client.group)
      ) {
        return await this.handleVendorOwnedClient(client);
      } else {
        return await this.handleGroupOwnedClient(client);
      }
    } catch (error) {
      console.error(
        '[Trace] Failed to prepare client for tree selection:',
        error,
      );
      return null;
    }
  }

  // #endregion

  // #region ========== PAGINATION-BASED CLIENT LOADING ==========

  /**
   * Recursively loads clients using the load more functionality until the target client is found
   *
   * This is a critical method that implements safe pagination to find a specific client
   * without causing infinite loops. It's used when:
   * - A deep link points to a client not in loaded data
   * - A search result selects a client that hasn't been loaded yet
   * - Navigation requires a specific client that may be deep in the pagination
   *
   * Safety Mechanisms (prevents infinite loops):
   * 1. MAX_PAGINATION_ATTEMPTS limit (50 attempts = 5000 clients max)
   * 2. Error handling that breaks the loop on API failures
   * 3. hasMore check from pagination state
   * 4. Updates pagination state to hasMore=false on errors
   *
   * Flow:
   * 1. Check if client is already in loaded data (quick return)
   * 2. Load initial page if no pagination exists (first 100 clients)
   * 3. Loop through remaining pages until client found or limits reached:
   *    - Load next 100 clients
   *    - Check if target client is in the new batch
   *    - Break on error to prevent infinite retries
   *    - Stop at MAX_PAGINATION_ATTEMPTS
   * 4. Return client if found, null otherwise
   *
   * @param targetClientId The ID of the client to find
   * @param vendorId The vendor ID that owns the client (or its group)
   * @param groupId Optional group ID if client is in a group
   * @returns Promise resolving to ClientListItem if found, null if not found after all attempts
   */
  private async loadClientsUntilFound(
    targetClientId: string,
    vendorId: string,
    groupId?: string,
  ): Promise<ClientListItem | null> {
    const effectiveDistributorId = this.getEffectiveDistributorId();
    if (isNullOrUndefined(effectiveDistributorId)) {
      console.warn(
        '[Trace] loadClientsUntilFound: No context available for loading clients',
      );
      return null;
    }

    // Check if client is already in loaded data
    let foundClient = this.findClientInLoadedData(
      targetClientId,
      vendorId,
      groupId,
      effectiveDistributorId,
    );
    if (foundClient) {
      return foundClient;
    }

    // If not found, start loading more clients recursively
    const paginationKey = this.adminState.getDistributorPaginationKey(
      effectiveDistributorId,
      vendorId,
      groupId,
    );

    // Get current pagination state using context-scoped pagination
    let currentPagination =
      this.adminState.clientPagination()[effectiveDistributorId]?.[
        paginationKey
      ];

    // If no pagination exists, load the first page
    if (isNullOrUndefined(currentPagination)) {
      await this.loadInitialClients(vendorId, groupId, effectiveDistributorId);

      // Check again after initial load
      foundClient = this.findClientInLoadedData(
        targetClientId,
        vendorId,
        groupId,
        effectiveDistributorId,
      );
      if (foundClient) {
        return foundClient;
      }

      // Update pagination state
      currentPagination =
        this.adminState.clientPagination()[effectiveDistributorId]?.[
          paginationKey
        ];
    }

    // Safety mechanism: Limit the number of pagination attempts to prevent infinite loops
    const MAX_PAGINATION_ATTEMPTS = 50; // Maximum 5000 clients (50 * 100)
    let paginationAttempts = 0;

    // Continue loading more pages until client is found or no more data
    while (
      isDefined(currentPagination) &&
      currentPagination.hasMore &&
      isNullOrUndefined(foundClient) &&
      paginationAttempts < MAX_PAGINATION_ATTEMPTS
    ) {
      paginationAttempts++;

      try {
        await this.loadMoreClientsPage(
          vendorId,
          groupId,
          effectiveDistributorId,
        );
      } catch (error) {
        console.error(
          `[Trace] Error loading page ${paginationAttempts} while searching for client ${targetClientId}:`,
          error,
        );
        // Break the loop on error to prevent infinite retries
        break;
      }

      // Check if client is now found
      foundClient = this.findClientInLoadedData(
        targetClientId,
        vendorId,
        groupId,
        effectiveDistributorId,
      );
      if (foundClient) {
        return foundClient;
      }

      // Update pagination state for next iteration
      currentPagination =
        this.adminState.clientPagination()[effectiveDistributorId]?.[
          paginationKey
        ];
    }

    // Log if we hit the pagination limit
    if (paginationAttempts >= MAX_PAGINATION_ATTEMPTS) {
      console.warn(
        `[Trace] Reached maximum pagination attempts (${MAX_PAGINATION_ATTEMPTS}) while searching for client ${targetClientId}`,
      );
    }

    if (isNullOrUndefined(foundClient)) {
      console.warn(
        `[Trace] loadClientsUntilFound: Client ${targetClientId} NOT found after loading ${paginationAttempts} additional pages`,
      );
    }

    return foundClient ?? null;
  }

  /**
   * Loads initial clients for a vendor or group within context
   */
  private async loadInitialClients(
    vendorId: string,
    groupId?: string,
    distributorId?: string,
  ): Promise<void> {
    const contextDistributorId =
      distributorId ?? this.getEffectiveDistributorId();
    if (isNullOrUndefined(contextDistributorId)) {
      console.warn('No context available for loading initial clients');
      return;
    }

    try {
      const clientResponse = await this.adminTreeService.listVendorClients(
        vendorId,
        groupId,
        0,
        100,
      );

      const paginationUpdate = {
        offset: 0,
        limit: 100,
        total: clientResponse.total,
        hasMore: clientResponse.hasMore,
      };

      if (isDefined(groupId)) {
        this.adminState.addGroupClients(
          contextDistributorId,
          vendorId,
          groupId,
          clientResponse.clients,
          paginationUpdate,
          false, // Replace, not append
        );
      } else {
        this.adminState.addDistributorVendorClients(
          contextDistributorId,
          vendorId,
          clientResponse.clients,
          paginationUpdate,
          false, // Replace, not append
        );
      }
    } catch (error) {
      // Handle 403 errors specifically
      if (error instanceof HttpErrorResponse && error.status === 403) {
        console.warn('Access denied when loading initial clients (403)');
        // Update permission level if not already set
        if (this.adminState.userPermissionLevel() === null) {
          this.adminState.setUserPermissionLevel(
            UserPermissionLevel.CLIENT_ONLY,
          );
        }
        // Re-throw so caller knows to stop
        throw error;
      }
      console.error('Failed to load initial clients:', error);
      // Re-throw other errors
      throw error;
    }
  }

  /**
   * Loads the next page of clients using the existing load more functionality
   *
   * This method loads the next 100 clients from the API and updates the state.
   * It includes critical error handling to prevent infinite loops:
   *
   * Error Handling:
   * - On error, sets hasMore=false to stop future pagination attempts
   * - Uses empty array to update just the pagination state
   * - Re-throws error so caller knows the load failed
   *
   * The error handling is crucial because without it, a persistent API error
   * (network issue, auth problem, etc.) would cause the caller to retry
   * indefinitely, causing an infinite loop and browser hang.
   *
   * @param vendorId The vendor ID to load clients for
   * @param groupId Optional group ID if loading group clients
   * @param distributorId Optional distributor ID (uses current if not provided)
   * @throws Error if the API request fails (after updating state to prevent retries)
   */
  private async loadMoreClientsPage(
    vendorId: string,
    groupId?: string,
    distributorId?: string,
  ): Promise<void> {
    const contextDistributorId =
      distributorId ?? this.getEffectiveDistributorId();
    if (isNullOrUndefined(contextDistributorId)) {
      console.warn('No context available for loading more clients');
      return;
    }

    const paginationKey = this.adminState.getDistributorPaginationKey(
      contextDistributorId,
      vendorId,
      groupId,
    );
    const currentPagination =
      this.adminState.clientPagination()[contextDistributorId]?.[paginationKey];

    if (!currentPagination?.hasMore || currentPagination.loadingMore) {
      return;
    }

    const nextOffset = currentPagination.offset + currentPagination.limit;

    try {
      this.adminState.setDistributorLoadingMore(
        contextDistributorId,
        vendorId,
        groupId,
        true,
      );

      const clientResponse = await this.adminTreeService.listVendorClients(
        vendorId,
        groupId,
        nextOffset,
        100,
      );

      const paginationUpdate = {
        offset: nextOffset,
        limit: 100,
        total: clientResponse.total,
        hasMore: clientResponse.hasMore,
      };

      if (isDefined(groupId)) {
        this.adminState.addGroupClients(
          contextDistributorId,
          vendorId,
          groupId,
          clientResponse.clients,
          paginationUpdate,
          true, // Append results
        );
      } else {
        this.adminState.addDistributorVendorClients(
          contextDistributorId,
          vendorId,
          clientResponse.clients,
          paginationUpdate,
          true, // Append results
        );
      }
    } catch (error) {
      // Handle 403 errors specifically
      if (error instanceof HttpErrorResponse && error.status === 403) {
        console.warn('Access denied when loading more clients (403)');
        // Update permission level if not already set
        if (this.adminState.userPermissionLevel() === null) {
          this.adminState.setUserPermissionLevel(
            UserPermissionLevel.CLIENT_ONLY,
          );
        }
      }

      console.error('Failed to load more clients:', error);
      // Mark as no more data to prevent retry loops on persistent errors
      this.adminState.setDistributorLoadingMore(
        contextDistributorId,
        vendorId,
        groupId,
        false,
      );
      // Update pagination to indicate no more data available due to error
      const errorPaginationUpdate = {
        offset: nextOffset,
        limit: 100,
        total: currentPagination.total,
        hasMore: false, // Stop trying to load more after an error
      };
      if (isDefined(groupId)) {
        this.adminState.addGroupClients(
          contextDistributorId,
          vendorId,
          groupId,
          [], // Empty array - just updating pagination
          errorPaginationUpdate,
          true, // Append to preserve existing clients
        );
      } else {
        this.adminState.addDistributorVendorClients(
          contextDistributorId,
          vendorId,
          [], // Empty array - just updating pagination
          errorPaginationUpdate,
          true, // Append to preserve existing clients
        );
      }
      // Re-throw the error so the caller knows the load failed
      throw error;
    }
  }

  /**
   * Finds a client in the currently loaded data within distributor context
   */
  private findClientInLoadedData(
    clientId: string,
    vendorId: string,
    groupId?: string,
    distributorId?: string,
  ): ClientListItem | undefined {
    const contextDistributorId =
      distributorId ?? this.getEffectiveDistributorId();
    if (isNullOrUndefined(contextDistributorId)) {
      return undefined;
    }

    if (isDefined(groupId)) {
      // Search in group clients using context-scoped data
      const groupClients =
        this.adminState.groupClients()[contextDistributorId]?.[vendorId]?.[
          groupId
        ];
      return groupClients?.find((client) => client.id === clientId);
    } else {
      // Search in vendor clients using context-scoped data
      const vendorClients =
        this.adminState.vendorClients()[contextDistributorId]?.[vendorId];
      return vendorClients?.find((client) => client.id === clientId);
    }
  }

  /**
   * Searches for and loads a client using the global search API
   * @param clientId The ID of the client to find
   * @param _searchContext Optional context about where to look (vendor/group) - not used in global search
   * @returns Promise that resolves to true if client was found and loaded
   */
  async searchAndLoadClient(
    clientId: string,
    _searchContext?: { vendorId?: string; groupId?: string },
  ): Promise<boolean> {
    // Use global search to find the client first
    try {
      const searchResponse = await firstValueFrom(
        this.cacheService.getOrFetch(
          `admin-search-${clientId}`,
          () =>
            this.searchService
              .searchEntities(clientId)
              .pipe(catchError(() => of(null))),
          30000,
        ),
      );

      if (searchResponse?.items) {
        const clientEntity = searchResponse.items.find(
          (item) => item.id === clientId,
        );
        if (clientEntity) {
          const result = this.mapEntityToTreeResult(clientEntity);
          await this.ensureParentNodesLoaded(result);

          // Ensure the client itself is loaded if it's a client entity
          // This handles deep pagination - client might be on page 10+
          if (result.type === ADMIN_TREE.NODE_TYPES.CLIENT) {
            const hierarchy = this.extractHierarchyFromResult(result);
            if (isDefined(hierarchy.vendorId)) {
              await this.loadClientsUntilFound(
                clientId,
                hierarchy.vendorId,
                hierarchy.groupId,
              );
            }
          }

          return true;
        }
      }
    } catch (error) {
      console.error('Failed to search and load client:', error);
    }

    return false;
  }
  // #endregion

  // #region ========== UTILITIES & PRELOADING SEARCH DATA ==========

  /**
   * Preloads search data for better performance
   * This method is called from AdminDataService for background preloading
   */
  async preloadSearchData(): Promise<void> {
    const effectiveDistributorId = this.getEffectiveDistributorId();
    if (isNullOrUndefined(effectiveDistributorId)) {
      // No context available for preloading - this is okay, search will still work
      return;
    }

    // With global search, we don't need to preload data as much
    // But we can still preload some basic vendor/group data if needed
    try {
      const distributorVendors =
        this.adminState.distributorVendors()[effectiveDistributorId] ?? [];

      // Preload basic vendor data for common operations
      const preloadPromises = distributorVendors.map(async (vendor) => {
        try {
          if (
            !this.adminState.isDistributorVendorLoaded(
              effectiveDistributorId,
              vendor.id,
            )
          ) {
            await this.loadVendorData(vendor.id, effectiveDistributorId);
          }
        } catch (error) {
          console.warn(
            `Failed to preload vendor data for ${vendor.id}:`,
            error,
          );
        }
      });

      await Promise.all(preloadPromises);
    } catch (error) {
      console.warn('Failed to preload search data:', error);
    }
  }
  // #endregion
}
