import { CommonModule } from '@angular/common';
import {
  ChangeDetectorRef,
  Component,
  DestroyRef,
  OnInit,
  TemplateRef,
  effect,
  inject,
  signal,
  viewChild,
} from '@angular/core';
import { takeUntilDestroyed } from '@angular/core/rxjs-interop';
import { ReactiveFormsModule } from '@angular/forms';

import { ButtonModule } from 'primeng/button';
import { DynamicDialogRef, DynamicDialogConfig } from 'primeng/dynamicdialog';
import { finalize } from 'rxjs';

import {
  EntitiesService,
  GeoLockProfileCreateRequest,
  GeoLockProfileResponse,
} from '../../../../../api';
import { WizardComponent } from '../../../../../shared/components/wizard/wizard.component';
import { isDefined, isNullOrUndefined } from '../../../../../shared/utils';
import { BroadcastEventMessageService } from '../../../services/broadcast-event-messages.service';
import { GeoLockProfileDetailsSectionComponent } from '../geo-lock-profile-details-section/geo-lock-profile-details-section.component';
import { MeasurementUnitType, Step } from '../../../../../shared/models';
import { MeasurementUnitsService } from '../../../../../shared/services/measurement-units/measurement-units.service';

@Component({
  selector: 'app-geo-lock-profile-create-wizard',
  imports: [
    CommonModule,
    ReactiveFormsModule,
    WizardComponent,
    ButtonModule,
    GeoLockProfileDetailsSectionComponent,
  ],
  templateUrl: './geo-lock-profile-create-wizard.component.html',
  styleUrl: './geo-lock-profile-create-wizard.component.scss',
})
export class GeoLockProfileCreateWizardComponent implements OnInit {
  geoLockProfileDetailsStep = viewChild<
    TemplateRef<{
      $implicit: Step;
    }>
  >('geoLockProfileDetailsStep');
  confirmationTemplate = viewChild<TemplateRef<{ $implicit: Step }>>(
    'confirmationTemplate',
  );
  private readonly cdr = inject(ChangeDetectorRef);

  // References to form components
  geoLockProfileDetailsForm = viewChild<GeoLockProfileDetailsSectionComponent>(
    'geoLockProfileDetailsForm',
  );

  private readonly entitiesService = inject(EntitiesService);
  private readonly broadcastService = inject(BroadcastEventMessageService);
  protected readonly destroyRef = inject(DestroyRef);
  private readonly dialogRef = inject(DynamicDialogRef);
  private readonly dialogConfig = inject(DynamicDialogConfig);
  readonly measurementUnitsService = inject(MeasurementUnitsService);

  // GeoLockProfile model to collect data across steps
  newGeoLockProfile = signal<Partial<GeoLockProfileCreateRequest>>({});

  // Form validity state
  formIsValid = signal<boolean>(false);

  displayWizard = false;
  steps: Step[] = [];
  ownerId: string | null = null;
  isSubmitting = signal<boolean>(false);

  constructor() {
    effect(() => {
      const geoLockProfileDetailsStep = this.geoLockProfileDetailsStep();
      const confirmationTemplate = this.confirmationTemplate();

      if (
        isNullOrUndefined(geoLockProfileDetailsStep) ||
        isNullOrUndefined(confirmationTemplate)
      ) {
        return;
      }
      this.steps = [
        {
          value: 1,
          header: 'Geo Lock Profile Details',
          content: geoLockProfileDetailsStep,
          complete: false,
          validate: () => this.geoLockProfileDetailsForm()?.validate() ?? false,
        },
        {
          value: 2,
          header: 'Confirmation',
          content: confirmationTemplate,
          complete: true,
        },
      ];
    });
  }

  ngOnInit(): void {
    if (isDefined(this.dialogConfig.data)) {
      this.ownerId = this.dialogConfig.data.ownerId ?? null;

      // Initialize geoLockProfile with ownerId
      this.newGeoLockProfile.update((current) => ({
        ...current,
        ownerId: this.ownerId ?? '',
      }));
    }
  }

  openWizard() {
    this.displayWizard = true;
    this.newGeoLockProfile.set({});
  }

  // Form validity handlers
  onDetailsFormValidityChange(isValid: boolean) {
    this.steps[0].disableNext = !isValid;
    this.formIsValid.set(isValid);
  }

  // Update the geoLockProfile state with form data
  updateGeoLockProfileDetails(formData: Record<string, unknown>) {
    this.newGeoLockProfile.update((current) => {
      const updated = { ...current };

      Object.keys(formData).forEach((key) => {
        if (key !== 'ownerId') {
          updated[key] = formData[key];
        }
      });

      if (isNullOrUndefined(updated.ownerId)) {
        updated.ownerId = this.ownerId ?? '';
      }

      return updated;
    });
  }

  onWizardComplete(): void {
    // Process the final data from all steps
    const geoLockProfileData =
      this.newGeoLockProfile() as GeoLockProfileCreateRequest;
    this.isSubmitting.set(true);

    this.entitiesService
      .createGeoLockProfile(geoLockProfileData)
      .pipe(
        takeUntilDestroyed(this.destroyRef),
        finalize(() => this.isSubmitting.set(false)),
      )
      .subscribe({
        next: (response: GeoLockProfileResponse) => {
          this.broadcastService.broadcast('entity-updated', {
            entityType: 'geo-lock-profile',
            operation: 'create',
            entityId: response.id,
          });
          this.dialogRef.close(response);
        },
        error: (error: Error) => {
          // Handle error, could show message and keep dialog open
          console.error('Error creating geo lock profile:', error);
        },
      });
  }

  // Update cancel to close dialog with no result
  onWizardCancel(): void {
    this.newGeoLockProfile.set({});
    this.dialogRef.close();
  }

  getRadius(): number | null {
    const radius = this.newGeoLockProfile().radiusKm;
    return isDefined(radius)
      ? this.measurementUnitsService.fromBackend(
          MeasurementUnitType.DISTANCE,
          radius,
          2,
        )
      : null;
  }
}
