import { CommonModule } from '@angular/common';
import { Component, inject, input } from '@angular/core';
import { Validators } from '@angular/forms';

import {
  ZoneCreateRequest,
  ZoneListItem,
  ZoneResponse,
  ZoneUpdateRequest,
  EntitiesService,
} from '../../../../../api';
import { DynamicFormComponent } from '../../../../../shared/components/forms/dynamic-form/dynamic-form.component';
import {
  AssetGroupingTreeItem,
  createDropdownField,
  createNumberField,
  MeasurementUnitType,
} from '../../../../../shared/models';
import { TypedEntitySectionBaseComponent } from '../../base';
import { MeasurementUnitsService } from '../../../../../shared/services/measurement-units/measurement-units.service';
import { Observable } from 'rxjs';

@Component({
  selector: 'app-geofence-coordinates-section',
  imports: [CommonModule, DynamicFormComponent],
  templateUrl: './geofence-coordinates-section.component.html',
  styleUrl: './geofence-coordinates-section.component.scss',
})
export class GeofenceCoordinatesSectionComponent extends TypedEntitySectionBaseComponent<
  ZoneListItem,
  ZoneUpdateRequest,
  Partial<ZoneCreateRequest>,
  ZoneResponse
> {
  readonly ownerId = input<string | null>(null);
  private readonly measurementUnitsService = inject(MeasurementUnitsService);
  private readonly entitiesService = inject(EntitiesService);

  zoneGroups: AssetGroupingTreeItem[] = [];
  costCenters: AssetGroupingTreeItem[] = [];

  constructor() {
    super();
  }

  protected override updateFormFields(): void {
    const geofence = this.entity();
    this.formFields = [
      createNumberField('lat', 'Latitude', geofence?.['lat'] ?? undefined, {
        name: 'lat',
        validations: [
          {
            name: 'required',
            validator: Validators.required,
            message: 'Latitude is required',
          },
          {
            name: 'min',
            validator: Validators.min(-90),
            message: 'Latitude must be greater or equal to -90',
          },
          {
            name: 'max',
            validator: Validators.max(90),
            message: 'Latitude must be less or equal to 90',
          },
        ],
        props: {
          appendTo: 'body',
          showClear: true,
          min: -90,
          max: 90,
          step: 0.00001,
          maxFractionDigits: 5,
        },
      }),
      createNumberField('lon', 'Longitude', geofence?.['lon'], {
        name: 'lon',
        validations: [
          {
            name: 'required',
            validator: Validators.required,
            message: 'Longitude is required',
          },
          {
            name: 'min',
            validator: Validators.min(-180),
            message: 'Longitude must be greater or equal to -180',
          },
          {
            name: 'max',
            validator: Validators.max(180),
            message: 'Longitude must be less or equal to 180',
          },
        ],
        props: {
          appendTo: 'body',
          showClear: true,
          min: -180,
          max: 180,
          step: 0.00001,
          maxFractionDigits: 5,
        },
      }),
      createDropdownField(
        'shape',
        'Shape',
        [
          { label: 'Hexagon', value: 'hexagon' },
          { label: 'Octagon', value: 'octagon' },
          { label: 'Square', value: 'square' },
          { label: 'Triangle', value: 'triangle' },
        ],
        geofence?.['shape'] ?? '',
        {
          name: 'shape',
          validations: [
            {
              name: 'required',
              validator: Validators.required,
              message: 'Shape is required',
            },
          ],
          props: {
            appendTo: 'body',
            showClear: true,
          },
        },
      ),
      createNumberField('shapeRadius', 'Radius', geofence?.['shapeRadius'], {
        name: 'shapeRadius',
        validations: [
          {
            name: 'required',
            validator: Validators.required,
            message: 'Radius is required',
          },
          {
            name: 'min',
            validator: Validators.min(0),
            message: 'Radius must be greater or equal to 0',
          },
        ],
        props: {
          appendTo: 'body',
          showClear: true,
          suffix: this.measurementUnitsService.unitSymbol(
            MeasurementUnitType.DISTANCE,
          ),
        },
      }),
    ];
  }

  override getUpdateMethod(): (
    id: string,
    data: ZoneUpdateRequest,
  ) => Observable<ZoneResponse> {
    return (id, data) => this.entitiesService.updateZone(id, data);
  }

  protected override transformFormData(
    formData: Record<string, unknown> | Record<string, string | number>[],
  ): ZoneUpdateRequest {
    // Handle both possible input types
    const normalizedFormData = Array.isArray(formData)
      ? formData.reduce((acc, item) => ({ ...acc, ...item }), {})
      : formData;

    // Transform the coordinate-specific form data
    const transformedData: Partial<ZoneUpdateRequest> = {};

    // Map coordinate fields
    if (normalizedFormData['lat'] !== undefined) {
      transformedData['lat'] = normalizedFormData['lat'] as number;
    }

    if (normalizedFormData['lon'] !== undefined) {
      transformedData['lon'] = normalizedFormData['lon'] as number;
    }

    if (normalizedFormData['shape'] !== undefined) {
      transformedData['shape'] = normalizedFormData['shape'] as string;
    }

    if (normalizedFormData['shapeRadius'] !== undefined) {
      // Convert the radius to backend units
      const radiusValue = normalizedFormData['shapeRadius'] as number;
      transformedData['shapeRadius'] = this.measurementUnitsService.toBackend(
        MeasurementUnitType.DISTANCE,
        radiusValue,
      );
    }

    // For coordinate updates, we typically need the zone ID
    const entity = this.entity();
    if (entity && 'id' in entity) {
      transformedData['id'] = (entity as Record<string, unknown>)[
        'id'
      ] as string;
    }

    // Add ownerId if available and not null/undefined
    const ownerId = this.ownerId();
    if (ownerId !== null && ownerId !== undefined) {
      transformedData['ownerId'] = ownerId;
    }

    return transformedData as ZoneUpdateRequest;
  }
}
