import { Injectable } from '@angular/core';
import { TreeNode } from 'primeng/api';
import { ClientResponse } from '../../../../api';

/**
 * Service responsible for tree node operations and traversal.
 * Provides methods for finding and manipulating nodes in the administration tree.
 */
@Injectable({
  providedIn: 'root',
})
export class AdminTreeNodeService {
  /**
   * Finds the parent vendor node of a given target node in the tree
   * @param nodes - Array of tree nodes to search through
   * @param targetNode - The node whose parent vendor is being searched for
   * @returns The parent vendor TreeNode if found, null otherwise
   */
  findParentVendorNode(
    nodes: TreeNode[],
    targetNode: TreeNode,
  ): TreeNode | null {
    for (const node of nodes) {
      if (!node.children) continue;

      const found = this.searchChildNodes(node, targetNode);
      if (found) return node;

      for (const child of node.children) {
        if (!child.children) continue;
        const foundInChild = this.findParentVendorNode([child], targetNode);
        if (foundInChild) return foundInChild;
      }
    }
    return null;
  }

  private searchChildNodes(node: TreeNode, targetNode: TreeNode): boolean {
    return node.children?.includes(targetNode) ?? false;
  }

  /**
   * Finds a node in the tree that corresponds to the selected client
   * @param nodes - Array of tree nodes to search through
   * @param selectedClient - The client to search for
   * @returns The matching TreeNode if found, null otherwise
   */
  findSelectedNode(
    nodes: TreeNode[],
    selectedClient: ClientResponse | null,
  ): TreeNode | null {
    if (!selectedClient) return null;
    return this.searchNode(nodes, selectedClient.id);
  }

  /**
   * Recursively searches for a node with matching client ID
   * @param nodes - Array of tree nodes to search through
   * @param clientId - The client ID to search for
   * @returns The matching TreeNode if found, null otherwise
   */
  private searchNode(nodes: TreeNode[], clientId: string): TreeNode | null {
    for (const node of nodes) {
      if (node.key === clientId) {
        return node;
      }
      if (node.children) {
        const found = this.searchNode(node.children, clientId);
        if (found) return found;
      }
    }
    return null;
  }

  /**
   * Finds a node in the tree by key, optionally filtering by type
   * @param nodes - Array of tree nodes to search through
   * @param key - The node key to search for
   * @param type - Optional node type to match
   * @returns The matching TreeNode if found, null otherwise
   */
  findNodeByKey(
    nodes: TreeNode[],
    key: string,
    type?: string,
  ): TreeNode | null {
    for (const node of nodes) {
      if (node.key === key && (type === undefined || node.type === type)) {
        return node;
      }
      if (node.children) {
        const found = this.findNodeByKey(node.children, key, type);
        if (found) return found;
      }
    }
    return null;
  }
}
