import {
  DestroyRef,
  Injectable,
  computed,
  inject,
  signal,
} from '@angular/core';

import { TableLazyLoadEvent } from 'primeng/table';
import { catchError, EMPTY, firstValueFrom } from 'rxjs';

import {
  GridConfiguration,
  GridDataService,
  DataGridParams,
  GridColumn,
  FilterCriteria,
  FilterValue,
  DATE_FORMATS,
} from '../../../shared/models';
import { UtilityService } from '../../../core/services/utility.service';
import { GridBaseService } from '../../../shared/services/grid-base.service';
import { isDefined, isEmpty, isNullOrUndefined } from '../../../shared/utils';
import { DeviceConfigProfilesDataAdapter } from '../services/adapters';
import {
  EntitiesService,
  DeviceConfigProfileListItem,
  IdName,
  DeviceTypeListItem,
} from '../../../api';
import { DEFAULT_PAGE_SIZE } from '../../../core/models';
import { AdminStateService } from '../services/state/admin-state.service';
import { AdminTreeService } from '../services/tree/admin-tree.service';
import { DynamicDialogRef, DialogService } from 'primeng/dynamicdialog';
import { takeUntilDestroyed } from '@angular/core/rxjs-interop';
import { DeviceConfigProfileCreateWizardComponent } from '../components/device-config-profiles/device-config-profile-create-wizard/device-config-profile-create-wizard.component';
import { MeasurementDateTimeService } from '../../../shared/services/measurement-units/measurement-date-time.service';

@Injectable({
  providedIn: 'root',
})
export class DeviceConfigProfilesGridConfig
  implements GridConfiguration<DeviceConfigProfileListItem>
{
  private readonly deviceConfigProfilesService = inject(
    DeviceConfigProfilesDataAdapter,
  );
  private readonly utilityService = inject(UtilityService);
  private readonly gridBaseService = inject(GridBaseService);
  private readonly entitiesService = inject(EntitiesService);
  private readonly adminStateService = inject(AdminStateService);
  private readonly adminTreeService = inject(AdminTreeService);
  private dialogRef: DynamicDialogRef | null = null;
  private readonly dialogService = inject(DialogService);
  private readonly measurementDateTimeService = inject(
    MeasurementDateTimeService,
  );

  private readonly defaultStateValues = [
    DeviceConfigProfileListItem.StateEnum.Active,
  ];
  private readonly filterState = signal<Record<string, FilterCriteria>>({
    state: {
      value: this.defaultStateValues,
      operator: 'in',
    },
  });
  private params: Record<string, string> = { ownerId: '' };
  private ownerId = '';
  private readonly destroyRef = inject(DestroyRef);

  private readonly rqlFilter = computed(() =>
    this.utilityService.constructRqlFilter(this.filterState()),
  );
  private readonly fieldTransformations: Record<string, string> = {
    type: 'deviceType.id',
  };
  private readonly stateLabels: Record<
    DeviceConfigProfileListItem.StateEnum,
    string
  > = {
    active: 'Active',
    deleted: 'Deleted',
  };

  entityType = 'device-config-profiles';
  columns: GridColumn<DeviceConfigProfileListItem>[] = [
    {
      field: 'name',
      header: 'Name',
      filter: true,
      filterType: 'text',
      filterMatchMode: 'contains',
    },
    {
      field: 'type',
      header: 'Type',
      sortable: true,
      filterType: 'multiselect',
      filter: true,
      filterMatchMode: 'in',
      valueGetter: (rowData: DeviceConfigProfileListItem) => {
        return rowData.deviceType?.name ?? '';
      },
      filterConfig: {
        type: 'multiselect',
        options: [],
      },
    },
    {
      field: 'state',
      header: 'State',
      valueGetter: (rowData: DeviceConfigProfileListItem) =>
        isDefined(rowData.state) ? this.stateLabels[rowData.state] : '',
      filter: true,
      filterType: 'multiselect',
      filterMatchMode: 'in',
      filterConfig: {
        type: 'multiselect',
        options: [
          {
            label: 'Active',
            value: DeviceConfigProfileListItem.StateEnum.Active,
          },
          {
            label: 'Deleted',
            value: DeviceConfigProfileListItem.StateEnum.Deleted,
          },
        ],
        defaultSelected: this.defaultStateValues,
      },
    },
    {
      field: 'modifiedDate',
      header: 'Modified',
      filter: false,
      valueGetter: (rowData: DeviceConfigProfileListItem) =>
        isDefined(rowData.modifiedDate)
          ? this.measurementDateTimeService.formatDateTime(
              this.measurementDateTimeService.createDateAssumingUtc(
                rowData['modifiedDate'],
              ),
              DATE_FORMATS.DATE_TIME_SECONDS,
            )
          : '',
    },
  ];

  dataService: GridDataService<DeviceConfigProfileListItem> =
    this.deviceConfigProfilesService;

  getFilters(): Record<string, FilterCriteria> {
    return this.filterState();
  }

  clearFilters(): void {
    this.filterState.set({
      state: {
        value: this.defaultStateValues,
        operator: 'in',
      },
    });
  }

  initialize(params: Record<string, string>) {
    this.params = params;
    if (isDefined(this.params['clientId'])) {
      this.ownerId = String(this.params['clientId']);
    } else if (isDefined(this.params['vendorId'])) {
      this.ownerId = String(this.params['vendorId']);
    }
    this.loadFilterOptions().catch(() => {});
  }

  getTitle(): string {
    return 'Device Config Profiles';
  }

  getRequestParams(event?: TableLazyLoadEvent): DataGridParams {
    let sortField = 'name:asc'; // Default

    if (isDefined(event?.sortField)) {
      const sortOrder = event.sortOrder === 1 ? 'asc' : 'desc';
      sortField = `${event.sortField}:${sortOrder}`;
    }

    return {
      owner: this.ownerId,
      offset: isDefined(event?.first) ? event.first : 0,
      recurse: false,
      limit: isDefined(event?.rows) ? event.rows : DEFAULT_PAGE_SIZE,
      sortField: sortField,
      filters: this.rqlFilter(),
    };
  }

  onFilterChange(field: string, value: FilterValue): void {
    const actualField = this.fieldTransformations[field] || field;
    const column = this.columns.find((col) => col.field === field);

    if (Array.isArray(value)) {
      const newFilters = this.gridBaseService.handleFilters(
        this.filterState(),
        actualField,
        value,
        undefined,
        column?.filterMatchMode,
      );

      this.filterState.set(newFilters);
      return;
    }

    // For non-array values, use the grid base service
    const newFilters = this.gridBaseService.handleFilters(
      this.filterState(),
      actualField,
      value,
      undefined,
      column?.filterMatchMode,
    );

    this.filterState.set(newFilters);
  }

  onAdd(): void {
    this.openDeviceConfigProfileCreationDialog(this.ownerId)
      .pipe(takeUntilDestroyed(this.destroyRef))
      .subscribe((createdDeviceConfigProfile) => {
        if (isDefined(createdDeviceConfigProfile)) {
          // Refresh your deviceConfigProfile list or take any other necessary actions
          // this.refreshDeviceConfigProfiles();
        }
      });
  }

  openDeviceConfigProfileCreationDialog(ownerId: string) {
    this.dialogRef = this.dialogService.open(
      DeviceConfigProfileCreateWizardComponent,
      {
        header: 'Device Config Profile Wizard',
        contentStyle: { overflow: 'auto' },
        baseZIndex: 10000,
        data: { ownerId: ownerId },
        position: 'top',
        modal: true,
      },
    );

    return (this.dialogRef as DynamicDialogRef).onClose;
  }

  private async loadFilterOptions(): Promise<void> {
    try {
      // Get device types from the API
      const deviceTypesResponse = await firstValueFrom(
        this.entitiesService.listDeviceTypes(this.ownerId).pipe(
          catchError(() => {
            console.error('Failed to load device types');
            return EMPTY;
          }),
        ),
      );

      if (
        isNullOrUndefined(deviceTypesResponse) ||
        isEmpty(deviceTypesResponse.items)
      ) {
        console.warn('No device types found for client');
        return;
      }

      const deviceTypes = deviceTypesResponse.items;
      let allowedRootDeviceTypeIds =
        await this.getAvailableRootDeviceTypeIdsForClient();

      if (isEmpty(allowedRootDeviceTypeIds)) {
        allowedRootDeviceTypeIds =
          await this.getAvailableRootDeviceTypeIdsForVendor();
      }

      const typeColumn = this.columns.find((col) => col.field === 'type');
      if (typeColumn?.filterConfig) {
        const exceptionDeviceTypeIds: string[] = [];

        const filteredOptions = this.filterAllowedDeviceTypes(
          this.ownerId,
          deviceTypes,
          allowedRootDeviceTypeIds,
          exceptionDeviceTypeIds,
        );
        typeColumn.filterConfig.options = filteredOptions.map((option) => ({
          label: option.value,
          value: option.key,
        }));
      }
    } catch (error) {
      console.error('Error loading device type filter options:', error);
    }
  }

  /**
   * Gets the available root device type IDs for the current client
   * @returns Promise resolving to an array of device type IDs
   */
  private async getAvailableRootDeviceTypeIdsForClient(): Promise<string[]> {
    if (isNullOrUndefined(this.params['clientId'])) {
      return [];
    }

    // First check the admin state service
    const client = this.adminStateService.getClientById(
      this.params['clientId'],
    );

    if (client && isDefined(client.availableDeviceTypes)) {
      return client.availableDeviceTypes.map(
        (deviceType: IdName) => deviceType.id,
      );
    }

    // If client not found in state, try loading from service
    try {
      const fetchedClient = await this.adminTreeService.tryLoadClient(
        this.params['clientId'],
      );

      if (fetchedClient?.availableDeviceTypes) {
        return fetchedClient.availableDeviceTypes.map(
          (deviceType: IdName) => deviceType.id,
        );
      }
    } catch (error) {
      console.error('Error loading client device types:', error);
    }

    return [];
  }

  /**
   * Gets the available root device type IDs for a selected vendor
   * @returns Promise resolving to an array of device type IDs
   */
  private async getAvailableRootDeviceTypeIdsForVendor(): Promise<string[]> {
    const clientId = this.params['clientId'];
    let client = null;
    let vendorId = null;
    if (isDefined(clientId)) {
      client =
        this.adminStateService.getClientById(clientId) ??
        (await this.adminTreeService.tryLoadClient(clientId));
      vendorId = client ? client.owner.id : null;
    } else {
      vendorId = this.params['vendorId'];
    }

    if (isNullOrUndefined(vendorId)) {
      return [];
    }
    // First check the admin state service
    const vendor = this.adminStateService.getVendorById(vendorId);

    if (vendor && isDefined(vendor.availableDeviceTypes)) {
      return vendor.availableDeviceTypes.map(
        (deviceType: IdName) => deviceType.id,
      );
    }

    // If vendor not found in state, try loading from service
    try {
      const fetchedVendor =
        await this.adminTreeService.fetchClientVendor(vendorId);

      if (fetchedVendor && isDefined(fetchedVendor.availableDeviceTypes)) {
        return fetchedVendor.availableDeviceTypes.map(
          (deviceType: IdName) => deviceType.id,
        );
      }
    } catch (error) {
      console.error('Error loading vendor device types:', error);
    }

    return [];
  }

  /**
   * Filters device types based on allowed types and exceptions,
   * and formats them for dropdown selection
   *
   * @param ownerId - ID of the owner (client/vendor)
   * @param deviceTypes - List of device types to filter
   * @param allowedTypes - List of allowed root device type IDs
   * @param exceptions - Device type IDs that are always allowed
   * @returns Array of formatted key-value pairs for dropdown options
   */
  private filterAllowedDeviceTypes(
    ownerId: string,
    deviceTypes: DeviceTypeListItem[],
    allowedTypes: string[],
    exceptions: string[],
  ): { key: string; value: string }[] {
    return deviceTypes
      .filter((item) => {
        // Always include exceptions
        if (exceptions.includes(item.id)) {
          return true;
        }

        // Otherwise, check if it's active, has a parent, and meets ownership criteria
        return (
          item.state === 'active' &&
          isDefined(item.parent) &&
          (item.owner.id === ownerId ||
            allowedTypes.length === 0 ||
            allowedTypes.includes(item.parent.id))
        );
      })
      .map((item) => {
        // Find the parent device type name
        const parentName =
          deviceTypes.find((x) => x.id === item?.parent?.id)?.name ?? 'Unknown';

        return {
          key: item.id,
          value: `${parentName} - ${item.name}`,
        };
      })
      .sort((a, b) => a.value.localeCompare(b.value));
  }
}
