import { CommonModule } from '@angular/common';
import {
  ChangeDetectorRef,
  Component,
  DestroyRef,
  OnInit,
  TemplateRef,
  effect,
  inject,
  signal,
  viewChild,
} from '@angular/core';
import { takeUntilDestroyed } from '@angular/core/rxjs-interop';
import { ReactiveFormsModule } from '@angular/forms';

import { ButtonModule } from 'primeng/button';
import { DynamicDialogRef, DynamicDialogConfig } from 'primeng/dynamicdialog';

import {
  ClientCreateRequest,
  ClientResponse,
  AccountsService,
} from '../../../../../api';
import { WizardComponent } from '../../../../../shared/components/wizard/wizard.component';
import { isDefined, isNullOrUndefined } from '../../../../../shared/utils';
import { Step } from '../../../../../shared/models';
import { BroadcastEventMessageService } from '../../../services/broadcast-event-messages.service';
import { ClientDetailsComponent } from '../../clients/client-details/client-details/client-details.component';
import { RequestCacheService } from '../../../../../shared/services/request-cache.service';
import { catchError, finalize, of } from 'rxjs';
import { DEFAULT_SYSTEM_ID } from '../../../../../core/models';

@Component({
  selector: 'app-vendor-client-create-wizard',
  imports: [
    CommonModule,
    ReactiveFormsModule,
    WizardComponent,
    ButtonModule,
    ClientDetailsComponent,
  ],
  templateUrl: './vendor-client-create-wizard.component.html',
  styleUrl: './vendor-client-create-wizard.component.scss',
})
export class VendorClientCreateWizardComponent implements OnInit {
  clientDetailsStep = viewChild<
    TemplateRef<{
      $implicit: Step;
    }>
  >('clientDetailsStep');
  confirmationTemplate = viewChild<TemplateRef<{ $implicit: Step }>>(
    'confirmationTemplate',
  );

  // References to form components
  clientDetailsForm = viewChild<ClientDetailsComponent>('clientDetailsForm');

  private readonly accountsService = inject(AccountsService);
  private readonly broadcastService = inject(BroadcastEventMessageService);
  protected readonly destroyRef = inject(DestroyRef);
  private readonly dialogRef = inject(DynamicDialogRef);
  private readonly dialogConfig = inject(DynamicDialogConfig);
  private readonly cacheService = inject(RequestCacheService);
  private readonly cdr = inject(ChangeDetectorRef);

  // Client model to collect data across steps
  newClient = signal<Partial<ClientCreateRequest>>({});

  // Form validity state
  formIsValid = signal<boolean>(false);

  displayWizard = false;
  steps: Step[] = [];
  previousStepIndex = 0;
  currentStepIndex = 0;
  ownerId: string | null = null;
  companyGroups: { label: string; value: string }[] = [];
  isSubmitting = signal<boolean>(false);

  constructor() {
    effect(() => {
      const clientDetailsStep = this.clientDetailsStep();
      const confirmationTemplate = this.confirmationTemplate();

      if (
        isNullOrUndefined(clientDetailsStep) ||
        isNullOrUndefined(confirmationTemplate)
      ) {
        return;
      }
      this.steps = [
        {
          value: 1,
          header: 'Client Details',
          content: clientDetailsStep,
          complete: false,
          validate: () => this.clientDetailsForm()?.validate() ?? false,
        },
        {
          value: 2,
          header: 'Confirmation',
          content: confirmationTemplate,
          complete: true,
        },
      ];
    });
  }

  ngOnInit(): void {
    if (isDefined(this.dialogConfig.data)) {
      this.ownerId = this.dialogConfig.data.ownerId ?? null;

      // Initialize client with ownerId
      this.newClient.update((current) => ({
        ...current,
        ownerId: this.ownerId ?? '',
      }));

      this.cacheService
        .getOrFetch(
          `company-groups-${this.ownerId}`,
          () => this.accountsService.listCompanyGroups(this.ownerId as string),
          300000, // 5 minutes cache TTL
        )
        .pipe(
          takeUntilDestroyed(this.destroyRef),
          catchError(() => {
            return of({ items: [] });
          }),
        )
        .subscribe((data) => {
          this.companyGroups = [
            { value: DEFAULT_SYSTEM_ID, label: 'None' },
            ...data.items
              .map((item) => ({
                label: item.name ?? '',
                value: item.id ?? '',
              }))
              .sort((a, b) => a.label.localeCompare(b.label)),
          ];
        });
    }
  }

  openWizard() {
    this.displayWizard = true;
    this.newClient.set({});
  }

  // Form validity handlers
  onDetailsFormValidityChange(isValid: boolean) {
    this.steps[0].disableNext = !isValid;
    this.formIsValid.set(isValid);
  }

  // Update the client state with form data
  updateClientDetails(formData: Record<string, unknown>) {
    this.newClient.update((current) => {
      const updated = { ...current };

      Object.keys(formData).forEach((key) => {
        if (key !== 'ownerId') {
          updated[key] = formData[key];
        }
      });

      if (isNullOrUndefined(updated.ownerId)) {
        updated.ownerId = this.ownerId ?? '';
      }

      return updated;
    });
  }

  onWizardComplete(): void {
    // Process the final data from all steps
    const clientData = this.newClient() as ClientCreateRequest;
    this.isSubmitting.set(true);

    this.accountsService
      .createClient(clientData)
      .pipe(
        takeUntilDestroyed(this.destroyRef),
        finalize(() => this.isSubmitting.set(false)),
      )
      .subscribe({
        next: (response: ClientResponse) => {
          this.broadcastService.broadcast('entity-updated', {
            entityType: 'client',
            operation: 'create',
            entityId: response.id,
          });
          this.broadcastService.broadcast('tree-refresh-required', {
            entityType: 'client',
            operation: 'create',
            entityId: response.id,
          });
          this.dialogRef.close(response);
        },
        error: (error: Error) => {
          // Handle error, could show message and keep dialog open
          console.error('Error creating client:', error);
        },
      });
  }

  // Update cancel to close dialog with no result
  onWizardCancel(): void {
    this.newClient.set({});
    this.dialogRef.close();
  }

  getGroupName(groupId: string | null | undefined): string {
    if (isNullOrUndefined(groupId) || groupId === '') {
      return 'Not provided';
    }
    if (groupId === DEFAULT_SYSTEM_ID) {
      return 'None';
    }
    const group = this.companyGroups.find((g) => g.value === groupId);
    return isDefined(group) ? group.label : 'Unknown Group';
  }
}
