import { inject, Injectable } from '@angular/core';
import { TreeNode } from 'primeng/api';
import { AdminStateService } from '../state/admin-state.service';
import { AdminTreeVendorService } from './admin-tree-vendor.service';
import { ADMIN_TREE } from '../../models/tree-node-data.interface';
import { ClientResponse, VendorResponse } from '../../../../api';
import { isDefined, isNullOrUndefined } from '../../../../shared/utils';

/**
 * Service responsible for handling tree node selection and updating state accordingly.
 * Centralized logic for selecting clients, vendors, groups, and distributors.
 */
@Injectable({
  providedIn: 'root',
})
export class AdminTreeSelectionService {
  private readonly adminState = inject(AdminStateService);
  private readonly vendorService = inject(AdminTreeVendorService);

  /**
   * Selects a node in the tree and updates the admin state accordingly.
   * Handles different node types: SUPER_ADMIN, DISTRIBUTOR, VENDOR, GROUP, CLIENT
   *
   * @param node - The TreeNode to select
   */
  selectNode(node: TreeNode): void {
    // Prevent selecting the "Load More" node
    if (
      isNullOrUndefined(node.data?.id) ||
      node.type === ADMIN_TREE.NODE_TYPES.LOAD_MORE
    ) {
      return;
    }

    // Update global selection state
    this.adminState.setSelectedNode(
      node.key ?? (node.data?.id as string),
      node.type ?? null,
    );

    switch (node.type) {
      case ADMIN_TREE.NODE_TYPES.SUPER_ADMIN:
        this.selectSuperAdmin(node);
        break;
      case ADMIN_TREE.NODE_TYPES.CLIENT:
        this.selectClient(node);
        break;
      case ADMIN_TREE.NODE_TYPES.VENDOR:
        this.selectVendor(node);
        break;
      case ADMIN_TREE.NODE_TYPES.GROUP:
        this.selectGroup(node);
        break;
      case ADMIN_TREE.NODE_TYPES.DISTRIBUTOR:
        this.selectDistributor(node);
        break;
      default:
        console.warn('Unknown node type for selection:', node.type);
    }
  }

  /**
   * Selects the super admin root node
   */
  private selectSuperAdmin(_node: TreeNode): void {
    this.adminState.setSelectedClient(null);
    this.adminState.setClientVendor(null);
    this.adminState.setVendorDistributor(null);
  }

  /**
   * Selects a client node
   */
  private selectClient(node: TreeNode): void {
    const clientData = node.data as ClientResponse;
    // Use setSelectedClient directly - it will handle fetching complete data if needed
    this.adminState.setSelectedClient(clientData);

    // Ensure parent context is maintained
    this.updateLineage(node);
  }

  /**
   * Selects a vendor node and sets distributor context
   */
  private selectVendor(node: TreeNode): void {
    const vendor = node.data as VendorResponse;
    this.adminState.setClientVendor(vendor);
    this.adminState.setSelectedClient(null);

    // Update parent distributor context if it exists in the tree
    this.updateLineage(node);

    // If distributor wasn't found in tree (e.g. single vendor view), try to fetch it
    if (!this.adminState.vendorDistributor() && isDefined(vendor.owner?.id)) {
      void this.setVendorDistributorFromOwnerId(vendor.owner.id);
    }
  }

  /**
   * Selects a group node
   */
  private selectGroup(node: TreeNode): void {
    this.adminState.setSelectedClient(null);

    // Maintain context by updating lineage
    this.updateLineage(node);

    // Use distributor-scoped expansion if possible
    const distributorId = this.getDistributorContextFromNode(node);
    if (isDefined(distributorId)) {
      this.adminState.setDistributorExpandedGroup(
        distributorId,
        node.data.id,
        true,
      );
    }
  }

  /**
   * Selects a distributor node
   */
  private selectDistributor(node: TreeNode): void {
    this.adminState.setVendorDistributor(node.data);
    this.adminState.setSelectedClient(null);
    this.adminState.setClientVendor(null);
  }

  /**
   * Sets the vendor's distributor in state by fetching distributor details from the API.
   * @param distributorId - The ID of the distributor (from vendor.owner.id)
   */
  private async setVendorDistributorFromOwnerId(
    distributorId: string,
  ): Promise<void> {
    try {
      const distributor =
        await this.vendorService.fetchVendorDistributor(distributorId);
      if (isDefined(distributor)) {
        this.adminState.setVendorDistributor(distributor);
      }
    } catch (error) {
      console.error('Failed to fetch vendor distributor:', error);
    }
  }

  /**
   * Helper to ensure the parent distributor/vendor signals stay populated
   * even when a specific child (like a Group or Client) is selected.
   * This maintains the active context required for API calls.
   */
  private updateLineage(node: TreeNode): void {
    let current = node.parent;
    while (current) {
      if (current.type === ADMIN_TREE.NODE_TYPES.VENDOR) {
        // Only set if different from current to minimize signal triggers
        if (this.adminState.clientVendor()?.id !== current.data?.id) {
          this.adminState.setClientVendor(current.data);
        }
      } else if (current.type === ADMIN_TREE.NODE_TYPES.DISTRIBUTOR) {
        if (this.adminState.vendorDistributor()?.id !== current.data?.id) {
          this.adminState.setVendorDistributor(current.data);
        }
      } else if (current.type === ADMIN_TREE.NODE_TYPES.GROUP) {
        // Ensure parent groups are marked as expanded in state
        const distributorId = this.getDistributorContextFromNode(current);
        if (isDefined(distributorId)) {
          this.adminState.setDistributorExpandedGroup(
            distributorId,
            current.data.id,
            true,
          );
        }
      }
      current = current.parent;
    }
  }

  /**
   * Helper method to get distributor context from a tree node by walking up the tree
   */
  private getDistributorContextFromNode(node: TreeNode): string | null {
    let current = node.parent;
    while (current) {
      if (current.type === ADMIN_TREE.NODE_TYPES.DISTRIBUTOR) {
        return (current.data?.id ?? current.key) as string;
      }
      current = current.parent;
    }
    return null;
  }
}
