import {
  ChangeDetectionStrategy,
  Component,
  effect,
  inject,
  input,
  output,
  signal,
} from '@angular/core';
import { CommonModule } from '@angular/common';
import { RouterModule } from '@angular/router';

import { AdaptiveLayoutComponent } from '../../../../../shared/components/splitter/adaptive-layout/adaptive-layout.component';
import { DetailsPanelComponent } from '../../details-panel/details-panel.component';
import { EntityDetailsService } from '../../../services/entity/entity-details.service';
import { DrawerModule } from 'primeng/drawer';

@Component({
  selector: 'app-admin-main-content',
  imports: [
    CommonModule,
    RouterModule,
    AdaptiveLayoutComponent,
    DetailsPanelComponent,
    DrawerModule,
  ],
  templateUrl: './admin-main-content.component.html',
  styleUrl: './admin-main-content.component.scss',
  changeDetection: ChangeDetectionStrategy.OnPush,
})
export class AdminMainContentComponent {
  readonly detailsService = inject(EntityDetailsService);

  // Input properties
  readonly nestedContentScrollable = input.required<() => boolean>();
  readonly detailsPanelClass = input.required<() => string>();
  readonly shouldUseDrawer = input<() => boolean>(() => false);

  // Local writable signal for drawer visibility
  readonly drawerVisible = signal<boolean>(false);

  // Output events
  readonly routerOutletActivate = output<unknown>();

  constructor() {
    // Sync drawer visibility with details panel state when in drawer mode
    effect(() => {
      const shouldUseDrawer = this.shouldUseDrawer()();
      const showPanel = this.detailsService.showPanel();

      this.drawerVisible.set(shouldUseDrawer && showPanel);
    });
  }

  /**
   * Handles router outlet activation
   */
  onRouterOutletActivate(component: unknown): void {
    this.routerOutletActivate.emit(component);
  }

  /**
   * Handles drawer close
   */
  onDrawerClose(): void {
    this.drawerVisible.set(false);
    this.detailsService.closeDetailsPanel();
  }
}
