import { inject, Injectable, Injector } from '@angular/core';
import { EntityDetailsStateService } from './entity-details.state.service';
import { ConfirmationService } from 'primeng/api';
import { catchError, Observable, Subject, tap, throwError } from 'rxjs';
import { ActionIcon, StateOption } from '../../models';
import { CustomFieldDefinition } from '../../../../api';
import { isDefined, isNullOrUndefined } from '../../../../shared/utils';
import { BroadcastEventMessageService } from '../broadcast-event-messages.service';
import { ENTITY_ADAPTER_REGISTRY } from './entity-adapter.token';
import { EntityConfigAdapter } from '../../models/entity-config-adapter.interface';

interface OperationParams {
  state?: string;
  companyId?: string;
  customFields?: Record<string, CustomFieldDefinition[]>;
  ownerType?: 'client' | 'vendor' | 'distributor';
  // Add other possible parameters here as needed
}

@Injectable({ providedIn: 'root' })
export class DetailsConfigurationService {
  private readonly entityStateService = inject(EntityDetailsStateService);
  private readonly confirmationService = inject(ConfirmationService);
  private readonly broadcastService = inject(BroadcastEventMessageService);
  private readonly injector = inject(Injector);
  private readonly adapters = inject(ENTITY_ADAPTER_REGISTRY);

  // Track which popover is active
  // Track which popover is active
  readonly activePopover = this.entityStateService.activePopover;

  // Registry of entity action handlers
  private readonly actionHandlers: Record<
    string,
    Record<
      string,
      (entityId: string, params: OperationParams) => Observable<unknown>
    >
  > = {};

  // Registry of entity action providers
  private readonly actionProviders: Record<
    string,
    (entityId: string) => ActionIcon[]
  > = {};

  // State options for different entity types
  readonly stateOptions: Record<string, StateOption[]> = {};

  constructor() {
    // Register default handlers
    this.initializeDefaultHandlers();
  }

  /**
   * Initialize default operation handlers and state options
   */
  private initializeDefaultHandlers(): void {
    this.initializeRegisteredAdapters();
  }

  private initializeRegisteredAdapters(): void {
    const registrations = this.adapters;
    for (const reg of registrations) {
      const adapter = this.injector.get(
        reg.adapter,
      ) as Partial<EntityConfigAdapter>;

      this.registerAdapterStateOptions(adapter, reg.types);
      this.registerAdapterActionIcons(adapter, reg.types);
      this.registerAdapterUpdateState(adapter, reg.types);
      this.registerAdapterDeleteHandler(adapter, reg.types);
    }
  }

  private registerAdapterStateOptions(
    adapter: Partial<EntityConfigAdapter>,
    types: string[],
  ): void {
    if (typeof adapter.getStateOptions !== 'function') return;

    const options = adapter.getStateOptions();
    for (const type of types) {
      this.registerStateOptions(type, options);
    }
  }

  private registerAdapterActionIcons(
    adapter: Partial<EntityConfigAdapter>,
    types: string[],
  ): void {
    if (typeof adapter.getActionIcons !== 'function') return;

    const getActionIcons = adapter.getActionIcons.bind(adapter);
    for (const type of types) {
      this.registerActionProvider(type, (id) => getActionIcons(id));
    }
  }

  private registerAdapterUpdateState(
    adapter: Partial<EntityConfigAdapter>,
    types: string[],
  ): void {
    if (typeof adapter.updateState !== 'function') return;

    const updateState = adapter.updateState.bind(adapter);
    for (const type of types) {
      this.registerOperationHandler(type, 'updateState', (id, params) => {
        if (isNullOrUndefined(params.state)) {
          return throwError(() => new Error('State parameter is required'));
        }
        return updateState(id, params.state);
      });
    }
  }

  private registerAdapterDeleteHandler(
    adapter: Partial<EntityConfigAdapter>,
    types: string[],
  ): void {
    if (typeof adapter.delete !== 'function') return;

    const deleteMethod = adapter.delete.bind(adapter);
    for (const type of types) {
      this.registerOperationHandler(type, 'delete', (id) => deleteMethod(id));
    }
  }

  /**
   * Gets action icons for the currently selected entity
   */
  getActionIcons(): ActionIcon[] {
    const entityType = this.entityStateService.getSelectedItemType();
    const entityId = this.entityStateService.getSelectedItemId();

    if (!entityType || !entityId) {
      return [];
    }

    // Common actions for all entities
    const commonActions: ActionIcon[] = [];

    // Entity-specific actions
    const specificActions = this.getEntitySpecificActions(entityType, entityId);

    return [...commonActions, ...specificActions];
  }

  /**
   * Get entity-specific action icons
   */
  private getEntitySpecificActions(
    entityType: string,
    entityId: string,
  ): ActionIcon[] {
    const provider = this.actionProviders[entityType];
    return isDefined(provider) ? provider(entityId) : [];
  }

  /**
   * Toggle popover visibility
   */
  togglePopover(id: string | null): void {
    this.entityStateService.togglePopover(id);
  }

  /**
   * Explicitly set the active popover id
   */
  setActivePopover(id: string | null): void {
    this.activePopover.set(id);
  }

  /**
   * Update entity state
   */
  updateEntityState(
    entityType: string,
    entityId: string,
    state: string,
  ): Observable<unknown> {
    // Close popover
    this.setActivePopover(null);
    const resultSubject = new Subject<unknown>();
    const message = `
    <div class="flex flex-col gap-2">
      <div>You are about to apply the following changes to 1 item:</div>
      <div class="p-2">
        <div class="flex items-center">
          <span class="font-medium capitalize">${state}</span>
          <span class="mx-2"></span>
          <span>Change state to <span class="capitalize">'${state}'</span></span>
        </div>
      </div>
      <div class="mt-2">Are you sure you want to continue?</div>
    </div>
  `;

    // Show confirmation
    this.confirmationService.confirm({
      message,
      header: 'Bulk Update',
      icon: 'pi pi-exclamation-triangle',
      accept: () => {
        this.executeOperation(entityType, 'updateState', entityId, {
          state,
        }).subscribe({
          next: (result) => resultSubject.next(result),
          error: (error) => resultSubject.error(error),
          complete: () => resultSubject.complete(),
        });
      },
      reject: () => {
        resultSubject.complete();
      },
      acceptButtonProps: { label: 'Apply' },
      rejectButtonProps: { label: 'Cancel', severity: 'secondary' },
    });

    return resultSubject.asObservable();
  }

  /**
   * Execute an operation on an entity
   * @returns Observable of the operation result
   */
  executeOperation(
    entityType: string,
    operationType: string,
    entityId: string,
    params?: OperationParams,
  ): Observable<unknown> {
    if (isNullOrUndefined(this.actionHandlers[entityType]?.[operationType])) {
      const errorMsg = `No handler found for operation ${operationType} on entity type ${entityType}`;
      return throwError(() => new Error(errorMsg));
    }

    const handler = this.actionHandlers[entityType][operationType];
    return handler(entityId, params ?? {}).pipe(
      tap((res) => {
        // Broadcast appropriate event based on operation type
        const eventType =
          operationType === 'delete' ? 'entity-deleted' : 'entity-updated';
        this.broadcastService.broadcast(eventType, {
          entityType,
          operation: operationType,
          entityId,
          data: res,
        });
      }),
      catchError((error) => {
        return throwError(() => error);
      }),
    );
  }

  /**
   * Get state options for an entity type
   */
  getStateOptions(entityType: string): StateOption[] {
    return this.stateOptions[entityType] ?? [];
  }

  /**
   * Register a new operation handler
   */
  registerOperationHandler(
    entityType: string,
    operationType: string,
    handler: (entityId: string, params: OperationParams) => Observable<unknown>,
  ): void {
    if (isNullOrUndefined(this.actionHandlers[entityType])) {
      this.actionHandlers[entityType] = {};
    }

    this.actionHandlers[entityType][operationType] = handler;
  }

  /**
   * Register state options for an entity type
   */
  registerStateOptions(entityType: string, options: StateOption[]): void {
    this.stateOptions[entityType] = options;
  }

  /**
   * Register action provider for an entity type
   */
  registerActionProvider(
    entityType: string,
    provider: (entityId: string) => ActionIcon[],
  ): void {
    this.actionProviders[entityType] = provider;
  }
}
