import { DestroyRef, inject, Injectable } from '@angular/core';
import { ActivatedRoute, Data, NavigationEnd, Router } from '@angular/router';
import { filter, map } from 'rxjs';
import { takeUntilDestroyed } from '@angular/core/rxjs-interop';

import { AdminDataService } from '../admin-data.service';
import { AdminContainerStateService } from '../state/admin-container-state.service';
import { EntityDetailsService } from '../entity/entity-details.service';
import { ClientDataService } from '../../../clients/services/client-data.service';
import { ADMIN_TREE } from '../../models/tree-node-data.interface';
import { isDefined, hasProperty, isString } from '../../../../shared/utils';

@Injectable({
  providedIn: 'root',
})
export class AdminRouteHandlerService {
  private readonly router = inject(Router);
  private readonly activatedRoute = inject(ActivatedRoute);
  private readonly adminDataService = inject(AdminDataService);
  private readonly containerState = inject(AdminContainerStateService);
  private readonly detailsService = inject(EntityDetailsService);
  private readonly clientDataService = inject(ClientDataService);
  private readonly destroyRef = inject(DestroyRef);

  /**
   * Initializes route handling and sets up navigation event subscriptions
   */
  initializeRouteHandling(callbacks: {
    selectNodeFromRouteOrState: () => void;
    ensureSuperAdminNodeExpansion: () => void;
    onChangeDetection: () => void;
  }): void {
    // Handle initial client navigation if needed
    this.handleInitialClientNavigation();

    // Load initial data and setup
    this.loadInitialDataAndSetup(callbacks);

    // Setup navigation event handling
    this.setupNavigationEventHandling(callbacks);

    // Setup scrollable state handling
    this.setupScrollableStateHandling();
  }

  /**
   * Handles initial client navigation if no route path is present
   */
  private handleInitialClientNavigation(): void {
    const selectedClient = this.clientDataService.getSelectedClient();

    const currentUrl = this.router.url;
    const isAtBaseAdminRoute =
      currentUrl === '/admin' || currentUrl.startsWith('/admin?');

    if (
      isDefined(selectedClient) &&
      hasProperty(selectedClient, 'id') &&
      isString(selectedClient.id) &&
      isAtBaseAdminRoute
    ) {
      this.router
        .navigate(['/admin', 'client', selectedClient.id])
        .catch((err) =>
          console.error('Failed to navigate to default client:', err),
        );
    }
  }

  /**
   * Loads initial data and performs setup
   */
  private loadInitialDataAndSetup(callbacks: {
    selectNodeFromRouteOrState: () => void;
    ensureSuperAdminNodeExpansion: () => void;
    onChangeDetection: () => void;
  }): void {
    // Get the client ID from the route if available
    const { keyToSelect, typeToSelect } = this.getEntityInfoFromRoute();
    const routeClientId =
      typeToSelect === ADMIN_TREE.NODE_TYPES.CLIENT && isDefined(keyToSelect)
        ? keyToSelect
        : undefined;

    this.adminDataService
      .loadInitialData(routeClientId)
      .then(() => {
        // Ensure super admin node is expanded by default when using modern schema
        callbacks.ensureSuperAdminNodeExpansion();

        callbacks.selectNodeFromRouteOrState();
        callbacks.onChangeDetection();
      })
      .catch(() => {
        // Even on error, try to restore selection if possible (e.g. from route)
        callbacks.selectNodeFromRouteOrState();
        callbacks.onChangeDetection();
      });
  }

  /**
   * Sets up navigation event handling for details panel query params and route synchronization
   */
  private setupNavigationEventHandling(callbacks: {
    selectNodeFromRouteOrState: () => void;
  }): void {
    this.router.events
      .pipe(
        filter(
          (event): event is NavigationEnd => event instanceof NavigationEnd,
        ),
        takeUntilDestroyed(this.destroyRef),
      )
      .subscribe(() => {
        this.handleDetailsQueryParams();
        // After any navigation, try to sync tree selection with the route
        callbacks.selectNodeFromRouteOrState();
      });
  }

  /**
   * Handles details panel query parameters
   */
  private handleDetailsQueryParams(): void {
    const currentQueryParams = this.activatedRoute.snapshot.queryParams;
    const hasDetailsParams =
      isDefined(currentQueryParams['details']) &&
      isDefined(currentQueryParams['type']);

    if (this.detailsService.showPanel() && !hasDetailsParams) {
      const item = this.detailsService.selectedItem();
      if (
        isDefined(item) &&
        hasProperty(item, 'id') &&
        hasProperty(item, 'type')
      ) {
        const baseUrl = this.router.url.split('?')[0];
        this.router
          .navigate([baseUrl], {
            queryParams: { details: item['id'], type: item['type'] ?? '' },
            queryParamsHandling: 'merge',
            replaceUrl: true,
          })
          .catch(() => {});
      }
    }
  }

  /**
   * Sets up handling for nested content scrollable state based on route data
   */
  private setupScrollableStateHandling(): void {
    // Function to get scrollable state from route
    const getScrollableState = (route: ActivatedRoute): boolean => {
      let currentRoute = route;
      while (currentRoute.firstChild) {
        currentRoute = currentRoute.firstChild;
      }
      const routeData: Data = currentRoute.snapshot.data;
      if (
        isDefined(routeData) &&
        typeof routeData['nestedContentScrollable'] === 'boolean'
      ) {
        return routeData['nestedContentScrollable'];
      }
      return false; // Default to false
    };

    // Set initial state
    this.containerState.setNestedContentScrollable(
      getScrollableState(this.activatedRoute),
    );

    // Subscribe to subsequent navigation events
    this.router.events
      .pipe(
        filter(
          (event): event is NavigationEnd => event instanceof NavigationEnd,
        ),
        map(() => getScrollableState(this.activatedRoute)), // Reuse the logic
        takeUntilDestroyed(this.destroyRef),
      )
      .subscribe((scrollableValue: boolean) => {
        this.containerState.setNestedContentScrollable(scrollableValue);
      });
  }

  /**
   * Gets entity information from route parameters
   */
  getEntityInfoFromRoute(): {
    keyToSelect: string | null;
    typeToSelect: string | null;
  } {
    // First try route traversal
    const routeResult = this.extractFromRouteTree();
    if (
      isDefined(routeResult.keyToSelect) &&
      isDefined(routeResult.typeToSelect)
    ) {
      return routeResult;
    }

    // Fallback: Parse the URL directly
    return this.extractFromUrl();
  }

  /**
   * Extracts entity info by traversing the route tree
   */
  private extractFromRouteTree(): {
    keyToSelect: string | null;
    typeToSelect: string | null;
  } {
    let currentRoute: ActivatedRoute | null = this.activatedRoute;
    let keyToSelect: string | null = null;
    let typeToSelect: string | null = null;

    while (currentRoute !== null) {
      const snapshot = currentRoute.snapshot;

      if (snapshot.url.length > 0) {
        const entityTypePath = snapshot.url[0]?.path;

        if (entityTypePath) {
          typeToSelect = this.getEntityTypeFromPath(entityTypePath);

          const entityId =
            snapshot.params[`${entityTypePath}Id`] ??
            snapshot.params['superAdminId'] ??
            snapshot.params['id'] ??
            (snapshot.url.length > 1 ? snapshot.url[1]?.path : null);

          if (isDefined(entityId) && isDefined(typeToSelect)) {
            // The super admin node in the tree always uses a constant key
            keyToSelect =
              typeToSelect === ADMIN_TREE.NODE_TYPES.SUPER_ADMIN
                ? 'super-admin-root'
                : entityId;
            break;
          }
        }
      }

      currentRoute = currentRoute.firstChild;
    }

    return { keyToSelect, typeToSelect };
  }

  /**
   * Extracts entity info by parsing the URL directly
   */
  private extractFromUrl(): {
    keyToSelect: string | null;
    typeToSelect: string | null;
  } {
    let keyToSelect: string | null = null;
    let typeToSelect: string | null = null;

    const adminRoutePattern =
      /\/admin\/(client|vendor|distributor|group|super-admin)\/([^/?]+)/;
    const urlMatch = adminRoutePattern.exec(this.router.url);

    if (urlMatch) {
      const [, pathType, entityId] = urlMatch;
      typeToSelect = this.getEntityTypeFromPath(pathType);
      keyToSelect =
        typeToSelect === ADMIN_TREE.NODE_TYPES.SUPER_ADMIN
          ? 'super-admin-root'
          : entityId;
    }

    return { keyToSelect, typeToSelect };
  }

  /**
   * Maps URL path to entity type
   */
  private getEntityTypeFromPath(path: string): string | null {
    switch (path) {
      case ADMIN_TREE.NODE_TYPES.CLIENT:
      case 'client':
        return ADMIN_TREE.NODE_TYPES.CLIENT;
      case ADMIN_TREE.NODE_TYPES.VENDOR:
      case 'vendor':
        return ADMIN_TREE.NODE_TYPES.VENDOR;
      case ADMIN_TREE.NODE_TYPES.DISTRIBUTOR:
      case 'distributor':
        return ADMIN_TREE.NODE_TYPES.DISTRIBUTOR;
      case ADMIN_TREE.NODE_TYPES.GROUP:
      case 'group':
        return ADMIN_TREE.NODE_TYPES.GROUP;
      case ADMIN_TREE.NODE_TYPES.SUPER_ADMIN:
      case 'super-admin':
        return ADMIN_TREE.NODE_TYPES.SUPER_ADMIN;
      default:
        return null;
    }
  }
}
