import { inject, Injectable, signal, WritableSignal } from '@angular/core';
import { TreeNode } from 'primeng/api';
import { AdminStateService } from '../state/admin-state.service';
import { AdminSearchService } from '../admin-search.service';
import { AdminNavigationService } from '../navigation/admin-navigation.service';
import { AdminScrollManagementService } from './admin-scroll-management.service';
import { AdminTreeSelectionService } from './admin-tree-selection.service';
import { TreeSearchResult } from '../../../../shared/models';
import { ADMIN_TREE } from '../../models/tree-node-data.interface';
import { ClientListItem, ClientResponse, IdNameType } from '../../../../api';
import { isDefined, isNullOrUndefined } from '../../../../shared/utils';
import { AdminTreeEntitySyncService } from './admin-tree-entity-sync.service';

// Constants for timing
const TIMING = {
  NODE_SELECTION_DELAY: 100,
  NODE_MONITORING_INTERVAL: 150,
  NODE_SELECTION_TIMEOUT: 10000,
} as const;

// Interface for pending node selection
interface PendingNodeSelection {
  nodeId: string;
  nodeType: string;
  timestamp: number;
}

/**
 * Service responsible for handling search result navigation and node selection scheduling.
 * Extracted from AdminTreeEventHandlerService to improve separation of concerns.
 */
@Injectable({
  providedIn: 'root',
})
export class AdminTreeSearchNavigationService {
  private readonly adminState = inject(AdminStateService);
  private readonly adminSearchService = inject(AdminSearchService);
  private readonly navigationService = inject(AdminNavigationService);
  private readonly scrollManagement = inject(AdminScrollManagementService);
  private readonly selectionService = inject(AdminTreeSelectionService);
  private readonly entitySync = inject(AdminTreeEntitySyncService);

  // Signals for tracking search state
  readonly searchingForNode = signal<boolean>(false);
  readonly searchingNodeKey = signal<string | null>(null);

  // Pending node selection tracking
  private pendingNodeSelection: PendingNodeSelection | null = null;

  // Callbacks to access tree data (set during initialization)
  private hierarchyTreeGetter?: () => TreeNode[];
  private findNodeByKeyRecursiveCallback?: (
    nodes: TreeNode[],
    key: string,
  ) => TreeNode | null;
  private selectedTreeNode!: WritableSignal<TreeNode | null>;

  /**
   * Initialize the service with required dependencies
   */
  initialize(dependencies: {
    getHierarchyTree: () => TreeNode[];
    findNodeByKeyRecursive: (nodes: TreeNode[], key: string) => TreeNode | null;
    selectedTreeNode: WritableSignal<TreeNode | null>;
  }): void {
    this.hierarchyTreeGetter = dependencies.getHierarchyTree;
    this.findNodeByKeyRecursiveCallback = dependencies.findNodeByKeyRecursive;
    this.selectedTreeNode = dependencies.selectedTreeNode;
  }

  /**
   * Navigate to a search result
   */
  async navigateToResult(result: TreeSearchResult): Promise<void> {
    const { type, id, name, owner } = result;

    switch (type) {
      case ADMIN_TREE.NODE_TYPES.CLIENT:
      case ADMIN_TREE.NODE_TYPES.VENDOR:
      case ADMIN_TREE.NODE_TYPES.DISTRIBUTOR:
        await this.navigationService.navigateToNode(result.type, result.id);
        await this.updateAdminStateForSearchResult(result);
        await this.scheduleSelection(result.id, result.type);
        break;

      case 'device':
      case 'simcard':
      case 'asset':
      case 'user':
        if (isDefined(owner) && isDefined(name)) {
          await this.updateAdminStateForSearchResult(
            result.owner as IdNameType,
          );
          await this.scheduleSelection(
            (result.owner as IdNameType).id,
            (result.owner as IdNameType).type as string,
          );
          await this.navigateAndSelectInGrid(type, id, name, owner);
        } else {
          console.warn(
            `Cannot navigate to ${type} without owner information or name.`,
          );
        }
        break;

      default:
        console.warn(`Navigation not handled for search result type: ${type}`);
    }
  }

  /**
   * Schedule node selection when node becomes available
   */
  async scheduleSelection(nodeId: string, nodeType: string): Promise<void> {
    // Try immediate selection first
    const immediateResult = this.trySelectNode(nodeId);
    if (immediateResult) {
      return;
    }

    // For client nodes, use existing search infrastructure
    if (nodeType === ADMIN_TREE.NODE_TYPES.CLIENT) {
      this.searchingForNode.set(true);
      this.searchingNodeKey.set(nodeId);

      try {
        // Use existing searchAndLoadClient - it handles all the tree loading logic
        await this.adminSearchService.searchAndLoadClient(nodeId);

        // After search completes, try selection again
        setTimeout(() => {
          this.trySelectNode(nodeId);
          this.searchingForNode.set(false);
          this.searchingNodeKey.set(null);
        }, TIMING.NODE_SELECTION_DELAY);
      } catch (error) {
        console.error(`Failed to search and load client ${nodeId}:`, error);
        this.searchingForNode.set(false);
        this.searchingNodeKey.set(null);

        // Even if search fails, try to load the client data directly
        try {
          const client = await this.entitySync.handleClientCreated(nodeId);
          if (isDefined(client)) {
            this.adminState.setSelectedClient(client);
          }
        } catch (loadError) {
          console.error(`Failed to load client ${nodeId} directly:`, loadError);
        }
      }
    } else {
      // For non-client nodes, use the monitoring approach
      this.scheduleSelectionWithMonitoring(nodeId, nodeType);
    }
  }

  /**
   * Schedule node selection with periodic monitoring
   */
  private scheduleSelectionWithMonitoring(
    nodeId: string,
    nodeType: string,
  ): void {
    // Store the pending selection
    this.pendingNodeSelection = { nodeId, nodeType, timestamp: Date.now() };

    // Monitor for availability
    this.monitorForNodeAvailability();
  }

  /**
   * Monitor for node availability with periodic checks
   */
  private monitorForNodeAvailability(): void {
    // Check every 150ms while we have a pending selection
    const checkInterval = setInterval(() => {
      if (!this.pendingNodeSelection) {
        clearInterval(checkInterval);
        return;
      }
      const { nodeId } = this.pendingNodeSelection;
      const found = this.trySelectNode(nodeId);
      if (found) {
        this.pendingNodeSelection = null;
        clearInterval(checkInterval);
      }
    }, TIMING.NODE_MONITORING_INTERVAL);

    // Set up a safety timeout to prevent infinite monitoring
    setTimeout(() => {
      if (this.pendingNodeSelection) {
        const { nodeId } = this.pendingNodeSelection;
        console.warn(
          `Timeout: Could not find tree node for ${nodeId} after extended wait`,
        );
        this.pendingNodeSelection = null;
      }
      clearInterval(checkInterval);
    }, TIMING.NODE_SELECTION_TIMEOUT);
  }

  /**
   * Try to select a node by ID in the current tree
   */
  trySelectNode(nodeId: string): boolean {
    const finalTree = this.hierarchyTreeGetter?.() ?? [];
    const nodeToSelect = this.findNodeByKeyRecursiveCallback?.(
      finalTree,
      nodeId,
    );

    if (nodeToSelect) {
      this.selectedTreeNode.set(nodeToSelect);
      this.selectionService.selectNode(nodeToSelect);
      this.scrollManagement.scrollToClient(nodeId);
      return true;
    }
    return false;
  }

  /**
   * Update admin state based on search result
   */
  private async updateAdminStateForSearchResult(
    result: IdNameType,
  ): Promise<void> {
    try {
      if (result.type === ADMIN_TREE.NODE_TYPES.CLIENT) {
        // Use the updated findClientHierarchy method with distributor support
        const hierarchy = this.adminSearchService.findClientHierarchy(
          result.id,
        );
        if (hierarchy) {
          const client = this.findClientInState(
            result.id,
            hierarchy.vendorId,
            hierarchy.groupId,
            hierarchy.distributorId,
          );
          if (client) {
            const clientResponse: ClientResponse = {
              ...client,
              entity: isDefined(client['entity'])
                ? client['entity']
                : { id: client.id, name: client.name },
            } as ClientResponse;
            this.adminState.setSelectedClient(clientResponse);
          }
        }
      } else if (result.type === ADMIN_TREE.NODE_TYPES.VENDOR) {
        // Use context-scoped vendor lookup (works for both distributor and vendor-only)
        const contextId = this.adminState.getActiveContextId();
        const vendor = this.adminState.getVendorById(
          result.id,
          contextId ?? undefined,
        );
        if (isDefined(vendor)) {
          this.adminState.setSelectedClient(null);
        }
      }
    } catch (error) {
      console.error('Failed to update admin state for search result:', error);
    }
  }

  /**
   * Find a client in the admin state using distributor-scoped data
   */
  private findClientInState(
    clientId: string,
    vendorId: string,
    groupId?: string,
    distributorId?: string,
  ): ClientListItem | undefined {
    const contextDistributorId =
      distributorId ?? this.adminState.getActiveContextId();
    if (isNullOrUndefined(contextDistributorId)) {
      return undefined;
    }

    if (isDefined(groupId)) {
      // Use distributor-scoped group clients
      const groupClients =
        this.adminState.groupClients()[contextDistributorId]?.[vendorId]?.[
          groupId
        ];
      return groupClients?.find((client) => client.id === clientId);
    } else {
      // Use distributor-scoped vendor clients
      const vendorClients =
        this.adminState.vendorClients()[contextDistributorId]?.[vendorId];
      return vendorClients?.find((client) => client.id === clientId);
    }
  }

  /**
   * Navigate to entity details and select in grid
   */
  private async navigateAndSelectInGrid(
    entityType: string,
    entityId: string,
    name: string,
    owner: unknown,
  ): Promise<void> {
    await this.navigationService.navigateToEntityDetails(
      entityType,
      entityId,
      owner as IdNameType,
    );
  }

  /**
   * Clear any pending node selections
   */
  cleanup(): void {
    this.pendingNodeSelection = null;
    this.searchingForNode.set(false);
    this.searchingNodeKey.set(null);
  }
}
