import { ResolveFn } from '@angular/router';
import { inject } from '@angular/core';
import { catchError, map, of } from 'rxjs';
import { MessageService } from 'primeng/api';

import { ClientResponse } from '../../../api';
import { isDefined, isString } from '../../../shared/utils';
import { ClientDataService } from '../../clients/services/client-data.service';

export const clientResolver: ResolveFn<ClientResponse | null> = (
  route,
  _state,
) => {
  const clientDataService = inject(ClientDataService);
  const messageService = inject(MessageService);
  const clientId = route.paramMap.get('clientId');

  if (!isDefined(clientId) || !isString(clientId)) {
    messageService.add({
      severity: 'error',
      summary: 'Error',
      detail: 'Invalid client ID',
    });
    return of(null);
  }

  // Use the cached fetch method from ClientDataService to prevent duplicate requests
  return clientDataService.fetchClientWithCache(clientId).pipe(
    map((response) => {
      if (!isDefined(response)) {
        messageService.add({
          severity: 'error',
          summary: 'Error',
          detail: 'No client data found',
        });
        return null;
      }
      return response;
    }),
    catchError(() => {
      messageService.add({
        severity: 'error',
        summary: 'Error',
        detail: 'Failed to load client data',
      });
      return of(null);
    }),
  );
};
